import React, { useState } from 'react';
import { connect } from 'react-redux';
import { AppStore } from '../../store/reducers';
import { Modal, Pagination, Button } from 'antd';
import util from '../../common/util';
import styles from './index.module.scss';
import TradexIcon from '../../components/ui/TradexIcon';
import { Empty, MainOrder, Skel, SubOrder } from './Components';
import Drawer from './Drawer';
import { GetOrderList, GetOrderListParams, orderfilter, ordersearch } from '../../api/order';
import { Route, RouteComponentProps } from 'react-router-dom';
import { OrderDetail, OrderType, SubOrderDetail, UserRole } from '../../types';
import { orderActions } from '../../config/status';
import { BankCardsStore, UserInfo, PadStore } from '../../store/types';
import { Modal as CusModal } from '../../components/AlertModal';
import { getBankCards } from '../../api/bankCards';
import { getPadInfo, getUserInfo } from '../../api/user';
import _get from '../../common/get';

import OrderFilter from '../../components/OrderFilter';
import { isNeedSignPad } from '../../common/isNeedSingPad';

import { cancel } from '../../api/http/request';

export enum OperType {
  View,
  Edit
}

export enum RequestType {
  GetList,
  Filter,
  Search
}

export interface DrawerConfig {
  operType: OperType;
  roleType: UserRole;
  orderType: OrderType;
}

interface Props extends RouteComponentProps {
  bankCards: BankCardsStore;
  userInfo: UserInfo;
  padInfo: PadStore;
}
interface State {
  role: UserRole;
  loading: boolean;
  orderList: OrderDetail[];
  currPage: number;
  totalCount: number;
  renderDrawer: boolean;
  showDrawer: boolean;
  drawerId: string;
  drawerData: OrderDetail | null;
  drawerContentType: number | string;
  drawerConfig: DrawerConfig;
  requestType: RequestType;
  search_txt: string;
  filterObj: object;
  accountModal: any;
  searchValue: any;
}

const defaultDrawerConfig = {
  operType: OperType.View,
  roleType: UserRole.Buy,
  orderType: OrderType.Main
};

class Order extends React.Component<Props, State> {
  state = {
    role: UserRole.Buy,
    loading: true,
    orderList: [],
    drawerId: '',
    drawerData: null,
    currPage: 1,
    totalCount: 0,
    renderDrawer: false,
    showDrawer: false,
    drawerContentType: 0,
    drawerConfig: defaultDrawerConfig,
    requestType: RequestType.GetList,
    search_txt: '',
    filterObj: {},
    accountModal: '' as any,
    searchValue: ''
  };

  init() {
    this.setState({
      role: (this.props.match.params as any).role === 'sold' ? UserRole.Sell : UserRole.Buy
    });
  }

  // Notification oid order_no type?
  // chat order_no
  componentDidMount() {
    const qs = this.props.location.search,
      oid = getQueryString(qs, 'oid'),
      order_no = getQueryString(qs, 'order_no');

    this.setState(
      {
        role: (this.props.match.params as any).role === 'sold' ? UserRole.Sell : UserRole.Buy
      },
      () => {
        if (order_no) {
          this.setState({
            requestType: RequestType.Search,
            searchValue: order_no
          });
          this.searchOrder(order_no || oid || '', 1, this.handleUrlDrawer);
        } else {
          this.getOrderList();
        }
      }
    );
  }

  handleUrlDrawer = () => {
    const qs = this.props.location.search,
      oid = getQueryString(qs, 'oid'),
      type = getQueryString(qs, 'type');

    if (oid && type) {
      const _type = Number(type) !== NaN ? Number(type) : type;
      this.openDrawer(oid, _type);
    }
  };

  filterChange = (option: object) => {
    const { filterObj, currPage, role } = this.state;
    // console.log('this.filterChange option -->', option)
    this.setState(
      {
        filterObj: option,
        requestType: RequestType.Filter
      },
      this.filterOrder
    );
  };

  filterOrder = (page = 1) => {
    this.setState({
      loading: true
    });
    const { filterObj, currPage, role } = this.state;
    // console.log('this.filterOrder option -->', filterObj)
    const reqObj = { ...filterObj, page, role: role === UserRole.Buy ? 'buyer' : 'seller' };
    typeof cancel === 'function' && cancel('Operation canceled by the user.');
    orderfilter(reqObj)
      .then(response => {
        // console.log('filterOrder response -->', response)
        const result = response.data;

        let list: OrderDetail[] = response.data.results;
        let count: number = response.data.count;
        list.forEach(d => {
          if (d.order_type === 2) {
            d['car_info'] = d.rfq_info as OrderDetail['car_info'];
          }

          const { color_options } = d;
          d.suborders.forEach(d2 => {
            d2._price = (color_options.find(
              d =>
                d.exterior_color.rgb === d2.exterior_color &&
                d.interior_color.rgb === d2.interior_color
            ) as any).price;
          });
        });

        this.setState({
          orderList: list,
          totalCount: count,
          loading: false,
          currPage: page
        });
      })
      .catch(err => {
        console.log('filterOrder err -->', err);
      });
  };

  searchChange = (search_txt: string) => {
    // console.log('this.searchChange search_txt --> ', search_txt)
    this.setState({
      requestType: RequestType.Search,
      search_txt: search_txt
    });
    this.searchOrder(search_txt);
  };

  searchOrder = (search_txt: string, page?: number, cb?: any) => {
    this.setState({
      loading: true
    });
    const { role } = this.state;
    const orderSearchOption = {
      page: page || 1,
      role: role === UserRole.Buy ? 'buyer' : 'seller',
      search_txt: search_txt.split('-')[0]
    };
    // source.cancel('Operation canceled by the user.')
    typeof cancel === 'function' && cancel('Operation canceled by the user.');
    ordersearch(orderSearchOption)
      .then(response => {
        // console.log(' searchOrder response  --> ', response)
        const result = response.data;

        let list: OrderDetail[] = response.data.results;
        let count: number = response.data.count;
        list.forEach(d => {
          if (d.order_type === 2) {
            d['car_info'] = d.rfq_info as OrderDetail['car_info'];
          }
          const { color_options } = d;
          d.suborders.forEach(d2 => {
            d2._price = (color_options.find(
              d =>
                d.exterior_color.rgb === d2.exterior_color &&
                d.interior_color.rgb === d2.interior_color
            ) as any).price;
          });
        });

        this.setState(
          {
            orderList: list,
            totalCount: count,
            loading: false,
            currPage: page || 1
          },
          () => {
            result.count > 0 && cb && typeof cb === 'function' && cb();
          }
        );
      })
      .catch(error => {
        console.log(' searchOrder error  --> ', error);
      });
  };

  hasPrimaryBankCard = (cards: any) => cards.length && cards.length > 0;

  componentDidUpdate(preProps: Props, preState: State) {
    const { requestType, search_txt, currPage } = this.state;
    if ((this.props.match.params as any).role !== (preProps.match.params as any).role) {
      this.init();
    }
    if (this.state.currPage !== preState.currPage) {
      util.scrollTop();
      if (requestType == RequestType.GetList) {
        this.getOrderList(currPage);
      } else if (requestType == RequestType.Filter) {
        this.filterOrder(currPage);
      } else {
        this.searchOrder(search_txt, currPage);
      }
    }
  }

  confirmBankAndPad = (bankCardsRes: any, padInfoRes: any, userInfoRes: any) => {
    const cards = _get(bankCardsRes, ['data', 'results']) || [];
    const padInfo = _get(padInfoRes, ['data']);
    const userInfo = _get(userInfoRes, ['data']);

    if (!this.hasPrimaryBankCard(cards)) {
      const accountModal = {
        message: 'You havent set up your bank account yet.',
        address: '/trade/account/bank',
        onClose: this.closeAccountModal,
        push: this.props.history.push
      };
      this.setState({ ...this.state, accountModal });
      return false;
    }
    if (isNeedSignPad(userInfo, padInfo)) {
      const accountModal = {
        message: 'You havent set up your Pre-Auhorized Debit (PAD) yet. ',
        address: '/trade/account/verification',
        onClose: this.closeAccountModal,
        push: this.props.history.push
      };
      this.setState({ ...this.state, accountModal });
      return false;
    }
    return true;
  };

  openDrawer = async (orderId: string, contentType: number | string) => {
    const { role, orderList, renderDrawer, drawerConfig, drawerData } = this.state;
    const pdfTypeList: (string | number)[] = ['PA', 'HB'];
    let orderType: OrderType;
    let item = (orderList as OrderDetail[]).find(d => d.id === orderId) as OrderDetail;
    if (contentType === 6) {
      this.setState({ loading: true });
      try {
        const [bankCardsRes, padInfoRes, userInfoRes] = await Promise.all([
          getBankCards(),
          getPadInfo(),
          getUserInfo()
        ]);
        this.setState({ loading: false });
        const result = this.confirmBankAndPad(bankCardsRes, padInfoRes, userInfoRes);
        if (!result) return;
      } catch {
        this.setState({ loading: false });
        return;
      }
    }
    if (item) {
      orderType = OrderType.Main;
      item = Object.assign({}, item, { suborders: [] });
    } else {
      orderType = OrderType.Sub;
      item = (orderList as OrderDetail[]).find(d =>
        d.suborders.find(d => d.id === orderId)
      ) as OrderDetail;
      item = Object.assign({}, item);
      const subItemIdx = item.suborders.findIndex(d => d.id === orderId);
      const subItem = item.suborders[subItemIdx];
      subItem._idx = subItemIdx;
      item.suborders = [subItem];
    }

    const openAction = () => {
      this.setState({
        renderDrawer: true,
        showDrawer: true,
        drawerId: orderId,
        drawerData: item as OrderDetail,
        drawerContentType: contentType,
        drawerConfig: {
          operType: orderActions.find(d => d.type === contentType) ? OperType.Edit : OperType.View,
          roleType: role,
          orderType
        }
      });
    };

    if (pdfTypeList.includes(contentType)) {
      const currItem = orderType === OrderType.Main ? item : item.suborders[0];
      const link = (currItem.links.find((d: any) => d.type === contentType) as any).value[0];
      window.open(link);
      return;
    }

    if (
      renderDrawer &&
      drawerConfig.operType === OperType.Edit &&
      orderId !== (drawerData as any).id
    ) {
      Modal.confirm({
        content: 'The current operation will overwrite unsaved data. Are you sure to continue?',
        cancelText: 'Not Now',
        okText: 'Close',
        onOk: () => this.closeDrawer(openAction)
      });
    } else {
      openAction();
    }
  };

  closeDrawer = (cb?: any) => {
    const { drawerData, drawerConfig } = this.state;
    this.setState(
      {
        renderDrawer: false,
        showDrawer: false,
        drawerContentType: 0,
        drawerConfig: defaultDrawerConfig
      },
      () => {
        typeof cb === 'function' && cb();
        if (drawerConfig.operType === OperType.Edit) {
          this.updateOrderItem((drawerData as any).id);
        }
      }
    );
  };

  toggleDrawer = () => {
    this.setState({
      showDrawer: !this.state.showDrawer
    });
  };

  async getOrderList(page = 1) {
    const is_buyer = this.state.role === UserRole.Buy ? 1 : 0;
    const role = this.state.role === UserRole.Buy ? 'buyer' : 'seller';
    const { list, count } = await this.fetchOrders({ page, role });
    this.setState(
      {
        orderList: list,
        totalCount: count
      },
      () => {
        page == 1 && this.handleUrlDrawer;
      }
    );
  }

  async updateOrderItem(oid: string) {
    const is_buyer = this.state.role === UserRole.Buy ? 1 : 0;
    const role = this.state.role === UserRole.Buy ? 'buyer' : 'seller';
    const {
      list: [item]
    } = await this.fetchOrders({ order_id: oid, role });
    if (item) {
      this.setState({
        orderList: this.state.orderList.map((d: OrderDetail) => (d.id === item.id ? item : d))
      });
    }
  }

  fetchOrders(query: GetOrderListParams): Promise<{ list: OrderDetail[]; count: number }> {
    this.setState({
      loading: true
    });
    return GetOrderList(query).then(data => {
      let list: OrderDetail[] = data.data.results;
      let count: number = data.data.count;
      list.forEach(d => {
        if (d.order_type === 2) {
          d['car_info'] = d.rfq_info as OrderDetail['car_info'];
        }

        const { color_options } = d;
        d.suborders.forEach(d2 => {
          d2._price = (color_options.find(
            d =>
              d.exterior_color.rgb === d2.exterior_color &&
              d.interior_color.rgb === d2.interior_color
          ) as any).price;
        });
      });
      this.setState({
        loading: false
      });
      return { list, count };
    });
  }

  pageChangeHandler = (page: number) => {
    this.getOrderList(page);
    this.setState({
      currPage: page
    });
  };

  closeAccountModal = () => this.setState({ ...this.state, accountModal: '' });

  render() {
    const {
      renderDrawer,
      showDrawer,
      loading,
      orderList,
      currPage,
      totalCount,
      role,
      drawerId,
      drawerData,
      drawerContentType,
      drawerConfig,
      accountModal,
      searchValue
    } = this.state;

    // if (loading) {
    //   return <Skel />;
    // }
    //
    // if (!orderList.length) {
    //   return <Empty />;
    // }

    return (
      <Route>
        <div>
          <OrderFilter
            total={totalCount}
            loading={loading}
            onChange={this.filterChange}
            onSearch={this.searchChange}
            searchValue={searchValue}
          />
          {loading && <Skel />}
          {!loading && !orderList.length && <Empty />}
          {!loading && orderList.length > 0 && (
            <div className={styles.orderList}>
              {orderList.map((d, idx: number) => (
                <OrderItem
                  key={idx}
                  detail={d}
                  role={role}
                  isInDrawer={false}
                  onBtnClick={this.openDrawer}
                />
              ))}
              {!loading && (
                <Pagination
                  pageSize={10}
                  current={currPage}
                  onChange={n => this.setState({ currPage: n })}
                  total={totalCount}
                  style={{ textAlign: 'right' }}
                  itemRender={(current, type, originalElement) => {
                    if (type === 'prev') {
                      return <a className={styles.pageButton}>Previous</a>;
                    }
                    if (type === 'next') {
                      return <a className={styles.pageButton}>Next</a>;
                    }
                    return originalElement;
                  }}
                />
              )}
              {drawerData && renderDrawer && (
                <Drawer
                  show={showDrawer}
                  onClose={this.closeDrawer.bind(null)}
                  toggleDrawer={this.toggleDrawer.bind(null)}
                  type={drawerContentType}
                  id={drawerId}
                  data={drawerData as any}
                  drawerConfig={drawerConfig}
                />
              )}
            </div>
          )}
          {accountModal ? (
            <AccountModal
              message={accountModal.message}
              address={accountModal.address}
              onClose={accountModal.onClose}
              push={accountModal.push}
            />
          ) : (
            ''
          )}
        </div>
      </Route>
    );
  }
}

export function OrderItem(props: {
  detail: OrderDetail;
  role: UserRole;
  isInDrawer: boolean;
  onBtnClick: (orderId: string, contentType: number | string) => void;
}) {
  const { detail, role, onBtnClick, isInDrawer } = props;
  const totalCount = detail.suborders && detail.suborders.length;
  const [showAll, setShowAll] = useState(totalCount <= 3);
  return (
    <div className={`${styles.orderItem} ${isInDrawer ? styles.inDrawer : undefined}`}>
      <MainOrder detail={detail} role={role} isInDrawer={isInDrawer} onBtnClick={onBtnClick} />
      <div className={`${styles.subList} ${showAll ? '' : styles.fold}`}>
        {detail.suborders.map((d: SubOrderDetail, idx) => (
          <SubOrder
            key={idx}
            detail={d}
            idx={idx}
            isInDrawer={isInDrawer}
            onBtnClick={onBtnClick}
            parentOrder={detail}
          />
        ))}
      </div>
      {totalCount > 3 && (
        <div className={`${styles.showAllBtn} cursor-pointer`}>
          <div onClick={() => setShowAll(!showAll)}>
            <span>Show {showAll ? 'Less' : `All ${util.pluralize(totalCount, 'Unit')}`}</span>
            <TradexIcon
              type="iconicon_rightarrow"
              size={16}
              color="#001a33"
              style={{ transform: `rotate(${showAll ? '-88' : '88'}deg)` }}
            />
          </div>
        </div>
      )}
    </div>
  );
}

function AccountModal(props: { message: string; address: string; push: any; onClose: () => void }) {
  const goto = (path: string) => () => props.push(path);

  return (
    <CusModal className={styles.bankModal} onClose={props.onClose}>
      <div className={styles.message}>{props.message}</div>
      <div className={styles.divider}></div>
      <div className={styles.operation}>
        <Button className={`${styles.common} ${styles.notNow}`} onClick={props.onClose}>
          Not Now
        </Button>
        <Button className={`${styles.common} ${styles.setUp}`} onClick={goto(props.address)}>
          Go to set up
        </Button>
      </div>
    </CusModal>
  );
}

function getQueryString(url: string, name: string): string | null {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
  var r = url.substr(1).match(reg);
  if (r != null) return decodeURI(r[2]);
  return null;
}

const mapStateToPros = (state: AppStore) => ({
  userInfo: state.user.userInfo,
  bankCards: state.bankCards,
  padInfo: state.pad
});

export default connect(mapStateToPros)(Order);
