import React from 'react';
import { Row } from '../Row';
import styles from './index.module.scss';
import _get from '../../../common/get';

type DescriptionsPrps = {
  style?: any;
  className?: any;
  children?: any;
  title?: any;
};

const Descriptions = (props: DescriptionsPrps) => {
  const { style, className = '', title, children } = props;

  return (
    <div style={style} className={`${styles.container} ${className}`}>
      {title ? title : ''}
      {title ? <div className={styles.divider}></div> : ''}
      {children ? children : ''}
    </div>
  );
};

Descriptions.Row = (props: any) => <Row className={styles.row}>{props.children}</Row>;

export { Descriptions };
