import React from 'react';
import styles from './index.module.scss';
import IconFont from '../../../../components/ui/TradexIcon';
import { Button, Spin, message as AntdMessage } from 'antd';
import { get, post } from '../../../../api/http';
import _get from '../../../../common/get';
import { FormattedMessage } from 'react-intl';

interface Props {
  detail: any;
  data: any;
  // drawerConfig: DrawerConfig;
  // setActionStatus: (status: ActionStatus) => void;
  onClose: () => void;
}

// type Props = any;
// type State = any;

interface signImage {
  url?: string;
  id?: string;
}

interface signtureGroups {
  full?: signImage;
  initial?: signImage;
}

interface signature {
  offsetLeft: number;
  offsetTop: number;
  type: string;
  sign_images: signImage[];
}

interface pageArrItem {
  index: number;
  picSrc: string;
  direction: string;
  isAreadyChoied?: boolean;
  signature: signature[];
}

interface LISTDATA {
  role: string;
  page_width: number;
  page_height: number;
  signature_width: number;
  signature_height: number;
  pageHorizontalHeight: number;
  image_config: pageArrItem[];
}

interface State {
  scale: number;
  paperHeight: number;
  LISTDATA: LISTDATA;
  signtureGroups: signtureGroups;
  showHint: boolean;
  isCanSubmit: boolean;
  activeCount: number;
  totalNumSignture: number;
  showSignSure: boolean;
  activeSignSureId: any;
  signImages: signImage[];
  signImagesKey: string;
  session: string;
  isLoading: boolean;
}

const message = {
  title: 'account_verification_drawer_title',
  button: 'account_verification_drawer_button'
};

// 1url urldata
// 2.     scroll

class Sign extends React.Component<Props, State> {
  static defaultProps: Props;

  private signBox = React.createRef<any>();

  state = {
    scale: 1,
    paperHeight: 0,
    LISTDATA: {
      role: '',
      signature_width: 0,
      signature_height: 0,
      page_height: 0,
      page_width: 0,
      pageHorizontalHeight: 0,
      image_config: [
        {
          isAreadyChoied: false,
          index: 0,
          picSrc: '',
          direction: '',
          pageHorizontalHeight: 0,
          signature: [
            {
              offsetLeft: 234,
              offsetTop: 1330,
              type: '',
              sign_images: []
            }
          ]
        }
      ]
    },
    // id  type   
    signtureGroups: {},
    //  
    showHint: false,
    //
    isCanSubmit: false,
    //num
    activeCount: 0,
    //all  num
    totalNumSignture: 0,

    // 
    showSignSure: false,
    activeSignSureId: 0,
    signImages: [],
    // signtureGroups  key
    signImagesKey: '',
    session: '',
    isLoading: false
  };

  componentDidMount() {
    this.getSign();
  }

  componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>, snapshot?: any): void {
    if (prevProps.detail !== this.props.detail) {
      this.getSign();
    }
  }

  getSign = () => {
    this.setState({ ...this.state, isLoading: true });
    get(`users/sign_pad/`)
      .then(({ data: { url } }: any) => {
        const session = getURLSearchParams(url, 'session') || '';
        this.setState({ ...this.state, isLoading: false, session });
        return url;
      })
      .then((url: any) => get(url, { format: 'json' }, false))
      .then((res1: any) => {
        if (res1.data.pages) {
          let GLOABLE_DATA = res1.data;
          let totalNumSignture = 0;
          GLOABLE_DATA.image_config.map((item: any) => {
            item.isAreadyChoied = false;
            item.signature.forEach(() => {
              totalNumSignture++;
            });
            return item;
          });
          var scale = this.caculateScale(GLOABLE_DATA.page_width);
          var signtureGroups =
            GLOABLE_DATA.role === 'seller'
              ? {
                  full: {},
                  initial: {}
                }
              : {
                  full: {}
                };
          this.setState({
            scale,
            signtureGroups,
            totalNumSignture,
            LISTDATA: GLOABLE_DATA
          });
        }
        if (res1.data.code === 400 || res1.data.code === '400') {
          AntdMessage.error(res1.data.msg || 'Error');
          this.props.onClose && this.props.onClose();
        }
      })
      .catch((e: any) => {
        if (e.response) {
          const error = _get(e, ['response', 'data', 'message']);
          AntdMessage.error(error || 'Error');
        } else {
          AntdMessage.error(e.message);
        }
        this.props.onClose && this.props.onClose();
      });
  };

  isHorizontal(pageItem: any) {
    return pageItem.direction === 'horizontal';
  }

  caculateScale = (width: number) => {
    var scale = 760 / width;
    return scale >= 1 ? 1 : scale;
  };

  signClick = (id: any, isAreadyChoied: boolean, signImages: Array<any>, groupKey: string) => {
    const { signtureGroups, LISTDATA } = this.state;
    const _LISTDATA = Object.assign({}, LISTDATA);
    this.setState({
      activeSignSureId: id
    });
    //  signtureGroups[groupKey]['url']  
    // @ts-ignore
    if (signtureGroups[groupKey]['url']) {
      if (!isAreadyChoied) {
        _LISTDATA.image_config.map(item => {
          if (item.index === id) {
            item.isAreadyChoied = true;
            this.setState({
              activeCount: this.state.activeCount + 1
            });
          }
          return item;
        });
        this.setState({
          LISTDATA: _LISTDATA
        });
        return;
      } else {
      }
    }
    this.setState({
      showSignSure: true,
      signImages: signImages,
      signImagesKey: groupKey
    });
    // }
  };

  submitSign = () => {
    const { LISTDATA } = this.state;
    const unSignedItems = LISTDATA.image_config.filter(
      item => !item.isAreadyChoied && item.signature.length
    );
    if (unSignedItems.length > 0) {
      this.setState({
        showHint: true
      });
      let scrollHeight = 0;
      LISTDATA.image_config.some((item: pageArrItem) => {
        if (!item.isAreadyChoied && item.signature.length) {
          scrollHeight += item.signature[0].offsetTop / 2;
          return true;
        } else {
          scrollHeight +=
            10 + (this.isHorizontal(item) ? LISTDATA.pageHorizontalHeight : LISTDATA.page_height);
        }
        // console.log('scrollHeight1', scrollHeight);
      });
      // console.log('scrollHeight2', scrollHeight);
      this.signBox.current.scroll({
        top: scrollHeight * this.state.scale,
        behavior: 'smooth'
      });
      return;
    }
    // console.log('');
    this.signSure();
  };

  //
  signSure = () => {
    // const { setActionStatus } = this.props;
    const { session, signtureGroups } = this.state;
    let imgs: any[] = [];
    for (let i in signtureGroups) {
      // @ts-ignore
      const { id, url } = signtureGroups[i];
      imgs.push({
        type: i,
        id,
        url
      });
    }

    const reqDate = {
      session: session,
      images: imgs
    };
    const ESIGN_ROOT = process.env.ESIGN_ROOT ? process.env.ESIGN_ROOT : '';
    post(`${ESIGN_ROOT}/signing/signature`, reqDate, !ESIGN_ROOT)
      .then((response: any) => {
        this.props.onClose && this.props.onClose();
      })
      .catch(err => {
        AntdMessage.error('Error');
      });
  };

  // 
  setSign = (id: number, url: string) => {
    const { signtureGroups, signImagesKey, LISTDATA, activeSignSureId } = this.state;
    const _signtureGroups = Object.assign({}, signtureGroups);
    // @ts-ignore
    _signtureGroups[signImagesKey]['url'] = url;
    // @ts-ignore
    _signtureGroups[signImagesKey]['id'] = id;
    // console.log('_signtureGroups', _signtureGroups);

    const _LISTDATA = Object.assign({}, LISTDATA);
    _LISTDATA.image_config.map(item => {
      if (item.index === activeSignSureId) {
        // console.log('item.index, ', item.index, 'id, ', id);
        if (!item.isAreadyChoied) {
          this.setState({
            activeCount: this.state.activeCount + 1
          });
        }
        item.isAreadyChoied = true;
      }
      return item;
    });
    // console.log('_LISTDATA', _LISTDATA);
    this.setState({
      signtureGroups: _signtureGroups,
      LISTDATA: _LISTDATA
    });
    this.signImgCancel();
  };

  signImgCancel = () => {
    this.setState({
      showSignSure: false
    });
  };

  paperHeight() {
    var {
      scale,
      LISTDATA: { page_height = 0, image_config = [], pageHorizontalHeight = 0 }
    } = this.state;
    var mgTop: number = 10;
    var sHeight: number = 0;
    image_config.forEach(item => {
      sHeight += 10;
      if (item.direction === 'horizontal') {
        sHeight += pageHorizontalHeight;
      } else {
        sHeight += page_height;
      }
    });
    this.setState({
      paperHeight: sHeight * scale
    });
  }

  render() {
    const {
      LISTDATA,
      scale,
      paperHeight,
      showHint,
      signtureGroups,
      signImagesKey,
      showSignSure,
      signImages,
      isLoading
    } = this.state;
    const isHorizontal = this.isHorizontal;
    return (
      <div className={styles.container}>
        <Spin spinning={isLoading} size="large">
          <div className={styles.content}>
            <div className={styles.title}>
              <FormattedMessage id={message.title} />
            </div>
            <div className={styles.agreement} ref={this.signBox}>
              {showSignSure ? (
                <SignSure
                  signImages={signImages}
                  setSign={this.setSign}
                  signImgCancel={this.signImgCancel}
                  signtureGroups={signtureGroups}
                  signImagesKey={signImagesKey}
                />
              ) : null}
              <div className={styles.paperView} style={{ height: paperHeight + 'px' }}>
                {/* & */}
                <div
                  className={styles.pagerContent}
                  style={{ transform: `scale(${scale}, ${scale})`, transformOrigin: '0% 0%' }}
                >
                  {LISTDATA.image_config.map((item, index) => (
                    <div
                      className={styles.itemBox}
                      key={index}
                      style={{
                        width: `${LISTDATA.page_width}px`,
                        height: `${
                          isHorizontal(item) ? LISTDATA.pageHorizontalHeight : LISTDATA.page_height
                        }px`
                      }}
                    >
                      <img src={item.picSrc} className={styles.itemImg} alt="" />
                      {!isHorizontal(item)
                        ? item.signature.map((signItem, signIndex) => (
                            <div
                              className={styles.sigtureContentItem}
                              style={{
                                top: `${signItem.offsetTop}px`,
                                left: `${signItem.offsetLeft}px`,
                                width: `${LISTDATA.signature_width}px`,
                                height: `${LISTDATA.signature_height}px`
                              }}
                              key={signIndex}
                            >
                              <SignItem
                                signClick={this.signClick}
                                id={item.index}
                                isAreadyChoied={item.isAreadyChoied}
                                showHint={showHint}
                                groupKey={signItem.type || 'full'}
                                signtureGroups={signtureGroups}
                                signImages={signItem.sign_images || []}
                              />
                            </div>
                          ))
                        : null}
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
          <Button className={styles.button} onClick={this.submitSign}>
            <FormattedMessage id={message.button} />
          </Button>
        </Spin>
      </div>
    );
  }
}

export default Sign;

type SignItemProps = {
  id: number;
  showHint: boolean;
  groupKey: string;
  signtureGroups: object;
  signImages: string[];
  signClick: any;
  isAreadyChoied?: boolean;
};

//
//      
class SignItem extends React.Component<SignItemProps, object> {
  signClick = () => {
    const { signClick, id, signImages, groupKey, isAreadyChoied } = this.props;
    signClick(id, isAreadyChoied, signImages, groupKey);
  };

  componentDidMount() {}

  render() {
    const {
      showHint,
      groupKey,
      signtureGroups,
      signImages,
      signClick,
      id,
      isAreadyChoied
    } = this.props;
    let signImgurl;

    try {
      // @ts-ignore
      signImgurl = isAreadyChoied && signtureGroups[groupKey] && signtureGroups[groupKey]['url'];
    } catch (e) {
      // console.log(e);
    }
    return (
      <div className={styles.sigtureView} onClick={this.signClick}>
        {!isAreadyChoied ? (
          <div className={`${styles.signIconBox} ${showHint ? styles.showHint : ''} `}>
            <IconFont
              type="iconicon_signpa_line"
              color={showHint ? '#f2565f' : '#ff9910'}
              className={styles.signIcon}
            />
            <span className={styles.signText}>Click to sign</span>
          </div>
        ) : null}

        {signImgurl ? <img src={signImgurl} alt="" className={styles.signImgurl} /> : null}
        {showHint && !isAreadyChoied ? (
          <p className={styles.signHint}>Please sign in every paint in purchase agreement.</p>
        ) : null}
      </div>
    );
  }
}

interface imgObj {
  id: number;
  url: string;
}

// type signSureProps = any;
interface signSureProps {
  signImages: imgObj[];
  setSign: Function;
  signImgCancel: Function;
  signtureGroups: object;
  signImagesKey: string;
}

class SignSure extends React.Component<signSureProps, object> {
  state = {
    isChosed: false,
    chosedObj: {
      id: 0,
      url: ''
    }
  };

  signImgClick = (imgObj: imgObj) => {
    const { id, url } = imgObj;
    this.setState({
      chosedObj: {
        id,
        url
      },
      isChosed: true
    });
  };

  signSureClick = () => {
    const { isChosed, chosedObj } = this.state;
    const { id, url } = chosedObj;
    if (!isChosed) {
      return;
    }
    this.props.setSign(id, url);
  };

  signImgCancel = () => {
    this.props.signImgCancel();
  };

  componentDidMount() {
    const { signtureGroups, signImagesKey } = this.props;

    // @ts-ignore
    const propsSignObj = signtureGroups[signImagesKey];
    if (propsSignObj['url']) {
      const { id, url } = propsSignObj;
      this.setState({
        chosedObj: {
          id,
          url
        },
        isChosed: true
      });
    }
  }

  render() {
    const { signImages } = this.props;
    const { isChosed, chosedObj } = this.state;
    // console.log('signSure ', signImages);
    return (
      <div className={styles.signSureBox}>
        {/**/}
        <IconFont
          type="iconicon_cancel_small"
          color="#001a33"
          onClick={this.props.signImgCancel}
          className={styles.signSureCancel}
        />
        <div className={styles.signSureBox__title}>Choose your signature</div>
        <div className={styles.signSureBox__cont}>
          {signImages.map((item, index) => (
            <div
              className={`${styles.signImgItemBox} ${
                chosedObj.id === item.id ? styles.signImgItemChose : ''
              }`}
              key={index}
              onClick={() => this.signImgClick(item)}
            >
              <img
                // src="https://tradex-china.zeno-cdn.com/media/files/cc7ef2cee9d648e8ac7d769cf3121517.png"
                src={item.url}
                alt=""
                className={styles.signImg}
              />
            </div>
          ))}
        </div>
        <div className={styles.point}>
          By signing this document with an electronic signature, I agree that such signature will be
          as valid as handwritten signature to the extent allowed by local law.
        </div>
        <Button
          className={`${styles.signSureBtn} ${
            isChosed ? styles.signSureActBtn : styles.signSureDisBtn
          }`}
          onClick={this.signSureClick}
        >
          Accept and sign
        </Button>
      </div>
    );
  }
}

function getURLSearchParams(url: string, key: string) {
  if (!url) return;
  const urlObj = new URL(url);
  if (!urlObj) return;
  const searchParams = new URLSearchParams(urlObj.search);
  if (!searchParams) return;
  return searchParams.get(key);
}
