import React, { Component } from 'react';

import Icon from '../../components/Icon';
import IconFont from '../../components/ui/TradexIcon';

import styles from './RegisterReviewed.module.scss';

import { getUserInfo } from '../../api/user';
import { path } from '../../routes/routers';

type Props = any;
// type State = any;

interface FormInput {
  [index: string]: any;
}

interface State {
  userRegisterInfo: FormInput;
  mailtoInfo: FormInput;
}

class RegisterReviewed extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      userRegisterInfo: {
        nickname: '',
        company_country: '',
        company_name: '',
        company_legal_name_in_english: '',
        company_operating_name: '',
        company_identification_number: '',
        tax_no: '', //
        company_address: '',
        company_address2: '',
        company_address3: '',
        city: '',
        province_state: '',
        postal_or_zip_code: '', //
        num_sold_last_year: '',
        num_in_stocks: '',
        first_name: '',
        last_name: '',
        position_at_company: '',
        office_phone_number: '',
        work_email: ''
      },
      mailtoInfo: {
        'User Name': '',
        'Country Where Company is Registered': ''
      }
    };
  }

  componentDidMount() {
    // console.log(localStorage.getItem('userRegisterInfo'))
    const _userRegisterInfo = JSON.parse(localStorage.getItem('userRegisterInfo') as string) || {};
    const { userRegisterInfo, mailtoInfo } = this.state;
    for (var i in userRegisterInfo) {
      userRegisterInfo[i] = _userRegisterInfo[i];
    }
    const RegisterInfoArr = Object.entries(userRegisterInfo);
    // console.log('RegisterInfoArr', RegisterInfoArr)
    RegisterInfoArr.map((item: any[]) => {
      if (item[0] == 'nickname') {
        mailtoInfo['User Name'] = item[1];
      } else if (item[0] == 'company_country') {
        mailtoInfo['Country Where Company is Registered'] = item[1];
      } else if (item[1] != null && item[1] != undefined && item[1] != '') {
        mailtoInfo[
          item[0]
            .split('_')
            .map((ii: string) => ii.replace(ii.charAt(0), ii.charAt(0).toUpperCase()))
            .join(' ')
        ] = item[1];
      }
    });

    this.setState({ userRegisterInfo, mailtoInfo });
    this.getUserInfo();
  }

  getUserInfo = () => {
    getUserInfo()
      .then(
        response => {
          console.log('response ->', response);
          const res = response.data;

          localStorage.setItem('paper', JSON.stringify(res.paper));
          localStorage.setItem('is_new', res.is_new);
          localStorage.setItem('userRegisterInfo', JSON.stringify(res || {}));

          const {
            history: { push, replace }
          } = this.props;
          replace(`${path.Index}`);
        },
        err => console.log(err)
      )
      .catch(err => {
        console.log('getUserInfo err ->', err);
      });
  };

  render() {
    const { mailtoInfo } = this.state;
    let mailStr = '\n\n\n\n\nRegister content \n\n';
    Object.entries(mailtoInfo).map((item: any) => {
      mailStr += item[0] + ': ' + item[1] + '\n\n';
    });
    // console.log('mailStr', mailStr);
    mailStr = mailStr.replace(/\n/g, escape('\n'));
    // console.log('mailStr', mailStr);

    return (
      <div className={styles.RegisterReviewedBox}>
        <Icon kind="picture_succeed" width={400} height={300} className={styles.ReviewedIcon} />
        <div className={styles.ReviewedMsgBox}>
          <p className={styles.label}>Your account is</p>
          <div className={styles.ReviewedMsgStatus}>
            <IconFont
              type="iconicon_time_line"
              color="#00274d"
              size={32}
              className={styles.ReviewedMsg__Icon}
            />
            <span className={styles.ReviewedMsg__content}>Being Reviewed</span>
          </div>
          <p className={styles.ReviewedHint}>
            We will notify you when finished.
            <br />
            Thanks for your patience!
          </p>
          <p className={styles.subHint}>
            <span className={styles.subHint__label}>For more details, plesae contact </span>
            <a
              href={`mailto:support@tradexport.com?subject=Reviewed&body=${mailStr}`}
              className={styles.contact}
            >
              {' '}
              support@tradexport.com
            </a>
          </p>
        </div>
      </div>
    );
  }
}

export default RegisterReviewed;
