import React from 'react';
import { FormattedMessage } from 'react-intl';
import styles from './Card.module.scss';
import { PrirmaryTag, PadTag } from '../Tags';
import _get from '../../../../common/get';

const Card = (props: any) => {
  const { isPrimary = false, isPad = false, onClick = () => {}, isSignedPad = false } = props;

  return (
    <div className={styles.container} onClick={onClick}>
      {isPrimary ? <PrirmaryTag className={styles.PrirmaryTag} /> : ''}
      {isSignedPad ? isPad ? <PadTag className={styles.PadTag} /> : '' : ''}
      <div className={styles.bankName}>{_get(props, ['institution_name']) || '--'}</div>
      <div className={styles.accountName}>
        <FormattedMessage id="account_bankAccounts_accountName" />
      </div>
      <div className={styles.accountNameValue}>{_get(props, ['account_name']) || '--'}</div>
      <div className={styles.swfitCode}>
        <FormattedMessage id="account_bankAccounts_swfitCode" />
      </div>
      <div className={styles.swfitCodeValue}>{_get(props, ['SWIFT']) || '--'}</div>
      <div className={styles.bankNumber}>
        <FormattedMessage id="account_bankAccounts_bankAccountNumber" />
      </div>
      <div className={styles.bankNumberValue}>{_get(props, ['account_number']) || '--'}</div>
    </div>
  );
};

export default Card;
