import { getPadInfo } from '../../api/user';
import { PadStore, PadAction, PadActionTypes } from '../types';

export const SetPadInfo = (status: PadStore): PadAction => ({
  type: PadActionTypes.SET_PAD_INFO,
  status
});

export const asyncSetPadInfo = () => (dispatch: any, getState: any) => {
  getPadInfo()
    .then(({ data }: any) => {
      const newAction = SetPadInfo({ ...data, isUseful: true });
      dispatch(newAction);
    })
    .catch(() => {
      const newAction = SetPadInfo({ isUseful: false });
      dispatch(newAction);
    });
};
