import React, { Component } from 'react';
import { Map, Marker, GoogleApiWrapper, GoogleAPI, InfoWindow } from 'google-maps-react';

interface Props {
  google: GoogleAPI;
  title: string;
  coordinate: {
    lat: number;
    lng: number;
  };
}

interface State {
  activeMap: Map | undefined;
  activeMarker: Marker | undefined;
  showingInfoWindow: boolean;
}

export class MapContainer extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      activeMap: undefined,
      activeMarker: undefined,
      showingInfoWindow: true
    };
  }

  onMarkerClick = (props: any, marker: any, e: any) => {
    this.setState({
      activeMap: props.map,
      activeMarker: marker,
      showingInfoWindow: true
    });
  };

  render() {
    const { coordinate, title } = this.props;
    const { activeMap, activeMarker, showingInfoWindow } = this.state;

    return (
      <Map google={this.props.google} zoom={14} initialCenter={coordinate}>
        <Marker
          title={title}
          icon={{
            url: require('../../assets/img/icon-location-area@3x.png'),
            scaledSize: new google.maps.Size(48, 48)
          }}
          onClick={this.onMarkerClick}
        />
        <InfoWindow
          google={this.props.google}
          marker={activeMarker as any}
          map={activeMap as any}
          visible={showingInfoWindow}
        >
          <div
            style={{
              height: 40,
              boxShadow: '0 0 2 0 rgba(0, 0, 0, 0.3)',
              fontSize: 14,
              fontWeight: 500,
              color: '#001a33',
              lineHeight: '40px'
            }}
          >
            {title}
          </div>
        </InfoWindow>
      </Map>
    );
  }
}
export default GoogleApiWrapper({
  apiKey: 'AIzaSyDiLfWQJ514WxulsSV6cg40qM3wI3ARLQw'
})(MapContainer);
