import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Badge } from 'antd';
import Drawer from '../../components/Drawer';
import styles from './index.module.scss';
import { AppStore } from '../../../../store/reducers';
import _get from '../../../../common/get';
import Button from '../../../../components/Btn';
import Sign from '../../components/Sign';
import { isNeedSignPad } from '../../../../common/isNeedSingPad';
import { asyncSetPadInfo } from '../../../../store/actions/pad';
import { FormattedMessage } from 'react-intl';

const Item = (props: any) => {
  const isSigned = _get(props, ['padInfo', 'is_signed']);
  const onClick = () => {
    if (isSigned) {
      props.view && props.view();
    } else {
      props.sign && props.sign();
    }
  };

  return (
    <div className={styles.item}>
      <div className={styles.content}>
        <div className={styles.caption}>
          <Badge dot={props.needSingPad} offset={[5, 0]}>
            <FormattedMessage id="account_verification_caption" />
          </Badge>
        </div>
        <div className={styles.description}>
          <FormattedMessage id="account_verification_description" />
        </div>
      </div>
      <Button className={styles.button} onClick={onClick}>
        {props.isSigned ? (
          <FormattedMessage id="account_verification_button_view" />
        ) : (
          <FormattedMessage id="account_verification_button_sign" />
        )}
      </Button>
    </div>
  );
};

const Verification = (props: any) => {
  const [isVisible, setVisible] = useState(false);

  const { style, className = '', padInfo, asyncSetPadInfo } = props;
  const needSingPad = isNeedSignPad(props.userInfo, props.padInfo);

  const view = () => window.open(padInfo.doc);
  const sign = () => setVisible(true);
  const onClose = () => {
    asyncSetPadInfo();
    setVisible(false);
  };

  return (
    <div style={style} className={`${className} ${styles.container}`}>
      <Item
        padInfo={padInfo}
        view={view}
        sign={sign}
        isSigned={padInfo['is_signed']}
        needSingPad={needSingPad}
      />
      {isVisible ? (
        <Drawer visible={isVisible} width={840} onClose={onClose}>
          <Sign onClose={onClose} />
        </Drawer>
      ) : (
        ''
      )}
    </div>
  );
};

const mapStateToPros = (state: AppStore) => ({
  userInfo: state.user.userInfo,
  padInfo: state.pad
});

const mapDispatchToProps = (dispatch: any) => ({
  asyncSetPadInfo: () => dispatch(asyncSetPadInfo())
});

export default connect(
  mapStateToPros,
  mapDispatchToProps
)(Verification);
