import React from 'react';

import styles from './index.module.scss';
import IconFont from '../../ui/TradexIcon';
import { NoticeItemType, NoticeItem } from '../../../store/types/notice';
import util from '../../../common/util';
type Props = {
  noticeItemType: NoticeItemType;
  noticeItem: NoticeItem;
};

type State = any;

class NoticeItem1 extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
  }
  static defaultProps: Props;
  state = {};

  render() {
    const { noticeItem } = this.props;
    return (
      <div className={styles.noticeItemBox} style={{ borderBottom: 0 }}>
        {/* body */}
        <div className={styles.body} style={{ padding: 0 }}>
          <div className={styles.bodyLeftInfo}>
            <img
              className={styles.carImg}
              src={`${
                noticeItem.body.garage
                  ? noticeItem.body.garage.thumbnail
                  : noticeItem.body.rfq
                  ? noticeItem.body.rfq.thumbnail
                  : ''
              }?x-oss-process=style/web_640`}
            ></img>
            <img
              className={styles.brandLogo}
              src={
                noticeItem.body.garage
                  ? noticeItem.body.garage.make_logo
                  : noticeItem.body.rfq
                  ? noticeItem.body.rfq.make_logo
                  : ''
              }
            />
          </div>
          <div className={styles.bodyRightInfo} style={{ lineHeight: '1.5' }}>
            <div className={styles.title}>
              {noticeItem.subgroup === 'F' ? (
                <IconFont type="iconicon_offer_line" className={styles.offerIcon} />
              ) : (
                <IconFont type="iconicon_information" className={styles.offerIcon} />
              )}
              <span>{`[${noticeItem.body.content}]`}</span>
            </div>
            {noticeItem.subgroup === 'T' && noticeItem.body.action.action_params.due_time && (
              <div
                className={styles.timeLeft}
                style={{
                  color:
                    util.formatNoticeLeftTimeStr(
                      noticeItem.body.action.action_params.due_time || 0
                    ) === 'Expired'
                      ? '#f5313b'
                      : '#ff9910'
                }}
              >
                <IconFont type="iconicon_time_line" className={styles.offerIcon} />
                <span>
                  {util.formatNoticeLeftTimeStr(noticeItem.body.action.action_params.due_time || 0)}
                </span>
              </div>
            )}
          </div>
        </div>
      </div>
    );
  }
}

export default NoticeItem1;
