  componentDidMount() {
    // console.log('componentDidMount');
    // console.log('componentDidMount this.props.detail.id', this.props.detail.id);
    this.getDisbursementsData();
  }

  getDisbursementsData = () => {
    this.props.setActionStatus(ActionStatus.Loading);
    this.setState({
      dataTitle: {
        title: {
          value: ''
        },
        description: {
          value: ''
        }
      },
      dataArr0: [],
      dataArr1: [],
      dataArr2: []
    });
    // const { dataArr0, dataArr1, dataArr2 } = this.state;
    const _dataArr0: orderObj[] = [];
    const _dataArr1: orderObj[] = [];
    const _dataArr2: orderObj[] = [];
    const _dataTitle: dataTitle = {
      title: {
        value: ''
      },
      description: {
        value: ''
      }
    };
    // console.log('this.props.detail', this.props.detail);
    const {
      detail: { id },
      drawerConfig: { roleType, orderType }
    } = this.props;
    // console.log('id', id);
    disbursements(id)
      .then((res: any) => {
        // console.log(res);
        const data: orderObj[] = res.data.results;

        const isBuyer = roleType === UserRole.Buy;

        const isMainOrder = orderType === OrderType.Main;
        //  
        if (isMainOrder) {
          data.slice(1).map((item: orderObj, index: number) => {
            if (item.title.value.indexOf('Total Amount') > -1) {
              _dataTitle.title.value = item.title.value;
              _dataTitle.description.value = item.description.value;
            } else {
              if (item.title.value.indexOf('Color Option') > -1) {
                _dataArr0.push(item);
              } else {
                _dataArr1.push(item);
              }
            }
          });
        } else {
          //
          data.slice(1).map((item, inddex) => {
            if (item.title.value === 'Total Amount') {
              _dataTitle.title.value = item.title.value;
              _dataTitle.description.value = item.description.value;
              item.values.map((ii: orderObj, jj: number) => {
                _dataArr0.push(ii);
              });
            } else {
              if (
                item.title.value === 'Amount Due' ||
                item.title.value === 'Payments Pending Review' ||
                item.title.value === 'Confirmed Payments'
              ) {
                _dataArr1.push(item);
              } else {
                _dataArr2.push(item);
              }
            }
          });
        }
        this.setState({
          dataTitle: _dataTitle,
          dataArr0: _dataArr0,
          dataArr1: _dataArr1,
          dataArr2: _dataArr2
        });
        this.props.setActionStatus(ActionStatus.Init);
      })
      .catch((err: any) => {
        console.log(err);
      });
  };

