import React, { useState } from 'react';
import styles from './index.module.scss';

type ExtraProps = {
  className?: string;
  onClick?: (e: any) => any;
  onMouseEnter?: (e: any) => any;
};

const Menu = (props: any) => {
  const [selectedChildren, setSelectedChildren] = useState<any[]>(props.selectedChildren);

  const { style, className = '' } = props;

  const handleClick = (e: any) => {
    setSelectedChildren([e.selectedKey]);
    e.selected && props.onClick && props.onClick(e);
  };
  const renderChild = (child: React.ReactElement) => {
    let extraProps: ExtraProps = {};
    if (selectedChildren.includes(child.props.itemKey)) {
      extraProps.className = child.props.className + ` ${styles.selected}`;
    }
    extraProps.onClick = (e: any) => {
      handleClick({ ...e, selectedKey: child.props.itemKey, selected: true });
    };
    return React.cloneElement(child, extraProps);
  };

  return (
    <div style={style} className={`${styles.container} ${className}`}>
      {React.Children.map(props.children, (child: React.ReactElement) => renderChild(child))}
    </div>
  );
};

const Item = (props: any) => {
  const { style, className = '', itemKey, ...restProps } = props;
  return (
    <div style={style} className={`${styles.item} ${className}`} {...restProps}>
      {props.children}
    </div>
  );
};

Menu.Item = Item;

export default Menu;
