import React, { useState } from 'react';
import { FormattedMessage } from 'react-intl';
import { Divider, Button, Spin, message } from 'antd';
import { Modal } from '../../../../components/AlertModal';
import { post } from '../../../../api/http';
import { ModalProps, ModalType } from './types';
import { ModalConfig } from './utils';
import _get from '../../../../common/get';
import styles from './index.module.scss';

const ChangeNameModal = (props: ModalProps) => {
  const [value, setValue] = useState('');
  const [isEmpty, setIsEmpty] = useState(true);
  const [warning, setWarning] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  const { onClose = () => {}, type = ModalType.Username, updateUserInfo } = props;
  const { text, verify, getPostParam } = ModalConfig[type];

  const onChange = (e: any) => {
    const inputVerify = _get(verify, ['onInput']);
    let value = e.target.value;
    let valueLength = value.trim().length;
    setValue(value);
    valueLength < 1 ? setIsEmpty(true) : setIsEmpty(false);
    if (inputVerify) {
      inputVerify(value) ? setWarning(false) : setWarning(true);
    } else {
      setWarning(false);
    }
  };

  const onClick = async () => {
    const submitVerify = _get(verify, ['onSubmit']);
    if (submitVerify) {
      if (submitVerify(value)) {
        setWarning(false);
      } else {
        setWarning(true);
        return;
      }
    }
    if (!warning && !isEmpty) {
      try {
        setIsLoading(true);
        await post('users/edit/', getPostParam(value));
        updateUserInfo && updateUserInfo();
      } catch (e) {
        if (e.response) {
          const error = _get(e, ['response', 'data', 'message']);
          message.error(error || 'Error');
        } else {
          message.error(e.message);
        }
      } finally {
        setIsLoading(false);
        onClose();
      }
    }
  };

  const TipsOrWarning = (
    <div className={warning ? styles.warning : styles.tips}>
      <FormattedMessage id={warning ? text.warning : text.tips} />
    </div>
  );
  const ButtonArea = (
    <div className={styles.ButtonArea}>
      <Button disabled={isEmpty} className={styles.button} onClick={onClick}>
        <FormattedMessage id={text.button} />
      </Button>
    </div>
  );
  const Content = (
    <div className={styles.content}>
      <div className={styles.title}>
        <FormattedMessage id={text.title} />
      </div>
      <Divider style={{ marginTop: '0px', marginBottom: '0px' }} />
      <div className={styles.input}>
        <input
          {...{ value, onChange }}
          style={{ caretColor: `#3acdfe` }}
          autoFocus={true}
          className={warning ? styles.WarningInput : ''}
        />
        {TipsOrWarning}
      </div>
      <Divider style={{ marginTop: '0px', marginBottom: '0px' }} />
      {ButtonArea}
    </div>
  );

  return (
    <Modal
      className={type === ModalType.Username ? styles.UsernameModal : styles.EmailModal}
      onClose={onClose}
    >
      {isLoading ? <Spin /> : Content}
    </Modal>
  );
};

export default ChangeNameModal;
export { ModalType };
