import Cookies from 'js-cookie';

export function getCookie(key = ''): string | undefined {
  return Cookies.get(key);
}

export function setCookie(key: string, value: string | object, opt?: object): void {
  return Cookies.set(key, value, opt);
}

export function removeCookie(key = ''): void {
  return Cookies.remove(key);
}
