import React from 'react';
import styles from './index.module.scss';
import { Button } from 'antd';
import IconFont from '../../ui/TradexIcon';

type PercerntInputProps = {
  style?: any;
  className?: any;
  value?: string;
  min?: number;
  max?: number;
  onChange?: (value: string) => any;
};

export const PercentInput = (props: PercerntInputProps) => {
  const { value = '1', min = 1, max = 100, onChange } = props;

  const decrease = () => {
    let num = Number(value);
    num -= 5;
    if (num < min) {
      num = min;
    }
    onChange && onChange(num.toString());
  };
  const increase = () => {
    let num = Number(value);
    if (num === 1) {
      num = 5;
    } else {
      num += 5;
    }
    onChange && onChange(num.toString());
  };

  return (
    <div className={styles.content}>
      <Button className={styles.button} onClick={decrease} disabled={Number(value) <= min}>
        <IconFont type="iconicon_minus_line_thin" />
      </Button>
      <div className={styles.divider}></div>
      <div className={styles.value}>{`${value}%`}</div>
      <div className={styles.divider}></div>
      <Button className={styles.button} onClick={increase} disabled={Number(value) >= max}>
        <IconFont type="iconicon_add_line_thin" />
      </Button>
    </div>
  );
};
