import {
  Login,
  Register,
  Home,
  BuyMarket,
  VisitorMarket,
  SellMarket,
  SellGarage,
  CarDetail,
  Error,
  Order,
  Account,
  CreateCar
} from '../containers';

export enum path {
  Index = '/',
  Login = '/login',
  Register = '/register',
  Home = '/home',
  BuyMarket = '/trade/buy/market/list',
  VisitorMarket = '/trade/buy/market/visitor',
  BuyCarDetail = '/trade/buy/market/cardetail',
  SellMarket = '/trade/sell/market/list',
  SellGarage = '/trade/sell/garage/list',
  SellCarDetail = '/trade/sell/market/cardetail',
  Order = '/trade/order',
  Account = '/trade/account',
  CreateCar = '/createcar',
  NotFound = '/404'
}

export interface RouteTemplate {
  path: string;
  component: any;
  auth: boolean;
  title?: string;
  withBasicLayout?: boolean;
  layoutBackColor?: string;
}

const routes: RouteTemplate[] = [
  {
    path: path.Index, // 
    component: BuyMarket,
    auth: true,
    title: 'TRADE X | Home'
  },
  {
    path: path.Login,
    component: Login,
    auth: false
  },
  {
    path: `${path.Register}/:path/:paperStatus?`,
    component: Register,
    auth: true
  },
  {
    path: path.Home,
    component: Home,
    auth: false
  },
  {
    path: path.BuyMarket,
    component: BuyMarket,
    auth: true,
    title: 'TRADE X | Market List'
  },
  {
    path: path.VisitorMarket,
    component: VisitorMarket,
    auth: false,
    title: 'TRADE X | Market List'
  },
  {
    path: path.SellMarket,
    component: SellMarket,
    auth: true,
    title: 'TRADE X | My Car'
  },
  {
    path: path.SellGarage,
    component: SellGarage,
    auth: true,
    title: 'TRADE X | My Car'
  },
  {
    path: `${path.BuyCarDetail}/:id`,
    component: CarDetail,
    auth: true,
    withBasicLayout: true,
    layoutBackColor: 'white',
    title: 'TRADE X | Car Detail'
  },
  {
    path: `${path.BuyCarDetail}/:id/:operation`,
    component: CarDetail,
    auth: true,
    withBasicLayout: true,
    layoutBackColor: 'white',
    title: 'TRADE X | Car Detail'
  },
  {
    path: `${path.Order}/:role`,
    component: Order,
    auth: true,
    withBasicLayout: true,
    title: 'TRADE X | Order Center'
  },
  {
    path: `${path.Account}/:type`,
    component: Account,
    auth: true,
    withBasicLayout: true,
    title: 'TRADE X | Account'
  },
  {
    path: `${path.Account}/:type/:operation`,
    component: Account,
    auth: true,
    withBasicLayout: true,
    title: 'TRADE X | Account'
  },
  {
    path: `${path.CreateCar}`,
    component: CreateCar,
    auth: true,
    withBasicLayout: true,
    title: 'TRADE X | Create Car'
  },
  {
    path: '*',
    component: Error,
    auth: false
  }
];

export const defaultRoute: string = path.BuyMarket;
export const defaultDocTitle = 'TRADE X - B2B Automotive Trading Platform, Trade X Global Inc';

export default routes;
