import React from 'react';
import Item from './Item';
import Titel from './Title';
import InstructionTag from './InstructionTag';

type TitleItemProps = {
  style?: React.CSSProperties;
  className?: any;
  name: string;
  results: any[];
};

const TitleItem = ({ style, className = '', name, results }: TitleItemProps) => {
  const result = results.find(result => result.type === 'block_title');
  const title = result && result.data && result.data[0] && result.data[0].title;
  return (
    <Item style={style} className={className}>
      <Titel name={name} title={title} />
      {/* <InstructionTag /> */}
    </Item>
  );
};

export default TitleItem;
