import React from 'react';

const styles = {
  wrapper: {
    display: 'inline-block'
  },
  item: {
    display: 'inline-block',
    borderRadius: '50%',
    boxShadow: '0 0 1px #ebebeb'
  }
};

export default function TeriorColor(props: {
  inColor: string;
  exColor: string;
  size?: number;
  space?: number;
  className?: string;
  style?: React.CSSProperties;
}) {
  const { inColor, exColor, size = 16, space = 4, className = '', style = {} } = props;
  const sizeInfo = {
    width: size,
    height: size
  };
  return (
    <div className={className} style={{ ...styles.wrapper, height: size, ...style }}>
      <span
        style={{ ...styles.item, backgroundColor: inColor, ...sizeInfo, marginRight: space }}
      ></span>
      <span style={{ ...styles.item, backgroundColor: exColor, ...sizeInfo }}></span>
    </div>
  );
}
