import React, { CElement, Component } from 'react';
import {
  Upload,
  Icon,
  message,
  Select,
  Input,
  Checkbox,
  Button,
  Form,
  notification,
  Spin,
  Alert,
  Modal
} from 'antd';
import IconFont from '../../components/ui/TradexIcon';
import { RouteComponentProps, Route, withRouter, Redirect } from 'react-router-dom';
