import React from 'react';
import styles from './index.module.scss';
import { FormattedNumber } from 'react-intl';
import TradexIcon from '../TradexIcon';

export default function Mileage(props: {
  iconSize: number;
  textSize: number;
  space: number;
  value: number;
  style?: React.CSSProperties;
}) {
  const { iconSize, textSize, space, value, style } = props;
  const textStyle = {
    fontSize: textSize,
    marginLeft: space,
    marginTop: 1
  };
  return (
    <div className={styles.miles} style={style}>
      <TradexIcon type="iconicon_mileage_area" size={iconSize} color="#198cff" />
      <span style={textStyle}>
        <FormattedNumber value={value}></FormattedNumber>
        KM
      </span>
    </div>
  );
}
