import { UserTypes, UserInfoStore, UserActions } from '../types';

export const initialState: UserInfoStore = {
  userInfo: {
    first_name: '',
    last_name: '',
    userId: '',
    phone: '',
    username: '',
    company_country: '',
    headshot: '',
    display_name: '',
    work_email: '',
    work_email_confirmed: false,
    position_at_company: '',
    company_name: '',
    company_operating_name: '',
    company_identification_number: '',
    tax_no: '',
    office_phone_number: '',
    company_address: '',
    company_address2: '',
    company_address3: '',
    city: '',
    postal_or_zip_code: '',
    company_country_full_name: '',
    province_state: ''
  }
};

// todo action as any
export default (state = initialState, action: UserActions): UserInfoStore => {
  switch (action.type) {
    case UserTypes.SET_USER_INFO: {
      return {
        ...state,
        userInfo: {
          userId: (action as any).userInfo.userId,
          first_name: (action as any).userInfo.first_name,
          last_name: (action as any).userInfo.last_name,
          phone: (action as any).userInfo.phone,
          username: (action as any).userInfo.username,
          company_country: (action as any).userInfo.company_country,
          headshot: (action as any).userInfo.headshot,
          display_name: (action as any).userInfo.display_name,
          work_email: (action as any).userInfo.work_email,
          work_email_confirmed: (action as any).userInfo.work_email_confirmed,
          position_at_company: (action as any).userInfo.position_at_company,
          company_name: (action as any).userInfo.company_name,
          company_operating_name: (action as any).userInfo.company_operating_name,
          company_identification_number: (action as any).userInfo.company_identification_number,
          tax_no: (action as any).userInfo.tax_no,
          office_phone_number: (action as any).userInfo.office_phone_number,
          company_address: (action as any).userInfo.company_address,
          company_address2: (action as any).userInfo.company_address2,
          company_address3: (action as any).userInfo.company_address3,
          city: (action as any).userInfo.city,
          postal_or_zip_code: (action as any).userInfo.postal_or_zip_code,
          company_country_full_name: (action as any).userInfo.company_country_full_name,
          province_state: (action as any).userInfo.province_state
        }
      };
    }
    default:
      return state;
  }
};
