import React from 'react';
import { Input as AntdInput } from 'antd';
import styles from './index.module.scss';

type InputProps = {
  className?: any;
  onChange?: any;
  placeholder?: any;
  value?: any;
  warning?: boolean;
  type?: 'edit' | 'view';
  suffix?: any;
};

const Input = (props: InputProps) => {
  const {
    className = '',
    placeholder = '',
    warning = false,
    value = '',
    type = 'edit',
    suffix
  } = props;

  const onChange = ({ target: { value } }: any) => props.onChange && props.onChange(value);

  const Edit = (
    <AntdInput
      className={`${styles.input} ${warning ? styles.warning : ''} ${className}`}
      value={value}
      onChange={onChange}
      placeholder={placeholder}
    />
  );

  const View = (
    <AntdInput.TextArea
      className={`${styles.input} ${className}`}
      value={value}
      onChange={onChange}
      autosize={{ minRows: 1 }}
      disabled={true}
      placeholder={placeholder}
    />
  );

  const InputWithSuffix = (
    <AntdInput
      className={`${styles.input} ${warning ? styles.warning : ''} ${className}`}
      value={value}
      onChange={onChange}
      placeholder={placeholder}
      suffix={suffix}
      disabled={true}
    />
  );

  if (suffix) {
    return InputWithSuffix;
  } else {
    return type === 'edit' ? Edit : View;
  }
};

export default Input;
