import React from 'react';
import lottie from 'lottie-web';

interface LottieProps {
  animationData: string;
  renderer?: 'svg' | 'canvas' | 'html';
  loop?: boolean;
  autoplay?: boolean;
  style?: any;
  className?: any;
}

const LottieIcon = (props: LottieProps) => {
  const {
    animationData,
    style,
    renderer = 'svg',
    loop = true,
    autoplay = true,
    className = ''
  } = props;
  const setLottie = (container: any) => {
    if (container) {
      lottie.loadAnimation({
        container, // dom
        renderer, //
        loop, //
        autoplay, //
        animationData // 
      });
    }
  };

  return <div style={style} className={className} ref={setLottie}></div>;
};

export default LottieIcon;
