import React from 'react';
import styles from './index.module.scss';
import AddCard from './AddCard';
import Card from './Card';
import _get from '../../../../common/get';
import { FormattedMessage } from 'react-intl';

type CardWrapperProps = {
  isPrimary?: boolean;
  setAsPad?: any;
  setAsPrimary?: any;
  onClick?: any;
};

const operationMessage = {
  primary: 'account_bankAccounts_cardOperation_primary',
  pad: 'account_bankAccounts_cardOperation_pad'
};

export const CardWrapper = (props: CardWrapperProps) => {
  const { isPrimary = false, setAsPad = () => {}, setAsPrimary = () => {}, ...restProps } = props;

  const isSignedPad = _get(props, ['padInfo', 'is_signed']);
  const isPad = _get(restProps, ['is_PAD']) || false;
  const showPrimary = !isPrimary;
  const showPad = !isPad && isSignedPad;
  const Primary = (
    <div className={styles.cursor} onClick={setAsPrimary}>
      <FormattedMessage id={operationMessage.primary} />
    </div>
  );
  const Divider = <div className={styles.divider}>|</div>;
  const Pad = (
    <div className={styles.cursor} onClick={setAsPad}>
      <FormattedMessage id={operationMessage.pad} />
    </div>
  );

  return (
    <div className={styles.CardWrapper}>
      <Card {...restProps} isPrimary={isPrimary} isPad={isPad} isSignedPad={isSignedPad} />
      <div className={styles.operation}>
        {showPrimary ? Primary : ''}
        {showPrimary && showPad ? Divider : ''}
        {showPad ? Pad : ''}
      </div>
    </div>
  );
};

export { AddCard, Card };
