import React, { useState, useEffect } from 'react';
import { Input as AntdInput } from 'antd';

const getFormattedNumber = (number: number): [boolean, string] => {
  const locales = 'en-US';
  const options = {
    minimumFractionDigits: 2,
    maximumFractionDigits: 2
  };
  const fromattedNumber = new Intl.NumberFormat(locales, options).format(number);
  if (fromattedNumber == 'NaN') {
    return [true, number.toString()];
  } else {
    return [false, fromattedNumber];
  }
};

const Input = ({ style, className = '', setWarning, prefix, ...restProps }: any) => {
  const [value, setValue] = useState(restProps.value);

  useEffect(() => setValue(restProps.value), [restProps.value]);

  const onChange = (e: any) => {
    restProps.onChange && restProps.onChange(e.target.value);
  };

  const onBlur = () => {
    let numberStr = value.replace(/\s/gi, '').replace(/,/gi, '');
    const reg = /^-?(0|[1-9][0-9]*)(\.[0-9]*)?$/;
    if (!isNaN(Number(numberStr)) && reg.test(numberStr)) {
      const [warning, result] = getFormattedNumber(Number(numberStr));
      setWarning && setWarning(warning);
      setValue(result);
    } else {
      setWarning && setWarning(true);
    }
  };

  return (
    <AntdInput
      style={style}
      className={className}
      prefix={prefix}
      onChange={onChange}
      onBlur={onBlur}
      value={value}
    />
  );
};

export default Input;
