import React, { useState } from 'react';
import { Select as AntdSelect, Radio, Input } from 'antd';
import styles from './index.module.scss';

type Currency = {
  code?: string;
  symbol?: string;
  name?: string;
  flag?: string;
  isChecked?: boolean;
};

type SelectProps = {
  style?: any;
  className?: any;
  options?: Array<Currency>;
  onChange?: any;
  warning?: boolean;
  value?: any;
  disabled?: boolean;
};

const Backfill = (props: any) => {
  const { flag, code, className = '' } = props;
  return (
    <div className={`${styles.backfill} ${className}`}>
      <img className={styles.flag} src={flag} />
      <div className={styles.code}>{code}</div>
    </div>
  );
};

const Item = (props: any) => {
  const { flag, name, code, symbol, isLastItem = false, isChecked = false } = props;
  return (
    <div className={`${styles.option} ${isLastItem ? styles.last : ''}`}>
      <img className={styles.image} src={flag} />
      <div className={styles.name}>{name}</div>
      <div className={styles.code}>{`${code}(${symbol})`}</div>
      <Radio className={styles.radio} value={name} checked={isChecked} />
    </div>
  );
};

const Select = (props: SelectProps) => {
  const [options, setOptions] = useState(
    (): Array<Currency> => {
      const initalOptions = props.options || ([] as Array<Currency>);
      return initalOptions.map(option => ({ ...option, isChecked: false }));
    }
  );

  const { className = '', warning = false, value, disabled = false } = props;

  const onChange = (value: string) => {
    const temp = [...options];
    const newOptions = temp.map(item =>
      item.name === value ? { ...item, isChecked: true } : { ...item, isChecked: false }
    ) as Array<Currency>;
    const selectedOption = newOptions.find(item => item.name === value);
    setOptions(newOptions);
    selectedOption && props.onChange && props.onChange(selectedOption.code);
  };

  const select = (
    <AntdSelect
      className={`${styles.select} ${warning ? styles.warning : ''} ${className}`}
      dropdownClassName={`${styles.dropdown}`}
      onChange={onChange}
      optionLabelProp="backfill"
    >
      {options.map((item: any, index: number) => {
        const isLastItem = index === options.length - 1;
        const optionProps = {
          value: item.name,
          backfill: <Backfill flag={item.flag} code={item.code} />
        } as any;
        return (
          <AntdSelect.Option {...optionProps}>
            <Item {...item} isLastItem={isLastItem} />
          </AntdSelect.Option>
        );
      })}
    </AntdSelect>
  );
  const renderInput = () => {
    const option = options.find(option => option.code === value);
    const prefix = option ? (
      <Backfill className={styles.inputBackfill} flag={option.flag} code={option.code} />
    ) : (
      ''
    );

    return (
      <Input
        className={`${styles.input} ${warning ? styles.warning : ''} ${className}`}
        prefix={prefix}
        disabled={true}
      />
    );
  };

  return disabled ? renderInput() : select;
};

export default Select;
