import store from 'store';

const getKey = function (key: string) {
  return store.get(key);
};

export function txLog() {
  let debugMode = getKey('debug');
  if (debugMode === '1') {
    console.log.apply(null, <any>arguments);
  }
}

export function txWarn() {
  let debugMode = getKey('debug');
  if (debugMode === '1') {
    console.warn.apply(null, <any>arguments);
  }
} ``

export function txError() {
  let debugMode = getKey('debug');
  if (debugMode === '1') {
    console.error.apply(null, <any>arguments);
  }
}
