import React, { useState, useEffect } from 'react';
import styles from './index.module.scss';
import { message as AntdMessage } from 'antd';
import { FormattedMessage } from 'react-intl';
import BankAccountItem from '../BankAccountItem';
import PriceInfoItem from '../PriceInfoItem';
import NoteItem from '../NoteItem';
import ImageItem from '../ImageItem';
import LockedRateButton from '../LockedRateButton';
import {
  isValidPrice,
  priceToNumberStr,
  getTrackerDetails,
  isCAtoCA,
  isEFT,
  getNote
} from '../utils';
import _get from '../../../../../../common/get';
import { post } from '../../../../../../api/http';
import {
  ImageInfo,
  ImageItemProps,
  BankAccount,
  TRACK_TYPE,
  MSG_TYPE,
  PAYMENT_METHODS,
  DrawerProps,
  ActionStatus
} from '../types';
import { Button } from 'antd';
import TitleItem from '../TitleItem';
import WarningItem from '../WarningItem';
import { FormattedPrice } from '../../../../../../components/FormattedPrice';

const getBankAccountInfo = (data: any): BankAccount => {
  let payDepositDetails = getTrackerDetails(data, TRACK_TYPE.PAY_REMAINING);
  let bankAccountDetail = payDepositDetails.find(
    detail => detail['msg_type'] === MSG_TYPE.REMAING_BANK_ACCOUNT
  );
  if (bankAccountDetail) {
    return _get(bankAccountDetail, ['extra', 'bank_account']);
  } else {
    const CNBankAccountDetail = payDepositDetails.find(
      detail => detail['msg_type'] === MSG_TYPE.CN_REMAING_INFO
    );
    return _get(CNBankAccountDetail, ['extra', 'balance_payment', 'bank_account']);
  }
};

const getPrice = (carPriceStr: string): number => {
  const carPrice = Number.parseFloat(carPriceStr);
  if (isNaN(carPrice)) {
    return 0;
  } else {
    return carPrice;
  }
};

const getTotalInclShippingValue = (payment: any): any => {
  const carPrice = getPrice(_get(payment, ['net_pay_out']));
  const shipmentFee = getPrice(_get(payment, ['shipment_fee']));
  const currency = _get(payment, ['buyer_currency']);
  return <FormattedPrice value={carPrice + shipmentFee} currency={currency} />;
};

const getTotalInclShippingRow = (payment: any) => {
  const field_name = [
    {
      value: <FormattedMessage id="drawer_payRemaining_totalInclShipping" />
    }
  ];
  const field_value = [{ value: getTotalInclShippingValue(payment) }] || [{ value: '--' }];
  return { field_name, field_value };
};

const getNoCNPriceInfo = (payment: any) => {
  const { rows } = payment;
  const TotalInclShippingRow = getTotalInclShippingRow(payment);
  const DepositRow = rows[1];
  const RemainingPayment = rows[rows.length - 1];
  return [TotalInclShippingRow, DepositRow, RemainingPayment];
};

const getPayment = (data: any) => {
  let payDepositDetails = getTrackerDetails(data, TRACK_TYPE.PAY_REMAINING);
  let paymentMsg = payDepositDetails.find(detail => detail['msg_type'] === MSG_TYPE.REMAING_INFO);
  if (paymentMsg) {
    let payment = _get(paymentMsg, ['extra', 'payment']);
    let rows = getNoCNPriceInfo(payment);
    return { ...payment, rows };
  } else {
    paymentMsg = payDepositDetails.find(detail => detail['msg_type'] === MSG_TYPE.CN_REMAING_INFO);
    let payment = _get(paymentMsg, ['extra', 'balance_payment', 'payment']);
    payment = { ...payment, isCN: true };
    return payment;
  }
};

const PayRemaining = ({ style, className = '', detail, setActionStatus, data }: DrawerProps) => {
  const [images, setImages] = useState([] as Array<ImageInfo>);
  const [amount, setAmount] = useState('');
  const [uploadWarning, setUploadWarning] = useState(false);
  const [inputWarning, setInputWarning] = useState(false);
  const [results, setResults] = useState([] as any[]);
  const [payment, setPayment] = useState({
    from_currency: '',
    to_currency: '',
    amount: 0,
    is_book_rate: false
  } as {
    from_currency: string;
    to_currency: string;
    amount: number | string;
    is_book_rate: boolean;
  });
  const [paymentId, setPaymentId] = useState(() => {
    const suborders: any[] = data && data.suborders && data.suborders.length ? data.suborders : [];
    const sub = suborders.find(item => detail.id === item.id);
    const actions: any[] = sub && sub.actions && sub.actions.length ? sub.actions : [];
    const action = actions.find(item => item.action_type === 15);
    const paymentId = _get(action, ['payment_id']) || '';
    return paymentId;
  });

  useEffect(() => {
    post('paying/paying_conf/', { payment_id: paymentId })
      .then(res => {
        setResults(_get(res, ['data', 'results']));
        setPayment(_get(res, ['data', 'payment']));
      })
      .catch((e: any) => {
        const mess = _get(e, ['response', 'data', 'detail']) || 'ERROR';
        AntdMessage.error(mess);
        setActionStatus(ActionStatus.Error);
      });
  }, []);

  const isCA2CA = isCAtoCA(data['warehouse'], data['dest_port']);
  const { buyer_currency: buyerCurrency } = detail;

  const imagesChange = (images: Array<ImageInfo>) => {
    images.length > 0 ? setUploadWarning(false) : setUploadWarning(true);
    setImages(images);
  };
  const amountChange = (value: string) => setAmount(value);

  const submit = () => {
    const isValidImages = images.length > 0;
    const isValidAmount = isValidPrice(amount);
    setUploadWarning(!isValidImages);
    setInputWarning(!isValidAmount);
    if (isValidImages && isValidAmount && paymentId) {
      const param = {
        images: images.map(item => item.url),
        amount_claimed: Number(priceToNumberStr(amount))
      };
      setActionStatus(ActionStatus.Loading);
      post(`payments/${paymentId}/upload_evidence/`, param)
        .then(() => setActionStatus(ActionStatus.Success))
        .catch(() => setActionStatus(ActionStatus.Error));
    }
  };
  const submitEFT = (id: string) => {
    const param = {
      images: [],
      quote_id: id
    };
    setActionStatus(ActionStatus.Loading);
    post(`payments/${paymentId}/upload_evidence/`, param)
      .then(() => setActionStatus(ActionStatus.Success))
      .catch(() => setActionStatus(ActionStatus.Error));
  };

  const submitCA2CAEFT = () => {
    setActionStatus(ActionStatus.Loading);
    post(`payments/${paymentId}/upload_evidence/`, {})
      .then(() => setActionStatus(ActionStatus.Success))
      .catch(() => setActionStatus(ActionStatus.Error));
  };

  const getImageItemProps = (): ImageItemProps => {
    const currency = buyerCurrency;
    return {
      className: `${styles.imageItem} ${uploadWarning || inputWarning ? styles.warning : ''}`,
      images,
      imagesChange,
      amount,
      amountChange,
      uploadWarning,
      setUploadWarning,
      inputWarning,
      setInputWarning,
      currency
    };
  };

  return (
    <div style={style} className={`${className} ${styles.container}`}>
      <TitleItem className={styles.titleItem} name="drawer_payDeposit_title" results={results} />
      <WarningItem className={styles.warningItem} results={results} />
      {!isEFT(results) ? (
        <BankAccountItem className={styles.bankAccountItem} results={results} />
      ) : (
        ''
      )}
      <PriceInfoItem
        className={`${styles.depositInfoItem} ${isEFT(results) ? styles.EFTitem : ''}`}
        {...{
          results,
          payment,
          totalPrice: _get(detail, ['total_price']),
          isEFT: isEFT(results),
          isCA2CA
        }}
      />
      {isEFT(results) ? (
        isCA2CA ? (
          <NoteItem
            className={styles.noteItem}
            content={<FormattedMessage id="drawer_payDeposit_CA2CANote" />}
          />
        ) : (
          <NoteItem className={styles.noteItem} content={getNote(results)} />
        )
      ) : (
        ''
      )}
      {!isEFT(results) ? <ImageItem {...getImageItemProps()} /> : ''}
      {!isEFT(results) ? (
        <Button className={styles.submit} onClick={submit}>
          <FormattedMessage id="drawer_payDeposit_submit" />
        </Button>
      ) : isCA2CA ? (
        <Button className={styles.submit} onClick={submitCA2CAEFT}>
          <FormattedMessage id="drawer_payRemaining_CA2CASubmit" />
        </Button>
      ) : (
        <LockedRateButton className={styles.submit} submit={submitEFT} payment={payment} />
      )}
    </div>
  );
};

export default PayRemaining;
