import axios from 'axios';

export const CancelToken = axios.CancelToken;

export let cancel: any;

import { getCookie, removeCookie } from '../../common/cookie';
import { createLog } from '../log';
import Security from '../../common/security';
// import Notification from '../components/Notification';

// ENV
let isDevelopment = process.env.NODE_ENV === 'development';

// create an axios instance
const GET_TIMEOUT = 30000;
const OTHER_TIMEOUT = 30000;

const API_ROOT = process.env.API_ROOT ? process.env.API_ROOT + '/api' : '/api';

const service = axios.create({
  baseURL: API_ROOT.replace(new RegExp('"', 'g'), '')
});

// request interceptor
service.interceptors.request.use(
  config => {
    // store.getters.token
    let token = getCookie('Authorization');
    if (token && (config.url as string).indexOf('signup-signin') == -1) {
      config.headers['Authorization'] = getCookie('Authorization');
    }
    config.headers['Accept-Language'] = 'en';
    config.headers['Tradex-Client-Type'] = 'web';

    // timeout settings
    if (config.method && config.method === 'get') {
      config.timeout = GET_TIMEOUT;
    } else {
      config.timeout = OTHER_TIMEOUT;
    }

    if (config.cancelToken) {
      config.cancelToken = new CancelToken(function executor(c) {
        // An executor function receives a cancel function as a parameter
        cancel = c;
      });
    }

    // security
    let url = isDevelopment ? config.baseURL + '/' + config.url : '/api/' + config.url;
    let signature = Security.createSignature(url);

    config.headers['tx-signature'] = signature;
    return config;
  },
  error => {
    console.warn('-------Request Interceptor---------');
    console.warn('ERROR: ', error);
    console.warn('-----------------------------------');
    return Promise.reject(error);
  }
);

// response interceptor
service.interceptors.response.use(
  response => response as any,
  error => {
    const TIMEOUT_ERR_STRING = 'Request Timeout';

    let { status } = error.response || { status: undefined };
    // let { responseURL } = error.request;
    let { data: { detail = TIMEOUT_ERR_STRING } = { detail: TIMEOUT_ERR_STRING } } =
      error.response || {};

    // if (process.env.NODE_ENV === 'development') {
    //   console.log('-------Response Interceptor---------');
    //   console.log('ERROR: ', error);
    //   // console.log('RESPONSE: ', error.response);
    //   // console.log('REQUEST: ', error.request);
    //   console.log('-----------------------------------');
    // }

    if (!status) {
      // console.log(' NO HTTP STATUS');
      ShowError(detail);
      return Promise.reject(error);
    }

    const { config: { url = '' } = { url: '' } } = error.response || {};
    const { host = '', href = '' } = window.location || {};
    let log_text = {
      request_url: url,
      status: status,
      error: detail
    };
    if ([404, 500, '404', '500'].includes(status) || detail === TIMEOUT_ERR_STRING) {
      // createLog({
      //   log_type: 'api',
      //   url: href,
      //   host: host,
      //   log_text: JSON.stringify(log_text),
      //   level: 40
      // });
    }
    // 404
    if ([404, '404'].includes(status)) {
      detail = detail || 'Sorry, the requested api not found';
    }

    // api => code 401 as unauthorized or token has expired
    // console.log(error && error.response && error)
    if (status === '401' || status === 401) {
      try {
        //Authorization cookies
        removeCookie('Authorization');
        window.location.href = '/login';
      } catch (exp) {
        __tx__.log(exp);
      }
    }
    __tx__.log('rejected', error);
    return Promise.reject(error);
  }
);

function ShowError(err: any, title = 'Error') {
  // Notification({
  //   title: title,
  //   message: err,
  //   type: 'error',
  //   duration: 4000
  // });
}

export default service;
