import React from 'react';
import styles from './index.module.scss';
import Item from '../Item';
import _get from '../../../../../../common/get';
import IconFont from '../../../../../../components/ui/TradexIcon';

type PriceInfoProps = {
  style?: React.CSSProperties;
  className?: any;
  isCA2CA?: boolean;
  results: any[];
  payment: any;
  isEFT: boolean;
  totalPrice: string;
  type?: number;
};

const PriceInfoItem = ({ style, className, name, value }: any) => {
  return (
    <Item style={style} className={className}>
      <div className={styles.name}>{name}</div>
      <div className={styles.value}>{value}</div>
    </Item>
  );
};

const PriceInfo = ({ style, className, ...restProps }: PriceInfoProps) => {
  const { results = [], payment, isEFT, isCA2CA = false, totalPrice, type = 0 } = restProps;
  const rows = payment.rows || [];

  const priceInfo = () => {
    let items: any[] = [];
    const blockDetail = results.find(result => result.type === 'block_detail');
    const detail = blockDetail && blockDetail.data;
    const blockPercentage = results.find(result => result.type === 'block_percentage');
    const percentage = blockPercentage && blockPercentage.data;
    detail &&
      detail.length &&
      detail.map((item: any) => {
        items.push(
          <PriceInfoItem
            key={item.title}
            className={styles.item}
            name={item.title}
            value={item.value}
          />
        );
      });
    if (type !== 16) {
      percentage &&
        percentage.length &&
        percentage.map((item: any) => {
          items.push(
            <PriceInfoItem
              key={item.title}
              className={styles.item}
              name={item.title}
              value={item.value}
            />
          );
        });
    }
    return items;
  };
  const EFTDepositInfo = () => {
    const blockTotal = results.find(result => result.type === 'block_total');
    const total = blockTotal && blockTotal.data;
    const exchangeCurrency = total && total[0] && total[0]['exchange_currency'];
    if (exchangeCurrency) {
      return (
        <div className={styles.EFTitem}>
          <div className={styles.name}>{total && total.length && total[0].title}</div>
          <div className={styles.value}>
            <div className={styles.currency}>
              <div className={styles.first}>{total && total.length && total[0].value}</div>
              <div className={styles.second}>
                {total && total.length && total[0]['exchange_currency']}
              </div>
            </div>
            <div className={styles.exchange}>
              <IconFont type="iconicon_currencyconverter_line" />
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <PriceInfoItem
          className={`${styles.item} ${styles.lastItem}`}
          name={total && total.length && total[0].title}
          value={total && total.length && total[0].value}
        />
      );
    }
  };
  const NoEFTDepositInfo = () => {
    const blockTotal = results.find(result => result.type === 'block_total');
    const total = blockTotal && blockTotal.data;
    const exchangeCurrency = total && total[0] && total[0]['exchange_currency'];
    if (exchangeCurrency) {
      return (
        <div className={styles.EFTitem}>
          <div className={styles.name}>{total && total.length && total[0].title}</div>
          <div className={styles.value}>
            <div className={styles.currency}>
              <div className={styles.first}>{total && total.length && total[0].value}</div>
              <div className={styles.second}>
                {total && total.length && total[0]['exchange_currency']}
              </div>
            </div>
            <div className={styles.exchange}>
              <IconFont type="iconicon_currencyconverter_line" />
            </div>
          </div>
        </div>
      );
    } else {
      return (
        <PriceInfoItem
          className={`${styles.item} ${styles.lastItem}`}
          name={total && total.length && total[0].title}
          value={total && total.length && total[0].value}
        />
      );
    }
  };

  const priceItems = priceInfo();

  return (
    <div style={style} className={`${styles.container} ${className}`}>
      {priceItems.length > 0 ? priceItems : ''}
      {priceItems.length > 0 ? <div className={styles.divider}></div> : ''}
      {isEFT ? EFTDepositInfo() : NoEFTDepositInfo()}
    </div>
  );
};

export default PriceInfo;
