import React from 'react';
import styles from './index.module.scss';
import IconFont from '../ui/TradexIcon';

const ImageDiv = ({ style, className, ...restProps }: any) => {
  const { url, remove, zoom, children } = restProps;
  return (
    <div style={style} className={`${styles.container} ${className}`}>
      <img src={url} alt="" className={styles.image} />
      <div className={styles.mask}>
        {children ? (
          children
        ) : (
          <div className={styles.content}>
            <IconFont type="iconicon_zoom" className={styles.icon} onClick={zoom} />
            <div className={styles.divider}></div>
            <IconFont type="iconicon_delete_line" className={styles.icon} onClick={remove} />
          </div>
        )}
      </div>
    </div>
  );
};

export default ImageDiv;
