/**
 * get get(obj, [string key, string....])
 * @author zixiu
 * @since 1.0.1
 * @version 1.0
 * @example a.b.c get(a, ['b', 'c])
 */

export default function _get(data: any, path: string[] | string) {
  if (typeof path === 'string') {
    path = path.split('.');
  }
  var i,
    len = path.length;
  for (i = 0; typeof data === 'object' && i < len; ++i) {
    if (data === null || data === undefined) return false;
    data = data[path[i]];
  }
  return data;
}
