import React, { Component, SFC } from 'react';
import { Form, Input, Select, Radio, notification } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';
import { DrawerChildProps } from '../type';
import { AddVehicle } from '../../../../../api/order';
import _get from '../../../../../common/get';
import { injectIntl, IntlShape } from 'react-intl';
import { ActionStatus } from '../../index';
import IconFont from '../../../../../components/ui/TradexIcon';
import { compose } from 'redux';
import styles from './index.module.scss';

interface SelectBtnProps {
  value: string;
  name: string;
  hideColor?: boolean;
}

const SelectBtn: SFC<SelectBtnProps> = ({ value, name, hideColor }) => {
  return (
    <Radio className={styles.radioBtn} value={value}>
      {!hideColor && value && (
        <span className={styles.circle} style={{ backgroundColor: `#${value}` }} />
      )}
      <div className={styles.wrappedName}>{name}</div>
    </Radio>
  );
};

interface IProps extends FormComponentProps, DrawerChildProps {
  intl: IntlShape;
}

interface colorOptionsType {
  interior_color: colorItem;
  exterior_color: colorItem;
  [x: string]: any;
}
interface colorItem {
  description: string;
  id: string;
  rgb: string;
  [x: string]: any;
}

interface IState {
  exteriorColor: colorItem[];
  interiorColor: colorItem[];
  loading: boolean;
}

class AddVehicleDrawer extends Component<IProps, IState> {
  static defaultProps: IProps;
  private form = this.props.form;

  state = {
    exteriorColor: [],
    interiorColor: [],
    loading: false
  };

  handleDrawer = (status: ActionStatus) => this.props.setActionStatus(status);

  handleForm = (e: React.FormEvent) => {
    if (this.state.loading || !this.props.detail) return;
    e.preventDefault();
    this.setState({ loading: true });
    this.handleDrawer(ActionStatus.Loading);
    const { validateFields, setFieldsValue } = this.form;
    validateFields(async (err: any, values: any) => {
      try {
        if (!err) {
          const {
            detail: { id, order_type }
          } = this.props;
          const { status } = await AddVehicle(id, order_type, {
            ...values
          });
          status === 200 && this.handleDrawer(ActionStatus.Success);
        }
      } catch (e) {
        const message =
          _get(e, ['response', 'data', 'message']) || 'Submission failed, please try again';
        if (_get(e, ['response', 'data', 'code']) === 'VIN_INVALID') {
          setFieldsValue({ vin: '' });
          validateFields();
          return;
        }
        notification.error({
          message: 'Add vehicle error',
          description: message
        });
        this.handleDrawer(ActionStatus.Error);
      } finally {
        this.setState({ loading: false });
      }
    });
  };

  initialColor = () => {
    try {
      const {
        data: { color_options }
      } = this.props;
      const exteriorColor: colorItem[] = [];
      const interiorColor: colorItem[] = [];
      color_options.map((item: colorOptionsType, i: number, all: any[]) => {
        !exteriorColor.some(v => v.id === item.exterior_color.id) &&
          exteriorColor.push(item.exterior_color);
        !interiorColor.some(v => v.id === item.exterior_color.id) &&
          interiorColor.push(item.interior_color);
      });
      this.setState({ exteriorColor, interiorColor });
    } catch (error) {
      notification.error({
        message: 'Drawer Initial error',
        description: 'Add vehicle initial error'
      });
      this.handleDrawer(ActionStatus.Error);
    }
  };

  componentDidMount() {
    this.initialColor();
  }

  render() {
    const { getFieldDecorator } = this.form;
    const { exteriorColor, interiorColor } = this.state;
    const { intl } = this.props;
    const km = intl.formatMessage({
      id: 'order_drawer_addVehicle_distanceUnit_km',
      defaultMessage: 'KM'
    });
    const m = intl.formatMessage({
      id: 'order_drawer_addVehicle_distanceUnit_m',
      defaultMessage: 'MI'
    });
    const selectRoot = () => document.getElementById('wrap') || document.body;

    return (
      <div id="wrap" className={styles.wrap}>
        <div className={styles.title}>Add a Vehicle</div>
        <div className={styles.container}>
          <Form layout="vertical" hideRequiredMark>
            <Form.Item label="VIN" className={styles.vin}>
              {getFieldDecorator('vin', {
                rules: [
                  { required: true, message: 'Valid VIN Required' },
                  { len: 17, message: 'Valid VIN Required' }
                ],
                validateTrigger: 'onBlur'
              })(<Input />)}
            </Form.Item>
            <div className={styles.flexRow}>
              <Form.Item label="Exterior Color" className={styles.color}>
                {getFieldDecorator('exterior_color', {
                  initialValue:
                    exteriorColor.length === 1 ? (exteriorColor[0] as colorItem).rgb : '',
                  rules: [{ required: true, message: 'Required' }]
                })(
                  <Radio.Group style={{ width: '100%' }}>
                    {exteriorColor.length > 0 && (
                      <Select
                        getPopupContainer={selectRoot}
                        className={styles.hackDropdown}
                        dropdownClassName="add-vehicle-dropdown"
                        suffixIcon={
                          <IconFont
                            type="iconicon_dropdown_line"
                            className={styles.selectDownIcon}
                          />
                        }
                        defaultValue={
                          exteriorColor.length === 1 ? (exteriorColor[0] as colorItem).rgb : null
                        }
                      >
                        {exteriorColor.map((v: colorItem) => (
                          <Select.Option value={v.rgb} key={v.id}>
                            <SelectBtn value={v.rgb} name={v.description} />
                          </Select.Option>
                        ))}
                      </Select>
                    )}
                  </Radio.Group>
                )}
              </Form.Item>
              <Form.Item label="Interior Color" className={styles.color}>
                {getFieldDecorator('interior_color', {
                  initialValue:
                    interiorColor.length === 1 ? (interiorColor[0] as colorItem).rgb : '',
                  rules: [{ required: true, message: 'Required' }]
                })(
                  <Radio.Group style={{ width: '100%' }}>
                    {interiorColor.length > 0 && (
                      <Select
                        getPopupContainer={selectRoot}
                        className={styles.hackDropdown}
                        dropdownClassName="add-vehicle-dropdown"
                        suffixIcon={
                          <IconFont
                            type="iconicon_dropdown_line"
                            className={styles.selectDownIcon}
                          />
                        }
                        defaultValue={
                          interiorColor.length === 1 ? (interiorColor[0] as colorItem).rgb : null
                        }
                      >
                        {interiorColor.map((v: colorItem) => (
                          <Select.Option value={v.rgb} key={v.id}>
                            <SelectBtn value={v.rgb} name={v.description} />
                          </Select.Option>
                        ))}
                      </Select>
                    )}
                  </Radio.Group>
                )}
              </Form.Item>
            </div>
            <div className={styles.flexRow}>
              <span className={styles.spectialLable}>
                Estimated Mileage when arriving warehouse
              </span>
              <Form.Item className={styles.distance_to_warehouse}>
                {getFieldDecorator('distance_to_warehouse', {
                  rules: [{ required: true, message: 'Valid Number Required' }],
                  validateTrigger: 'onBlur',
                  getValueFromEvent: event => {
                    return event.target.value.replace(/\D/g, '');
                  }
                })(<Input />)}
              </Form.Item>
              <Form.Item className={styles.distance_unit}>
                {getFieldDecorator('distance_unit', { initialValue: 'KM' })(
                  <Radio.Group style={{ width: '100%' }}>
                    <Select
                      className={styles.hackDropdown}
                      dropdownClassName="add-vehicle-dropdown"
                      getPopupContainer={selectRoot}
                      defaultValue="KM"
                      suffixIcon={
                        <IconFont type="iconicon_dropdown_line" className={styles.selectDownIcon} />
                      }
                    >
                      <Select.Option value={km}>
                        <SelectBtn name={km} value={km} hideColor />
                      </Select.Option>
                      <Select.Option value={'M'}>
                        <SelectBtn name={m} value={m} hideColor />
                      </Select.Option>
                    </Select>
                  </Radio.Group>
                )}
              </Form.Item>
            </div>
          </Form>
        </div>
        <button onClick={this.handleForm} className={styles.bottomBtn}>
          Submit
        </button>
      </div>
    );
  }
}

export default compose(
  injectIntl,
  Form.create<IProps>()
)(AddVehicleDrawer);
