import React from 'react';
import { Descriptions } from '../Descriptions';
import { FormattedPrice } from '../../FormattedPrice';
import { InputNumber, InputNumberWithPrefix } from '../../InputNumber';
import styles from './index.module.scss';
import { FormattedMessage } from 'react-intl';

type Props = any;
type State = any;

export class CarPrice extends React.Component<Props, State> {
  constructor(props: any) {
    super(props);
  }

  renderByType(option: { type: string; price: string; msrp: string }) {
    let type = option.type;
    if (type === 'BuyNow') {
      return this.renderBuyNow(option.price);
    } else if (type === 'CarPrice') {
      return this.renderCarPrice();
    } else if (type === 'MSRP') {
      return this.renderMSRP(option.msrp);
    }
  }

  renderBuyNow(price: any) {
    let { currency } = this.props;
    return (
      <Descriptions.Row>
        <div className={styles.name}>
          <FormattedMessage id="makeOffer_carPrice_buyNowPrice" defaultMessage="Buy now price" />
        </div>
        <div className={styles.value}>
          <FormattedPrice value={Number(price)} currency={currency} />
        </div>
      </Descriptions.Row>
    );
  }

  renderMSRP(msrp: string) {
    let { additionalFeeChange, currency, additionalFee } = this.props;
    return (
      <React.Fragment>
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage id="makeOffer_carPrice_msrp" defaultMessage="MSRP" />
          </div>
          <div className={styles.value}>
            <FormattedPrice value={Number(msrp)} currency={currency} />
          </div>
        </Descriptions.Row>
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage
              id="makeOffer_carPrice_additionalFee"
              defaultMessage="Additional fee"
            />
          </div>
          <div className={styles.value}>
            <InputNumberWithPrefix
              value={additionalFee}
              onChange={(value: string) => additionalFeeChange(value)}
            />
          </div>
        </Descriptions.Row>
      </React.Fragment>
    );
  }

  renderCarPrice() {
    let { carPriceChange, price = '0', currency, msrp } = this.props;
    return (
      <React.Fragment>
        {this.props.isNewCar ? (
          <Descriptions.Row>
            <div className={styles.name}>
              <FormattedMessage id="makeOffer_carPrice_msrp" defaultMessage="Buy now price" />
            </div>
            <div className={styles.value}>
              <FormattedPrice value={Number(msrp)} currency={currency} />
            </div>
          </Descriptions.Row>
        ) : (
          ''
        )}
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage id="makeOffer_carPrice_carPrice" defaultMessage="Buy now price" />
          </div>
          <div className={styles.value}>
            <InputNumberWithPrefix
              value={price}
              inputStyle={{ width: 120 }}
              onChange={(value: string) => carPriceChange(value)}
            />
          </div>
        </Descriptions.Row>
      </React.Fragment>
    );
  }

  title = (
    <div className={styles.title}>
      <FormattedMessage id="makeOffer_carPrice_carPrice" defaultMessage="Car Price" />
    </div>
  );

  numberOfCars = <React.Fragment></React.Fragment>;

  render() {
    let { style, className = '', type, price, msrp } = this.props;
    let option = {
      type,
      price,
      msrp
    };

    return (
      <Descriptions style={style} className={className} title={this.title}>
        {this.renderByType(option)}
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage
              id="makeOffer_carPrice_numberOfCars"
              defaultMessage="Number of cars"
            />
          </div>
          <div className={styles.value}>
            <InputNumber max={this.props.max} min={1} onChange={this.props.numberChange} />
          </div>
        </Descriptions.Row>
      </Descriptions>
    );
  }
}
