import { get, post } from './http';
import { AxiosPromise } from 'axios';

export function GetGarageDetail(id: string): AxiosPromise<any> {
  return get(`garages/${id}/`, {});
}

export function GetCountries(): AxiosPromise<any> {
  return get(`ports/countries/`, {});
}

export function GetProvinces(country: string): AxiosPromise<any> {
  return get(`ports/provinces/`, { country });
}

export function GetAllPorts(garage_id: string): AxiosPromise<any> {
  return get(`ports/all_ports/`, { garage_id });
}

export function GetPortsWithFee(data: { garageId: string; province: string }): AxiosPromise<any> {
  return get(`ports/with_shipment_fee/`, {
    garage_id: data.garageId,
    province: data.province
  });
}
