import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { FormattedMessage } from 'react-intl';
import styles from './index.module.scss';
import Profile from './containers/Profile';
import BankAccounts from './containers/BankAccounts';
import Verification from './containers/Verification';
import Menu from './components/Menu';
import _get from '../../common/get';
import { AppStore } from '../../store/reducers';
import { asyncSetPadInfo } from '../../store/actions/pad';
import { isNeedSignPad } from '../../common/isNeedSingPad';
import { Badge } from 'antd';
import { asyncSetBankCards, setPrimaryBankCard, setAsPad } from '../../store/actions/bankCards';

const navigations: any = {
  profile: 'account_navigations_item1',
  bank: 'account_navigations_item2',
  verification: 'account_navigations_item3'
};

const Account = (props: any) => {
  const [selectedItem, setSelectedItem] = useState(() => {
    const key = _get(props, ['match', 'params', 'type']) || 'profile';
    return navigations[key];
  });

  const needSingPad = isNeedSignPad(props.userInfo, props.padInfo);

  useEffect(() => {
    if (!props.padInfo.isUseful) {
      props.asyncSetPadInfo();
    }
    if (!props.bankCards.isUseful) {
      props.asyncSetBankCards();
    }
  }, [props]);

  return (
    <div className={styles.container}>
      <div className={styles.navigation}>
        <Menu
          className={styles.menu}
          onClick={(e: any) => setSelectedItem(e.selectedKey)}
          selectedChildren={[selectedItem]}
        >
          {Object.keys(navigations)
            .map(key => navigations[key])
            .map(item => (
              <Menu.Item className={styles.item} key={item} itemKey={item}>
                <div className={styles.itemText}>
                  <Badge dot={item === 'account_navigations_item3' && needSingPad} offset={[5, 0]}>
                    <FormattedMessage id={item} />
                  </Badge>
                </div>
              </Menu.Item>
            ))}
        </Menu>
      </div>
      <div className={styles.content}>
        {selectedItem === navigations.profile ? <Profile extraData={props} /> : ''}
        {selectedItem === navigations.bank ? (
          <BankAccounts
            setting={props.setting}
            bankCards={props.bankCards}
            padInfo={props.padInfo}
            getBankCards={props.asyncSetBankCards}
            setPrimaryBankCard={props.setPrimaryBankCard}
            setAsPad={props.setAsPad}
          />
        ) : (
          ''
        )}
        {selectedItem === navigations.verification ? (
          <Verification userInfo={props.userInfo} padInfo={props.padInfo} />
        ) : (
          ''
        )}
      </div>
    </div>
  );
};

const mapStateToPros = (state: AppStore) => ({
  setting: state.setting,
  userInfo: state.user.userInfo,
  padInfo: state.pad,
  bankCards: state.bankCards
});

const mapDispatchToProps = (dispatch: any) => ({
  asyncSetPadInfo: () => dispatch(asyncSetPadInfo()),
  asyncSetBankCards: () => dispatch(asyncSetBankCards()),
  setPrimaryBankCard: (id: string) => dispatch(setPrimaryBankCard(id)),
  setAsPad: (id: string) => dispatch(setAsPad(id))
});

export default connect(
  mapStateToPros,
  mapDispatchToProps
)(Account);
