    order_type,
    car_info,
    warehouse,
    ship_method,
    dest_port,
    chatuser,
    total_price,
    payment_method,
    created_at,
    garage,
    actions,
    links,
    total_cars,
    deposit_percentage,
    deposit_title,
    seller_currency
