import React from 'react';
import { Input } from 'antd';
import styles from './index.module.scss';

const prefixStyle = {
  width: 26,
  backgroundColor: '#ffffff',
  textAlign: 'center',
  color: '#001a33',
  borderRadius: 0
} as React.CSSProperties;

const inputStyle = {
  width: 89,
  color: '#3acdfe',
  borderRadius: 0
};

const groupStyle = {
  display: 'inline-flex',
  justifyContent: 'flex-end'
};

type Props = any;
type State = any;

export default class InputNumber extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
  }
  onChange(e: any) {
    const { value } = e.target;
    if (value.trim() === '') {
      this.props.onChange(value);
    } else {
      const reg = /^-?(0|[0-9][0-9]*)(\.[0-9]*)?$/;
      if ((!isNaN(value) && reg.test(value)) || value === '') {
        this.props.onChange(value);
      }
    }
  }
  onBlur() {
    const { value = '', onBlur, onChange } = this.props;
    if (value.charAt(value.length - 1) === '.' || value === '-') {
      onChange(value.slice(0, -1));
    }
    if (onBlur) {
      onBlur();
    }
  }
  render() {
    let {
      prefixValue = '$',
      onChange = (f: any) => f,
      value = '',
      style = {},
      prefixStyle: newPrefixStyle = {},
      inputStyle: newInputStyle = {},
      ...newProps
    } = this.props;
    style = { ...style, ...groupStyle };
    return (
      <Input.Group size="small" compact {...newProps} style={style}>
        <Input
          disabled={true}
          style={{ ...prefixStyle, ...newPrefixStyle }}
          className={styles.prefix}
          defaultValue="$"
        />
        <Input
          style={{ ...inputStyle, ...newInputStyle }}
          value={value}
          onChange={e => this.onChange(e)}
          onBlur={e => this.onBlur()}
        />
      </Input.Group>
    );
  }
}
