import { ActionType } from '../actions/createCar';

const SET_FIELDS = 'SET_FIELDS';

interface CreateCarStore {
  car_type: 1 | 2;
  car_status: '1' | '2';
  specification_location: string;
  cars: Car[];
  make_name: string;
  model_year: string;
  model_name: string;
  trim_name: string;
  engine_type: string;
  displacement: string;
  fuel_type: string;
  body: string;
  door_number: string;
  l_or_r: string;
}

export const initialState: CreateCarStore = {
  car_type: 2,
  car_status: '1',
  specification_location: 'Canada Spec',
  cars: [
    {
      vin: 'SALGV5REXKA519848',
      distance_to_warehouse: '0',
      distance_unit: 'KM'
    }
  ],
  make_name: '',
  model_year: '',
  model_name: '',
  trim_name: '',
  engine_type: '',
  displacement: '',
  fuel_type: '',
  body: '',
  door_number: '',
  l_or_r: ''
};

type Car = {
  vin: string;
  distance_to_warehouse: string;
  distance_unit: string;
};

export const setCreateCarFields = (fields: Partial<CreateCarStore>) => (
  dispatch: any,
  getState: any
) => {
  dispatch({
    type: SET_FIELDS,
    payload: {
      car_status: '2'
    }
  });
};

export default (state = initialState, action: any): CreateCarStore => {
  switch (action.type) {
    case SET_FIELDS: {
      return { ...state, ...action.payload };
    }
    case ActionType.SET_CAR_INFO: {
      return { ...state, ...action.payload };
    }
    default:
      return state;
  }
};
