// clean obj empty properties

type TClean = InstanceType<typeof Object>;
const clean = (obj: TClean): object =>
  Object.entries(obj).reduce((prev: any, [k, v]) => {
    if (v === null || v === undefined || v === '' || v === []) return prev;
    prev[k] = v;
    return prev;
  }, {});

export default clean;
