import React, { useState, useEffect } from 'react';
import styles from './index.module.scss';
import { post } from '../../../../../../api/http';
import { Button, Progress, Spin } from 'antd';
import IconFont from '../../../../../../components/ui/TradexIcon';
import { FormattedMessage } from 'react-intl';
import { FormattedPrice } from '../../../../../../components/FormattedPrice';
import _get from '../../../../../../common/get';
import { countdownGenerator, rorate } from '../utils';

type LockedRateProps = {
  onClose?: any;
  fromCurrency: string;
  toCurrency: string;
  amount: string;
  submit?: any;
};

const LockedRate = (props: LockedRateProps) => {
  const { onClose, fromCurrency, toCurrency, amount, submit } = props;

  const [rateData, setRateData] = useState();
  const [restTime, setRestTime] = useState(8);
  const [loading, setLoading] = useState(true);
  const [percent, setPercent] = useState(0);
  const [buyAmount, setBuyAmount] = useState(0);

  useEffect(() => {
    getRate();
  }, []);

  const countdown = countdownGenerator(setRestTime, setRestTime);

  const getRate = () => {
    const param = {
      from_currency: fromCurrency,
      to_currency: toCurrency,
      amount
    };
    setLoading(true);
    return post('fx/rate/', param).then(({ data }) => {
      const rate = _get(data, ['rate']) || _get(data, ['data', 'quoted_rate']);
      if (rate) {
        setRateData(data);
        setBuyAmount(Number.parseFloat(amount) / rate);
        setLoading(false);
        countdown(8, 1000);
        rorate(setPercent, onClose, 8000);
      }
    });
  };
  const lockedRate = () => {
    const quoteId = _get(rateData, ['quote_id']);
    submit(quoteId);
    onClose();
  };

  const rateInfo = (
    <FormattedMessage
      id="drawer_lockedRate_EFTNote"
      values={{
        buyerCurrency: fromCurrency,
        sellerCurrency: toCurrency,
        exchangeRate: _get(rateData, ['rate']) || _get(rateData, ['data', 'quoted_rate']),
        span: (msg: string) => <span className={styles.exchange}>{msg}</span>
      }}
    >
      {(text, text1, text2) => (
        <span>
          {text}
          {text1}
          {text2}
        </span>
      )}
    </FormattedMessage>
  );

  return (
    <div className={styles.mask}>
      <div className={styles.container}>
        <Button className={styles.cancelButton} onClick={onClose}>
          <IconFont type="iconicon_cancel" />
        </Button>
        {loading ? (
          <Spin />
        ) : (
          <React.Fragment>
            <div className={styles.item1}>
              <FormattedMessage id="drawer_lockedRate_title" />
            </div>
            <div className={styles.sellerPrice}>
              <FormattedPrice value={Number.parseFloat(amount)} currency={toCurrency} />
            </div>
            <div className={styles.buyerPrice}>
              {'('}
              <FormattedPrice value={buyAmount} currency={fromCurrency} />
              {')'}
            </div>
            <div className={styles.divider}></div>
            <div className={styles.rate}>{rateInfo}</div>
            <Button size="large" className={styles.lockedButton} onClick={lockedRate}>
              <Progress
                className={styles.process}
                width={28}
                type="circle"
                percent={percent}
                format={percent => <span className={styles.percent}>{`${restTime}s`}</span>}
              />
              <FormattedMessage id="drawer_lockedRate_button" />
            </Button>
          </React.Fragment>
        )}
      </div>
    </div>
  );
};

export default LockedRate;
