import React, { useState } from 'react';
import styles from './index.module.scss';
import { Spin } from 'antd';
import _get from '../../../../common/get';
import { AddCard, CardWrapper } from '../../components/Card';
import Drawer from '../../components/Drawer';
import Register from './Register';

const BankAccount = (props: any) => {
  const [bankCardInfo, setBankCardInfo] = useState();
  const [isVisible, setVisable] = useState(false);

  const {
    style,
    className = '',
    setting,
    getBankCards,
    bankCards,
    setAsPad,
    setPrimaryBankCard,
    padInfo
  } = props;
  const { cards, isLoading } = bankCards;

  const showDrawer = () => {
    setBankCardInfo('');
    setVisable(true);
  };
  const viewCardInfo = (cardInfo: any) => {
    setBankCardInfo(cardInfo);
    setVisable(true);
  };
  const onClose = () => setVisable(false);
  const update = () => getBankCards();

  return (
    <Spin spinning={isLoading}>
      <div style={style} className={`${className} ${styles.container}`}>
        {cards.map((card: any, index: number) => (
          <CardWrapper
            {...card}
            isPrimary={index === 0}
            setAsPad={() => setAsPad(card.id)}
            setAsPrimary={() => setPrimaryBankCard(card.id)}
            onClick={() => viewCardInfo(card)}
            padInfo={padInfo}
          />
        ))}
        {cards.length < 6 ? <AddCard onClick={showDrawer} /> : ''}
        {isVisible ? (
          <Drawer visible={isVisible} width={840} onClose={onClose}>
            <Register
              onClose={onClose}
              update={update}
              options={_get(setting, ['ALL_CURRENCIES'])}
              bankCardInfo={bankCardInfo}
            />
          </Drawer>
        ) : (
          ''
        )}
      </div>
    </Spin>
  );
};

export default BankAccount;
