import React, { useState } from 'react';
import styles from './index.module.scss';
import { Upload, Spin, message } from 'antd';
import IconFont from '../../../../components/ui/TradexIcon';
import Circle from '../Circle';
import { getCookie } from '../../../../common/cookie';
import { post } from '../../../../api/http/index';
import _get from '../../../../common/get';

type AvatarProps = {
  style?: any;
  className?: any;
  src?: string;
  size?: number;
  updateUserInfo?: any;
};

enum UploadStatus {
  LOADING = 'uploading',
  DONE = 'done',
  ERROR = 'error'
}
const apiRoot = process.env.API_ROOT ? process.env.API_ROOT + '/api' : '/api';
const action = apiRoot + '/v2/users/changeheadshot';
const defaultUploadProps = {
  showUploadList: false,
  action,
  method: 'POST' as 'POST' | 'PUT' | 'post' | 'put' | undefined,
  accept: '.jpg, .png',
  data: (file: any) => ({ headshot: file }),
  headers: {
    Authorization: getCookie('Authorization'),
    contentType: 'multipart/form-data'
  } as any
};

const Avatar = (props: AvatarProps) => {
  const [spining, setSpining] = useState(false);

  const { style, src = '', size = 80, updateUserInfo } = props;

  const renderImage = () => {
    const style = {
      width: `100%`,
      height: `100%`,
      borderRadius: '50%',
      objectFit: 'cover'
    } as React.CSSProperties;
    return (
      <div style={style}>
        <img src={src} style={style} />
        <Circle size={20} className={styles.icon}>
          <IconFont type="iconicon_camera_area" />
        </Circle>
      </div>
    );
  };

  const customRequest = ({ file, data }: any) => {
    let formData = new FormData();
    if (data) {
      Object.keys(data).forEach(key => {
        formData.append(key, data[key]);
      });
    }
    setSpining(true);
    post('users/changeheadshot/', formData)
      .then(() => {
        setSpining(false);
        updateUserInfo && updateUserInfo();
      })
      .catch(e => {
        if (e.response) {
          const error = _get(e, ['response', 'data', 'message']);
          message.error(error || 'Error');
        } else {
          message.error(e.message);
        }
        setSpining(false);
      });
  };
  const uploadProps = { ...defaultUploadProps, customRequest };

  return (
    <Circle {...{ style, size }} className={`${styles.container} ${props.className}`}>
      <Spin spinning={spining}>
        <Upload className={styles.upload} {...uploadProps}>
          {src ? renderImage() : ''}
        </Upload>
      </Spin>
    </Circle>
  );
};

export default Avatar;
