import React from 'react';
import styles from './index.module.scss';

export function Tag(props: {
  text: string;
  type: 'incoming' | 'rfq' | 'instock' | 'newcar' | 'usedcar' | 'units';
  size: 'small' | 'big';
  style?: React.CSSProperties;
  onClick?: any;
}) {
  const { text, size, type, style, onClick } = props;
  return (
    <span
      className={`${styles.tag} ${styles[size]} ${styles[type]}`}
      style={style}
      onClick={() => {
        onClick && onClick();
      }}
    >
      {text}
    </span>
  );
}

export function FillTag(props: {
  text: string;
  type: 'buy' | 'sell' | 'msrp' | 'units' | 'xn' | 'red' | 'blue';
  style?: React.CSSProperties;
  onClick?: any;
}) {
  const { text, type, style, onClick } = props;
  return (
    <span
      className={`${styles.fillTag} ${styles[type]}`}
      style={style}
      onClick={() => {
        onClick && onClick();
      }}
    >
      {text}
    </span>
  );
}
