import React from 'react';
import styles from './index.module.scss';
import { Skeleton } from 'antd';

export default function Skel() {
  return (
    <div className={styles.skeleton}>
      <div className={styles.up}>
        <Skeleton className={styles.left} avatar={{ shape: 'square', size: 520 }} />
        <Skeleton className={styles['ske-up']} active paragraph={{ rows: 14 }}></Skeleton>
      </div>
      {Array(10)
        .fill(1)
        .map((d, idx) => (
          <Skeleton
            className={styles['ske-down']}
            key={idx}
            active
            paragraph={{ rows: 2 }}
          ></Skeleton>
        ))}
    </div>
  );
}
