import React, { useState, ReactComponentElement, ReactElement } from 'react';
import { Drawer as AntDrawer, Button, Modal, Tooltip } from 'antd';
import styles from './index.module.scss';
import ConditionReport from './Components/ConditionReport';
import { DrawerConfig, OperType } from '../index';
import Cacelled from './Components/Cacelled';
import { PayDeposit, PayRemaining, PayServiceFee } from './Components/PayDeposit';
import { GetOrderDetail } from '../../../api/order';
import { OrderItem } from '../index';
import { UserRole, OrderDetail, OrderType } from '../../../types';
import Icon from '../../../components/Icon';
import TradexIcon from '../../../components/ui/TradexIcon';
import Schedule from './Components/Schedule';
import Sign from './Components/Sign';
import Track from './Components/Track';
import AddVehicle from '../Drawer/Components/AddVehicle';
import OrderDisbursements from './Components/OrderDisbursements';
import { orderActions, orderViewLinks } from '../../../config/status';
import LottieIcon from '../../../components/Lottie/LottieIcon';

export interface Props {
  id: string;
  data: OrderDetail;
  show: boolean;
  type: number | string;
  toggleDrawer: () => void;
  onClose: () => void;
  drawerConfig: DrawerConfig;
  [indx: string]: any;
}

interface State {
  detail: any;
  status: ActionStatus;
}

export enum ActionStatus {
  Init = 'init',
  Loading = 'loading',
  Success = 'success',
  Error = 'error',
  Timeout = 'timeout'
}

const drawerBodyStyle = {
  padding: 0,
  lineHeight: 'initial',
  fontSize: 'initial',
  backgroundColor: '#f6f8f9',
  minHeight: '100%'
};

function Result(props: { status: ActionStatus; onClose: () => void }) {
  let iconKind = '',
    btnText = '',
    desText = '';
  switch (props.status) {
    case ActionStatus.Success:
      iconKind = 'picture_succeed';
      btnText = 'OK';
      break;
    case ActionStatus.Error:
      iconKind = 'picture_failed';
      btnText = 'Close';
      break;
    case ActionStatus.Timeout:
      iconKind = 'picture_timeout';
      btnText = 'Close';
      desText = 'Unable to load the page, please try again.';
      break;
    default:
      iconKind = 'picture_succeed';
      btnText = 'OK';
  }

  return (
    <div className={styles.result}>
      <Icon kind={iconKind} width={440} height={330} style={{ marginTop: -23 }} />
      {desText && <div className={styles.tip}>{desText}</div>}
      <Button className={styles.drawerBtn} onClick={props.onClose}>
        {btnText}
      </Button>
    </div>
  );
}

const comList: { key: (number | string)[]; val: any }[] = [
  {
    key: ['CR', 11],
    val: ConditionReport
  },
  {
    key: ['CL'],
    val: Cacelled
  },
  {
    key: [1],
    val: Sign
  },
  {
    key: [2, 5, 13, 14, 'DF', 'PP'],
    val: Schedule
  },
  {
    key: ['SP'],
    val: Track
  },
  {
    key: [6],
    val: PayDeposit
  },
  {
    key: [15],
    val: PayRemaining
  },
  {
    key: [16],
    val: PayServiceFee
  },
  {
    key: [10],
    val: AddVehicle
  },
  {
    key: ['DI'],
    val: OrderDisbursements
  }
];

export default class Drawer extends React.Component<Props, State> {
  state = {
    status: ActionStatus.Init,
    detail: null
  };

  componentDidMount() {
    this.getOrderDetail();
  }

  componentDidUpdate(preProps: Props) {
    if (preProps.id !== this.props.id || (preProps.show !== this.props.show && this.props.show)) {
      this.getOrderDetail();
    }
  }

  getOrderDetail = () => {
    this.setState({
      status: ActionStatus.Loading
    });
    GetOrderDetail(this.props.id).then(data => {
      this.setState({
        detail: data.data,
        status: ActionStatus.Init
      });
    });
  };

  tryCloseDrawer = () => {
    const { onClose, drawerConfig } = this.props;
    if (drawerConfig.operType === OperType.Edit) {
      Modal.confirm({
        content: 'All unsaved changed will be lost. Are you sure to close the window?',
        cancelText: 'Not Now',
        okText: 'Close',
        onOk: onClose
      });
    } else {
      onClose();
    }
  };

  onDrawerAction = (result?: ActionStatus) => {
    if (result) {
      this.setState({ status: result });
      return;
    }
    this.props.onClose();
  };

  render() {
    const { data, show, drawerConfig, type, toggleDrawer, onClose } = this.props;
    const { status, detail } = this.state;
    const role = drawerConfig.roleType;
    const btnTipText = () => {
      const actionItem = orderActions.find(d => d.type === type);
      if (actionItem) return actionItem.name;
      const linkItem = orderViewLinks.find(d => d.type === type);
      if (linkItem) return linkItem.key;
      return '';
    };

    return (
      <AntDrawer
        placement="right"
        width={840}
        onClose={this.tryCloseDrawer}
        visible={show}
        maskClosable={false}
        bodyStyle={drawerBodyStyle}
        className={styles.drawer}
      >
        <>
          <div className={styles.sideBtn} onClick={toggleDrawer}>
            <Tooltip title={show ? '' : btnTipText} placement="left">
              <TradexIcon
                type={show ? 'iconicon_rightarrow' : 'iconicon_leftarrow'}
                size={48}
                color="#ffffff"
              />
            </Tooltip>
          </div>
          {status === ActionStatus.Loading ? (
            <div className={styles.loading}>
              <LottieIcon
                className={styles.lottie}
                animationData={require('../../../assets/lottie/smileface.json')}
              />
              {/* <img src={require('../../../assets/img/image-logo-blue@3x.png')} alt="" /> */}
            </div>
          ) : status !== ActionStatus.Init ? (
            <Result status={status} onClose={onClose} />
          ) : null}
          {detail && (
            <div className="prevent-scroll">
              <div className={styles.drawerHead}>
                <OrderItem detail={data} role={role} isInDrawer={true} onBtnClick={() => {}} />
              </div>
              <div className={styles.drawerBody}>
                {comList.map((d, idx) => {
                  console.log(typeof d);
                  return d.key.some(k => k === type) ? (
                    <d.val
                      key={idx}
                      drawerConfig={drawerConfig}
                      detail={detail}
                      data={data}
                      setActionStatus={this.onDrawerAction}
                    />
                  ) : null;
                })}
              </div>
            </div>
          )}
        </>
      </AntDrawer>
    );
  }
}
