export const setUnreadNum = () => (dispatch: any, getState: any) => {
  GetUnreadMsgNum()
    .then(({ data }) => {
      dispatch({
        type: ChatActionTypes.SET_UNREAD_NUM,
        num: {
          sell: data.count_as_seller,
          buy: data.count_as_buyer
        }
      });
    })
    .catch(() => {});
};

export const addActiveRoomId = (id: string) => ({
  type: ChatActionTypes.ADD_ACTIVE_ROOM_ID,
  item: { id }
});

export const updateActiveRoomId = (id: string, status: 'common' | 'min'): ChatAction => ({
  type: ChatActionTypes.UPDATE_ACTIVE_ROOM_ID,
  item: {
    id,
    status
  }
});

