export const getChatBuyListList = (page = 1, dispatch: any) => {
  let buyParm = { page: page };
  let buyList: chatBuyObject[];
  let hasnextBuylistPage = false;
  getChatBuyList(buyParm).then(response => {
    console.log(' getChatBuyListList', response);
    buyList = response.data.results.filter((i: any) => i.type === 1);
    if (response.data.next) {
      // buyParm.page = +1;
      // getChatBuyListList(page + 1, buyList, dispatch);
      hasnextBuylistPage = true;
    }
    dispatch({
      type: ChatActionTypes.SET_CHAT_BUY_LIST,
      list: buyList,
      buyListCurrentPage: page,
      hasnextBuylistPage: hasnextBuylistPage
    });
  });
};

