export const getChatSellListList = (page = 1, sellList: chatSellObject[] = [], dispatch: any) => {
  let sellParm = { page: page };
  // let buyList: chatBuyObject[] = buyList;
  getChatSellList(sellParm).then(response => {
    sellList = sellList.concat(response.data.results).filter(i => i.type === 1);
    if (response.data.next) {
      // buyParm.page = +1;
      getChatSellListList(page + 1, sellList, dispatch);
    }
    dispatch({
      type: ChatActionTypes.SET_CHAT_SELL_LIST,
      list: sellList
    });
  });
};

export const setChatBuyList = (
  buyList: chatBuyObject[],
  page: number,
  hasnextBuylistPage: boolean
) => ({
  type: ChatActionTypes.SET_CHAT_BUY_LIST,
  list: buyList,
  buyListCurrentPage: page,
  hasnextBuylistPage: hasnextBuylistPage
});

export const setChatSellList = (
  buyList: chatSellObject[],
  page: number,
  hasnextSelllistPage: boolean
) => ({
  type: ChatActionTypes.SET_CHAT_SELL_LIST,
  list: buyList,
  sellListCurrentPage: page,
  hasnextSelllistPage: hasnextSelllistPage
});

