export const upDateRoomUnread = (activeRole: any, chatRoomId: string) => (
  dispatch: any,
  getState: any
) => {
  resetChatRoomUnreadCount(chatRoomId)
    .then(response => {
      console.log(response);
      upDateChatRoomUnread(chatRoomId).then(({ data }) => {
        console.log(data);
        dispatch({
          type: ChatActionTypes.UPDATE_CHATROOM_UNRADE,
          chatrooms: data.chatrooms,
          chatRoomId: chatRoomId,
          activeRole
        });
        dispatch(setUnreadNum());
      });
    })
    .catch(error => {
      console.log('resetChatRoomUnreadCount error', error);
    });
};
