import React, { Component } from 'react';
import IconFont from '../../components/ui/TradexIcon';
import styles from './UploadImg.module.scss';
import { Button, Icon, Upload, notification, Spin, Modal } from 'antd';
import { uploadPaper } from '../../api/user';
import { Redirect } from 'react-router-dom';
import { path } from '../../routes/routers';

type Props = any;

// type State = any;

interface State {
  imgurl0: string;
  imgurl1: string;
  imgurl2: string;
  File0: any;
  File1: any;
  File2: any;
  showRequired: boolean;
  isLoading: boolean;
  showModal: boolean;
}

class UploadImg extends Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      imgurl0: '',
      imgurl1: '',
      imgurl2: '',
      File0: '',
      File1: '',
      File2: '',
      showRequired: false,
      isLoading: true,
      showModal: false
    };
  }

  componentDidMount(): void {
    this.setState({
      isLoading: false
    });
  }

  //upload   false 
  beforeUpload = (
    file: File | Blob,
    fileList: any,
    index: 0 | 1 | 2
  ): boolean | PromiseLike<void> => {
    // console.log('file', file);
    // console.log('URL.createObjectURL(file)', URL.createObjectURL(file));
    // let key:any = 'File' + index
    this.setState({
      ['imgurl' + index]: URL.createObjectURL(file),
      ['File' + index]: file
    } as Pick<State, any>);
    // console.log('fileList', fileList);
    return false;
  };

  formSubmit = () => {
    const { imgurl0, imgurl1, imgurl2, File0, File1, File2 } = this.state;
    if (imgurl0 && imgurl1 && imgurl2) {
      this.setState({
        isLoading: true
      });
      let formData = new FormData();
      formData.append('article', File0);
      formData.append('government_id', File1);
      formData.append('selfie', File2);
      uploadPaper(formData)
        .then(response => {
          this.setState({
            showModal: true,
            isLoading: false
          });
        })
        .catch(err => {
          console.log('err', err);
          this.setState({
            isLoading: false
          });
        });
    } else {
      // console.log('');
      this.setState({
        showRequired: true
      });
      notification.error({
        message: 'Error',
        description: 'Please provide Image.'
      });
      return;
    }
  };

  handleOk = () => {
    const {
      history: { push, replace }
    } = this.props;
    replace(`${path.Register}/RegisterReviewed/`);
  };

  render() {
    const {
      imgurl0,
      imgurl1,
      imgurl2,
      File0,
      File1,
      File2,
      showRequired,
      isLoading,
      showModal
    } = this.state;
    function upOrImg(imageUrl: string) {
      // console.log('imageUrl', imageUrl);
      return imageUrl ? (
        <img src={imageUrl} alt="avatar" className={styles.viewImg} />
      ) : (
        <IconFont
          type="iconicon_add_line_thin"
          color={showRequired ? '#f2565f' : '#acb6bf'}
          size={32}
        />
      );
    }

    return (
      <>
        {isLoading ? (
          <div className={styles.upLoadFormLoading}>
            <Spin size="large" />
          </div>
        ) : null}
        <Modal
          title="Thank You!"
          visible={showModal}
          onOk={this.handleOk}
          closable={false}
          centered={true}
          wrapClassName={styles.Modal}
          width={480}
          footer={
            <div className={styles.Modal__footer}>
              <Button className={styles.Modal__footer__btn} onClick={this.handleOk}>
                OK
              </Button>
            </div>
          }
        >
          <p className={styles.Modal__cont}>
            Your application is being verified, we will respond within 24 hours of our decision.
          </p>
        </Modal>
        <div className={styles.upLoadFormBox}>
          <div className={styles.formConatiner}>
            <p className={styles.formLabel}>Get Started</p>
            <p className={styles.formTitle}>Please Upload Following Images</p>
            <div className={styles.upLoadFormContainer}>
              <div className={styles.upFormController}>
                <IconFont
                  type="iconicon_buzinfo_line"
                  color="#00274d"
                  size={36}
                  className={styles.upItemIcon}
                />
                <p className={styles.upItemLabel}>
                  Articles of Incorporation or Business Formation
                </p>
                <Upload
                  name="avatar"
                  listType="picture-card"
                  className={`${styles.uploader} ${
                    imgurl0 ? styles.borderNone : showRequired ? styles.borderRed : ''
                  }`}
                  showUploadList={false}
                  action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                  beforeUpload={(file: File | Blob, fileList: any): boolean | PromiseLike<void> => {
                    this.beforeUpload(file, fileList, 0);
                    return false;
                  }}
                  // onChange={this.handleChange}
                >
                  {upOrImg(imgurl0)}
                </Upload>
              </div>

              <div className={styles.upFormController}>
                <IconFont
                  type="iconicon_govissuedid_line"
                  color="#00274d"
                  size={36}
                  className={styles.upItemIcon}
                />
                <p className={styles.upItemLabel}>Government Issued ID</p>
                <Upload
                  name="avatar"
                  listType="picture-card"
                  className={`${styles.uploader} ${
                    imgurl1 ? styles.borderNone : showRequired ? styles.borderRed : ''
                  }`}
                  showUploadList={false}
                  action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                  beforeUpload={(file: File | Blob, fileList: any): boolean | PromiseLike<void> => {
                    this.beforeUpload(file, fileList, 1);
                    return false;
                  }}
                  // onChange={this.handleChange}
                >
                  {upOrImg(imgurl1)}
                </Upload>
              </div>

              <div className={styles.upFormController}>
                <IconFont
                  type="iconicon_selfieid_line"
                  color="#00274d"
                  size={36}
                  className={styles.upItemIcon}
                />
                <p className={styles.upItemLabel}>Selfie with Government ID</p>
                <Upload
                  name="avatar"
                  listType="picture-card"
                  className={`${styles.uploader} ${
                    imgurl2 ? styles.borderNone : showRequired ? styles.borderRed : ''
                  }`}
                  showUploadList={false}
                  action="https://www.mocky.io/v2/5cc8019d300000980a055e76"
                  beforeUpload={(file: File | Blob, fileList: any): boolean | PromiseLike<void> => {
                    this.beforeUpload(file, fileList, 2);
                    return false;
                  }}
                  // onChange={this.handleChange}
                >
                  {upOrImg(imgurl2)}
                </Upload>
              </div>

              <Button className={styles.submitBtn} onClick={this.formSubmit} type="primary">
                Upload
              </Button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default UploadImg;
