//url  ?  eg session=2b0d41cfedcf4302ab4addff8152af32&format=json
function getQueryString(name: string, url: string) {
  var reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i');
  var r = url.match(reg);
  if (r != null) return decodeURI(r[2]);
  return null;
}
