// 
export const SetNoticeListLoading = (noticeListLoading: boolean) => ({
  type: NoticeActionType.SET_NOTICE_LIST_LOADING,
  noticeListLoading: noticeListLoading
});

/**
 * Notice 
 * @param tabType activetab
 * @param needCleanList tab
 */
export const setNoticeData = (tabType: NoticeTabType, needCleanList: boolean) => (
  dispatch: any,
  getState: any
) => {
  let { notice } = getState();
  let {
    noticeTabList: {
      offersListCurrentPage,
      hasNextOffersListPage,
      todoListCurrentPage,
      hasNextTodoListPage,
      updatesListCurrentPage,
      hasNextUpdatesListPage
    }
  } = notice;

  // 
  dispatch(SetNoticeListLoading(true));
  let queryParams = {};
  if (tabType === NoticeTabType.OFFERS) {
    queryParams = {
      subgroup: 'F',
      page: needCleanList ? 1 : offersListCurrentPage + 1
    };
  } else if (tabType === NoticeTabType.TO_DO) {
    queryParams = {
      subgroup: 'T',
      page: needCleanList ? 1 : todoListCurrentPage + 1
    };
  } else if (tabType === NoticeTabType.UPDATES) {
    queryParams = {
      subgroup: 'U',
      page: needCleanList ? 1 : updatesListCurrentPage + 1
    };
  }

  getNoticeList(queryParams)
    .then(response => {
      let { data } = response;
      let extra: NoticeExtra[] = data.extra;
      let items: NoticeItem[] = data.results;

      // extra
      let sum_unread = 0;
      let _noticeTabNum = {
        offers: 0,
        todo: 0,
        updates: 0,
        offers_unread: 0,
        todo_unread: 0,
        updates_unread: 0
      };

      extra.forEach(item => {
        sum_unread += item.unread;
        if (item.subgroup === 'F') {
          _noticeTabNum.offers = item.count;
          _noticeTabNum.offers_unread = item.unread;
        } else if (item.subgroup === 'T') {
          _noticeTabNum.todo = item.count;
          _noticeTabNum.todo_unread = item.unread;
        } else if (item.subgroup === 'U') {
          _noticeTabNum.updates = item.count;
          _noticeTabNum.updates_unread = item.unread;
        }
      });
      if (sum_unread > 0) {
        // Notificaton
        dispatch(setNewNotice(true));
      } else {
        dispatch(setNewNotice(false));
      }

      dispatch(
        setNoticeTabNum({
          //tab
          ..._noticeTabNum
        })
      );

      // List 
      dispatch(SetNoticeListLoading(false));
      if (tabType === NoticeTabType.OFFERS) {
        if (data.next) {
          hasNextOffersListPage = true;
        } else {
          hasNextOffersListPage = false;
        }
        if (needCleanList) {
          // 
          dispatch(setOffersList(items, 1, hasNextOffersListPage, true));
        } else {
          dispatch(setOffersList(items, offersListCurrentPage + 1, hasNextOffersListPage, false));
        }
      } else if (tabType === NoticeTabType.TO_DO) {
        if (data.next) {
          hasNextTodoListPage = true;
        } else {
          hasNextTodoListPage = false;
        }
        if (needCleanList) {
          // 
          dispatch(setTodoList(items, 1, hasNextTodoListPage, true));
        } else {
          dispatch(setTodoList(items, todoListCurrentPage + 1, hasNextTodoListPage, false));
        }
      } else if (tabType === NoticeTabType.UPDATES) {
        if (data.next) {
          hasNextUpdatesListPage = true;
        } else {
          hasNextUpdatesListPage = false;
        }
        if (needCleanList) {
          // 
          dispatch(setUpdatesList(items, 1, hasNextUpdatesListPage, true));
        } else {
          dispatch(
            setUpdatesList(items, updatesListCurrentPage + 1, hasNextUpdatesListPage, false)
          );
        }
      }
    })
    .catch(err => {
      dispatch(SetNoticeListLoading(false));
    });
};

