import { get, post, _delete } from './http';
import { AxiosPromise } from 'axios';

//
// params: {
//   page: number;
//   size: number;
//   car_type: 1 | 2;
// }
export function getMygarage(params: object): AxiosPromise<any> {
  return get('mygarage/', { ...params }, true, true);
}

export function moveToGarage(id: string): AxiosPromise<any> {
  return post(`garages/${id}/goto_garage/`, {});
}
export function publishToMarket(id: string): AxiosPromise<any> {
  return post(`garages/${id}/publish/`, {});
}
export function deletedCar(id: string): AxiosPromise<any> {
  return _delete(`garages/${id}/`, {});
}
