import React from 'react';
import styles from './index.module.scss';

type RowPros = {
  style?: any;
  className?: any;
  children?: any;
  type?: string;
};

const layout: { [index: string]: any } = {
  default: styles.default
};

export const Row = (props: RowPros) => {
  const { type = 'default', className = '', style } = props;

  return (
    <div style={style} className={`${layout[type]} ${className}`}>
      {props.children}
    </div>
  );
};
