import { get, post } from './http';
import { AxiosPromise } from 'axios';

export function decode(param: { vin: string; country: string }): AxiosPromise<any> {
  return post('vin/decode/', param);
}

export function getOptions(trim: string, isUsed: number = 0): AxiosPromise<any> {
  return get('cardata/options/', { trim: trim, is_used: isUsed });
}
