import React from 'react';
import styles from './index.module.scss';

type ItemProps = {
  style?: React.CSSProperties;
  className?: any;
  children?: any;
};

const Item = ({ style, className = '', children }: ItemProps) => (
  <div style={style} className={`${className} ${styles.item}`}>
    {children}
  </div>
);

export default Item;
