import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import _get from '../../common/get';
import { bindActionCreators, compose, Dispatch } from 'redux';
import { connect } from 'react-redux';
import styles from './index.module.scss';
import IconFont from '../../components/ui/TradexIcon';
import VINForm from './components/VINForm';
import NotesForm from './components/NotesForm';
import { AppStore } from '../../store/reducers';
import { setCreateCarFields } from '../../store/reducers/createCar';
import VehicleStatus from './components/VehicleStatus';
import UploadPhotos from './components/UploadPhotos';
import BasicInfo from './components/BasicInfo';

interface IProps {
  createCarFlow: number;
  createCar: any;
  setCreateCarFields: any;
}

interface IState {
  tabIndex: number;
  flowIndex: number;
}

class CreateCarContainer extends React.Component<IProps, IState> {
  state = {
    tabIndex: 3,
    flowIndex: 1
  };

  handleTab = (e: any) => {
    const index = _get(e, 'target.dataset.index');
    console.log(index);
    if (!index || index == this.state.tabIndex) return;
    this.setState({ tabIndex: parseFloat(index) });
  };

  render() {
    const { tabIndex, flowIndex } = this.state;
    console.log(this.props.createCar);
    const generateStatusIcon = (index: number) => {
      return flowIndex > index ? (
        <IconFont type="iconicon_choose_area" className={styles.finishedIcon} />
      ) : (
        <IconFont type="iconradio_unchoose" className={styles.initialIcon} />
      );
    };

    return (
      <div className={styles.container}>
        {/* <button
          onClick={() => {
            this.props.setCreateCarFields({ car_status: "2" });
            console.log(this.props);
          }}
        >
          123
        </button> */}
        <div className={styles.tab}>
          <div className={styles.tabMenu}>
            <div className={styles.tabMenuTitle}>OVERVIEW</div>
            <div className={styles.tabMenuList} onClick={this.handleTab}>
              <div
                data-index="0"
                className={`${styles.tabMenuItem} ${
                  tabIndex === 0 ? styles.tabMenuItemActive : ''
                }`}
              >
                {generateStatusIcon(0)}
                <div data-index="0">Vehicle Status</div>
              </div>
              <div
                data-index="1.1"
                className={`${styles.tabMenuItem} ${styles.tabMenuNestedItem} ${
                  tabIndex >= 1 && tabIndex < 2 ? styles.tabMenuItemActive : ''
                }`}
              >
                {generateStatusIcon(1)}
                <div data-index="1">Vehicle Details</div>
                {tabIndex >= 1 && tabIndex < 2 && (
                  <div className={styles.nestedMenu} data-index="1.1">
                    <div className={styles.nestedMenuItem} data-index="1.1">
                      VIN
                    </div>
                    <div className={styles.nestedMenuItem} data-index="1.2">
                      Basic Info
                    </div>
                    <div className={styles.nestedMenuItem} data-index="1.3">
                      Options
                    </div>
                    <div className={styles.nestedMenuItem} data-index="1.4">
                      Upload Photots
                    </div>
                  </div>
                )}
              </div>
              <div
                data-index="2"
                className={`${styles.tabMenuItem} ${
                  tabIndex === 2 ? styles.tabMenuItemActive : ''
                }`}
              >
                {generateStatusIcon(2)}
                <div data-index="2">Set The Price</div>
              </div>
              <div
                data-index="3"
                className={`${styles.tabMenuItem} ${
                  tabIndex === 3 ? styles.tabMenuItemActive : ''
                }`}
              >
                {generateStatusIcon(3)}
                <div data-index="3">Notes</div>
              </div>
            </div>
          </div>
          <div className={styles.tabContent}>
            {tabIndex === 0 && (
              <div>
                <VehicleStatus />
              </div>
            )}
            {tabIndex === 1.1 && (
              <div>
                {/* <div>tab1.1</div> */}
                <VINForm />
              </div>
            )}
            {tabIndex === 1.2 && (
              <div>
                <BasicInfo />
              </div>
            )}
            {tabIndex === 1.3 && (
              <div>
                <div>tab1.3</div>
              </div>
            )}
            {tabIndex === 1.4 && <UploadPhotos />}
            {tabIndex === 2 && (
              <div>
                <h1>tab2</h1>
              </div>
            )}
            {tabIndex === 3 && <NotesForm />}
          </div>
        </div>
      </div>
    );
  }
}

const mapState = (state: AppStore) => ({
  createCar: state.createCar
});

// const mapDispatch = (dispatch: Dispatch) => ({
//   setCreateCarFields: (fields: any) => dispatch(setCreateCarFields(fields) as any)
// })
const mapDispatch = (dispatch: Dispatch) =>
  bindActionCreators(
    {
      setCreateCarFields
    },
    dispatch
  );

export default compose(
  withRouter,
  connect(
    mapState,
    mapDispatch
  )
)(CreateCarContainer);
