import { getBankCards } from '../../api/bankCards';
import { BankCardsStore, BankCardsAction, BankCardsActionTypes } from '../types';
import _get from '../../common/get';
import { put } from '../../api/http';

export const SetBankCards = (bankCards: BankCardsStore): BankCardsAction => ({
  type: BankCardsActionTypes.SET_BANK_CARDS,
  bankCards
});

export const asyncSetBankCards = () => (dispatch: any, getState: any) => {
  getBankCards()
    .then((res: any) => {
      const cards = _get(res, ['data', 'results']) || [];
      const newAction = SetBankCards({ cards, isUseful: true, isLoading: false });
      dispatch(newAction);
    })
    .catch(() => {
      const newAction = SetBankCards({ cards: [], isUseful: false, isLoading: false });
      dispatch(newAction);
    });
};

export const SetBankLoading = (): BankCardsAction => ({
  type: BankCardsActionTypes.LOADING
});

export const setPrimaryBankCard = (id: string) => (dispatch: any, getState: any) => {
  dispatch(SetBankLoading());
  put(`users/bank_accounts/${id}/primary/`)
    .then(() => getBankCards())
    .then((res: any) => {
      const cards = _get(res, ['data', 'results']) || [];
      const newAction = SetBankCards({ cards, isUseful: true, isLoading: false });
      dispatch(newAction);
    })
    .catch(() => {
      const newAction = SetBankCards({ cards: [], isUseful: false, isLoading: false });
      dispatch(newAction);
    });
};

