import React from 'react';
import { FormattedMessage } from 'react-intl';
import styles from './index.module.scss';

type TitleProps = {
  style?: React.CSSProperties;
  className?: any;
  name?: any;
  title?: any;
};

const Titel = ({ style, className = '', name, title }: TitleProps) => (
  <div style={style} className={`${className} ${styles.title}`}>
    {title ? title : name ? <FormattedMessage id={name} /> : ''}
  </div>
);

export default Titel;
