/**
 * Notice 
 */

import { CarInfo, Suborders } from './index';

export enum NoticeActionType {
  SET_NOTICE_SHOW = 'SET_NOTICE_SHOW',
  SET_ACTIVE_NOTICE_TAB = 'SET_ACTIVE_NOTICE_TAB',
  SET_NEW_NOTICE = 'SET_NEW_NOTICE',
  SET_NOTICE_TAB_NUM = 'SET_NOTICE_TAB_NUM',
  SET_OFFERS_LIST = 'SET_OFFERS_LIST',
  SET_TODO_LIST = 'SET_TODO_LIST',
  SET_UPDATES_LIST = 'SET_UPDATES_LIST',
  SET_NOTICE_LIST_LOADING = 'SET_NOTICE_LIST_LOADING',
  SET_NOTICE_ITEM_READ_STATUS = 'SET_NOTICE_ITEM_READ_STATUS',
  REMOVE_NOTICE_ITEM = 'REMOVE_NOTICE_ITEM'
}
export interface NoticeStore {
  noticeShow: boolean;
  activeNoticeTab: NoticeTabType;
  hasNewNotice: boolean;

  noticeTabNum: {
    offers: number;
    todo: number;
    updates: number;
    offers_unread: number;
    todo_unread: number;
    updates_unread: number;
  };

  noticeTabList: {
    noticeListLoading: boolean;

    offersList: NoticeItem[];
    offersListCurrentPage: number;
    hasNextOffersListPage: boolean;

    todoList: NoticeItem[];
    todoListCurrentPage: number;
    hasNextTodoListPage: boolean;
    updatesListCurrentPage: number;

    updatesList: NoticeItem[];
    hasNextUpdatesListPage: boolean;
  };
}
export type NoticeAction =
  | SetNoticeShow
  | SetActiveNoticeTab
  | SetNewNotice
  | SetNoticeTabNum
  | SetOffersList
  | SetTodoList
  | SetUpdatesList
  | SetNoticeListLoading
  | SetNoticeItemReadStatus
  | RemoveNoticeItem;

export interface SetNoticeShow {
  type: NoticeActionType.SET_NOTICE_SHOW;
  noticeShow: NoticeStore['noticeShow'];
}
export interface SetActiveNoticeTab {
  type: NoticeActionType.SET_ACTIVE_NOTICE_TAB;
  activeNoticeTab: NoticeStore['activeNoticeTab'];
}

export interface SetNewNotice {
  type: NoticeActionType.SET_NEW_NOTICE;
  hasNewNotice: NoticeStore['hasNewNotice'];
}
export interface SetNoticeTabNum {
  type: NoticeActionType.SET_NOTICE_TAB_NUM;
  noticeTabNum: NoticeStore['noticeTabNum'];
}

export interface SetOffersList {
  type: NoticeActionType.SET_OFFERS_LIST;
  list: NoticeStore['noticeTabList']['offersList'];
  currentPage: number;
  hasNextPage: boolean;
  needCleanList: boolean;
}
export interface SetTodoList {
  type: NoticeActionType.SET_TODO_LIST;
  list: NoticeStore['noticeTabList']['todoList'];
  currentPage: number;
  hasNextPage: boolean;
  needCleanList: boolean;
}
export interface SetUpdatesList {
  type: NoticeActionType.SET_UPDATES_LIST;
  list: NoticeStore['noticeTabList']['updatesList'];
  currentPage: number;
  hasNextPage: boolean;
  needCleanList: boolean;
}

export interface SetNoticeListLoading {
  type: NoticeActionType.SET_NOTICE_LIST_LOADING;
  noticeListLoading: NoticeStore['noticeTabList']['noticeListLoading'];
}

export interface SetNoticeItemReadStatus {
  type: NoticeActionType.SET_NOTICE_ITEM_READ_STATUS;
  tabType: NoticeTabType;
  itemId: string;
}

export interface RemoveNoticeItem {
  type: NoticeActionType.REMOVE_NOTICE_ITEM;
  tabType: NoticeTabType;
  itemId: string;
}

export enum NoticeTabType {
  OFFERS = 'OFFERS',
  TO_DO = 'TO_DO',
  UPDATES = 'UPDATES'
}

export enum NoticeItemType {
  OFFERS = 'OFFERS',
  TO_DO = 'TO_DO',
  UPDATES = 'UPDATES',
  BUBBLE = 'BUBBLE'
}

export interface NoticeItem {
  id: string;
  is_read: boolean;
  is_ignored: boolean;
  subgroup: string;
  updated_at: number;
  sender: NoticeItemSender;
  body: NoticeItemBody;
}

export interface NoticeItemSender {
  id: string;
  headshot: string;
  sender_type: string;
}
export interface NoticeItemBody {
  msg_type: string;
  content: string;
  offer?: NoticeItemBodyOffer;
  order?: NoticeItemBodyOrder;
  action: NoticeItemBodyAction;
  garage?: NoticeItemGarage;
  rfq?: CarInfo;
}

export interface NoticeItemBodyOffer {
  id: string;
  listing_id?: string;
  car_info?: CarInfo;
}
export interface NoticeItemBodyOrder {
  id: string;
  order_no?: string;
  car_info?: CarInfo;
  suborders?: Suborders;
}
export interface NoticeItemBodyAction {
  action_name: string;
  action_type: number;
  action_params: NoticeItemBodyActionParams;
}

export interface NoticeItemGarage extends CarInfo {}
export interface NoticeItemBodyActionParams {
  due_time?: number;
  chatroom_id?: string;
  msg_id?: string;
  order_id?: string;
  order_no?: string;
}

export interface NoticeExtra {
  subgroup: string;
  unread: number;
  count: number;
}
