import React, { useState } from 'react';
import { Button } from 'antd';
import { FormattedMessage } from 'react-intl';
import LockedRate from './LockedRate';
import _get from '../../../../../common/get';

type LockedRateButtonProps = {
  style?: React.CSSProperties;
  className?: any;
  payment: any;
  submit: any;
};

const LockedRateButton = ({ style, className = '', payment, submit }: LockedRateButtonProps) => {
  const [showModal, setShowModal] = useState(false);

  const openModal = () => setShowModal(true);
  const closeModal = () => setShowModal(false);

  return (
    <React.Fragment>
      <Button style={style} className={className} onClick={openModal}>
        <FormattedMessage id="drawer_payDeposit_lockAndConfirm" />
      </Button>
      {showModal ? (
        <LockedRate
          submit={submit}
          fromCurrency={_get(payment, ['from_currency'])}
          toCurrency={_get(payment, ['to_currency'])}
          amount={_get(payment, ['amount'])}
          onClose={closeModal}
        />
      ) : (
        ''
      )}
    </React.Fragment>
  );
};

export default LockedRateButton;
