/**
 * make offer 
 */

import React from 'react';
import { Select } from 'antd';
import styles from './index.module.scss';

export type PaymentMethod = {
  name: string;
  value: number;
};

export type PaymentMethodSelectProps = {
  className?: any;
  style?: any;
  selectedPaymentMethod: PaymentMethod;
  paymentMethods: PaymentMethod[];
  onChange: (value: number) => any;
};

export const PaymentMethodSelect = (props: PaymentMethodSelectProps) => {
  const { style, className = '', selectedPaymentMethod, paymentMethods, onChange } = props;
  const Option = Select.Option;
  return (
    <div style={style} className={`${styles.select} ${className}`}>
      <Select
        defaultValue={selectedPaymentMethod.value}
        value={selectedPaymentMethod.value}
        dropdownClassName={styles.dropdown}
        onChange={onChange}
      >
        {paymentMethods.map((item: any) => (
          <Option key={item.value} value={item.value}>
            {item.name}
          </Option>
        ))}
      </Select>
    </div>
  );
};
