// natification module
import { NoticeTabType, NoticeStore, NoticeAction, NoticeActionType } from '../types/notice';

import _get from '../../common/get';
import { SSL_OP_NO_TICKET } from 'constants';

const initialState: NoticeStore = {
  noticeShow: false, // Notice 
  activeNoticeTab: NoticeTabType.OFFERS, //  Notice tab
  hasNewNotice: false, // Natification

  noticeTabNum: {
    offers: 0,
    todo: 0,
    updates: 0,
    offers_unread: 0,
    todo_unread: 0,
    updates_unread: 0
  },

  noticeTabList: {
    noticeListLoading: false,

    offersList: [],
    offersListCurrentPage: 0,
    hasNextOffersListPage: true,

    todoList: [],
    todoListCurrentPage: 0,
    hasNextTodoListPage: true,

    updatesList: [],
    updatesListCurrentPage: 0,
    hasNextUpdatesListPage: true
  }
};

export default (state = initialState, action: NoticeAction): NoticeStore => {
  switch (action.type) {
    case NoticeActionType.SET_NOTICE_SHOW: {
      return {
        ...state,
        noticeShow: action.noticeShow
      };
    }
    case NoticeActionType.SET_ACTIVE_NOTICE_TAB: {
      return {
        ...state,
        activeNoticeTab: action.activeNoticeTab
      };
    }
    case NoticeActionType.SET_NEW_NOTICE: {
      return {
        ...state,
        hasNewNotice: action.hasNewNotice
      };
    }

    case NoticeActionType.SET_NOTICE_TAB_NUM: {
      //tab
      return {
        ...state,
        noticeTabNum: action.noticeTabNum
      };
    }

    case NoticeActionType.SET_OFFERS_LIST: {
      const { noticeTabList } = state;
      const _noticeTabList = Object.assign({}, noticeTabList);
      const { list, currentPage, hasNextPage, needCleanList } = action;
      if (needCleanList) {
        // 
        _noticeTabList.offersList = list;
      } else {
        // 
        _noticeTabList.offersList = noticeTabList.offersList.concat(list);
      }
      _noticeTabList.offersListCurrentPage = currentPage;
      _noticeTabList.hasNextOffersListPage = hasNextPage;
      return {
        ...state,
        noticeTabList: _noticeTabList
      };
    }
    case NoticeActionType.SET_TODO_LIST: {
      const { noticeTabList } = state;
      const _noticeTabList = Object.assign({}, noticeTabList);
      const { list, currentPage, hasNextPage, needCleanList } = action;
      if (needCleanList) {
        // 
        _noticeTabList.todoList = list;
      } else {
        // 
        _noticeTabList.todoList = noticeTabList.todoList.concat(list);
      }
      _noticeTabList.todoListCurrentPage = currentPage;
      _noticeTabList.hasNextTodoListPage = hasNextPage;

      return {
        ...state,
        noticeTabList: _noticeTabList
      };
    }
    case NoticeActionType.SET_UPDATES_LIST: {
      const { noticeTabList } = state;
      const _noticeTabList = Object.assign({}, noticeTabList);
      const { list, currentPage, hasNextPage, needCleanList } = action;
      if (needCleanList) {
        // 
        _noticeTabList.updatesList = list;
      } else {
        // 
        _noticeTabList.updatesList = noticeTabList.updatesList.concat(list);
      }
      _noticeTabList.updatesListCurrentPage = currentPage;
      _noticeTabList.hasNextUpdatesListPage = hasNextPage;

      return {
        ...state,
        noticeTabList: _noticeTabList
      };
    }

    case NoticeActionType.SET_NOTICE_LIST_LOADING: {
      const { noticeTabList } = state;
      const _noticeTabList = Object.assign({}, noticeTabList);
      _noticeTabList.noticeListLoading = action.noticeListLoading;
      return {
        ...state,
        noticeTabList: _noticeTabList
      };
    }

    case NoticeActionType.SET_NOTICE_ITEM_READ_STATUS: {
      const { noticeTabList, noticeTabNum, hasNewNotice } = state;
      let _noticeTabList = Object.assign({}, noticeTabList);
      let _noticeTabNum = Object.assign({}, noticeTabNum);
      let _hasNewNotice = hasNewNotice;
      const { tabType, itemId } = action;
      let _unreadOffers = 0;
      let _unreadTodo = 0;
      let _unreadUpdates = 0;

      if (tabType === NoticeTabType.OFFERS) {
        let __offersList = _noticeTabList['offersList'].map(item => {
          return Object.assign({}, item, {
            is_read: item.id === itemId ? true : item.is_read
          });
        });
        _unreadOffers = __offersList.filter(item => !item.is_read).length;

        _noticeTabNum = Object.assign(_noticeTabNum, { offers_unread: _unreadOffers });
        _noticeTabList = Object.assign(_noticeTabList, { offersList: __offersList });
      }
      if (tabType === NoticeTabType.TO_DO) {
        let __todoList = _noticeTabList['todoList'].map(item => {
          return Object.assign({}, item, {
            is_read: item.id === itemId ? true : item.is_read
          });
        });
        _unreadTodo = __todoList.filter(item => !item.is_read).length;

        _noticeTabNum = Object.assign(_noticeTabNum, { todo_unread: _unreadTodo });
        _noticeTabList = Object.assign(_noticeTabList, { todoList: __todoList });
      }
      if (tabType === NoticeTabType.UPDATES) {
        let __updatesList = _noticeTabList['updatesList'].map(item => {
          return Object.assign({}, item, {
            is_read: item.id === itemId ? true : item.is_read
          });
        });
        _unreadUpdates = __updatesList.filter(item => !item.is_read).length;

        _noticeTabNum = Object.assign(_noticeTabNum, { updates_unread: _unreadUpdates });
        _noticeTabList = Object.assign(_noticeTabList, { updatesList: __updatesList });
      }

      if (_unreadOffers + _unreadTodo + _unreadUpdates > 0) {
        _hasNewNotice = true;
      } else {
        _hasNewNotice = false;
      }
      return {
        ...state,
        noticeTabList: _noticeTabList,
        noticeTabNum: _noticeTabNum,
        hasNewNotice: _hasNewNotice
      };
    }

    case NoticeActionType.REMOVE_NOTICE_ITEM: {
      const { noticeTabList } = state;
      let _noticeTabList = Object.assign({}, noticeTabList);
      const { tabType, itemId } = action;
      if (tabType === NoticeTabType.OFFERS) {
        let __offersList = _noticeTabList['offersList'].filter(item => item.id !== itemId);
        _noticeTabList = Object.assign(_noticeTabList, { offersList: __offersList });
      }
      if (tabType === NoticeTabType.TO_DO) {
        let __todoList = _noticeTabList['todoList'].filter(item => item.id !== itemId);
        _noticeTabList = Object.assign(_noticeTabList, { todoList: __todoList });
      }
      if (tabType === NoticeTabType.UPDATES) {
        let __updatesList = _noticeTabList['updatesList'].filter(item => item.id !== itemId);
        _noticeTabList = Object.assign(_noticeTabList, { updatesList: __updatesList });
      }
      return {
        ...state,
        noticeTabList: _noticeTabList
      };
    }

    default:
      return state;
  }
};
