import React from 'react';
import { Form, Input, Select, Button } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';
import { compose } from 'redux';
import { connect } from 'react-redux';
import IconFont from '../../../components/ui/TradexIcon';
import _get from '../../../common/get';
import { AppStore } from '../../../store/reducers/index';
import styles from './NotesForm.module.scss';

interface BasicProps {}

type IProps = BasicProps & FormComponentProps;

interface IState {}

class NotesForm extends React.Component<IProps, IState> {
  container: any;

  generateID = () =>
    Math.random()
      .toFixed(8)
      .substring(2);

  addField = () => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(this.generateID());
    form.setFieldsValue({
      keys: nextKeys
    });
  };

  deleteField = (k: number) => () => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    form.setFieldsValue({
      keys: keys.filter((key: number) => key !== k)
    });
  };

  handleSubmit = () => {
    const { form } = this.props;
    form.validateFieldsAndScroll(function(error, values) {
      if (error) {
        console.log(error);
        return;
      }

      console.log(values);
    });
  };

  render() {
    const { getFieldDecorator, getFieldsValue, getFieldValue } = this.props.form;
    console.log(getFieldsValue());
    getFieldDecorator('keys', { initialValue: [this.generateID()] });
    const keys = getFieldValue('keys');

    return (
      <div ref={re => (this.container = re)} className={styles.NotesForm}>
        <div className={styles.InfoContainer}>
          <div className={styles.title}>
            <IconFont type="iconicon_guide_area" className={styles.Icon} />
            <span>Shipping</span>
          </div>
          <div className={styles.subTitle}>Trade X International Logistics</div>
          <div className={styles.infomation}>
            International shipping services will be provided by Trade X. All you need to do is drop
            the vehicle in a Trade X warehouse.
          </div>
        </div>

        <div className={styles.optionContainer}>
          <div className={styles.title}>Seller Memo(optional)</div>
          <div className={styles.subTitle}>Only visible to yourself</div>

          <div className={styles.inlineForm}>
            <Form.Item label="Cost" className={styles.cost}>
              {getFieldDecorator('cost')(<Input />)}
            </Form.Item>
            <Form.Item label="unit" className={styles.unit}>
              {getFieldDecorator('unit', { initialValue: 'km' })(
                <Select getPopupContainer={() => this.container} style={{ width: 100 }}>
                  <Select.Option value="km">KM</Select.Option>
                  <Select.Option value="m">M</Select.Option>
                </Select>
              )}
            </Form.Item>
            <Form.Item label="Stock Number" className={styles.cost}>
              {getFieldDecorator('cost')(<Input />)}
            </Form.Item>
          </div>

          <Form.Item label="Other Memo" style={{ width: 668 }}>
            {getFieldDecorator('cost')(<Input />)}
          </Form.Item>
        </div>

        <div className={styles.btnRow}>
          <Button
            type="primary"
            className={styles.nextBtn}
            disabled={keys.length === 0}
            onClick={this.handleSubmit}
          >
            Preview
          </Button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: AppStore) => ({
  createCar: state.createCar
});

export default connect(mapStateToProps)(Form.create()(NotesForm));
