import { ModalType } from './types';

type ModalConfigType = {
  [index: string]: {
    text: {
      title: string;
      warning: string;
      button: string;
      tips: string;
    };
    verify?: {
      onInput?: (value: string) => boolean;
      onSubmit?: (value: string) => boolean;
    };
    getPostParam: (value: string) => any;
  };
};

const EmailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;

export const ModalConfig: ModalConfigType = {
  [ModalType.Email]: {
    text: {
      title: `account_profile_${ModalType.Email}_title`,
      warning: `account_profile_${ModalType.Email}_warning`,
      button: `account_profile_${ModalType.Email}_button`,
      tips: `account_profile_${ModalType.Email}_tips`
    },
    verify: {
      onSubmit: (value: string = ''): boolean => value === '' || EmailReg.test(value) // 
    },
    getPostParam: (value: string) => ({ work_email: value })
  },
  [ModalType.Username]: {
    text: {
      title: `account_profile_${ModalType.Username}_title`,
      warning: `account_profile_${ModalType.Username}_warning`,
      button: `account_profile_${ModalType.Username}_button`,
      tips: `account_profile_${ModalType.Username}_tips`
    },
    verify: {
      onSubmit: (value: string = ''): boolean => value.length <= 20
    },
    getPostParam: (value: string) => ({ nickname: value })
  }
};
