import store from 'store';
import { SettingTypes, SettingInfoStore, SettingActions } from '../types';

export const initialState: SettingInfoStore = store.get('Setting') || {
  ALL_CURRENCIES: [],
  DEFAULT_DEPOSIT_PERCENTAGE: 1
};

// todo action as any
export default (state = initialState, action: SettingActions): SettingInfoStore => {
  switch (action.type) {
    case SettingTypes.SET_SETTING_INFO: {
      store.set('Setting', (action as any).settingInfo);

      return {
        ...state,
        ALL_CURRENCIES: (action as any).settingInfo.ALL_CURRENCIES,
        DEFAULT_DEPOSIT_PERCENTAGE: (action as any).settingInfo.DEFAULT_DEPOSIT_PERCENTAGE
      };
    }
    default:
      return state;
  }
};
