import {
  CountTypes,
  HomeStore,
  CountAction,
} from '../types'

const initialState: HomeStore = {
  count: 5
};

export default (state = initialState, action: CountAction): HomeStore => {
  switch (action.type) {
    case CountTypes.INCREASE_COUNT: {
      if (action.page !== 'home') return state;
      return {
        ...state,
        count: ++state.count
      };
    }
    case CountTypes.REDUCE_COUNT: {
      if (action.page !== 'home') return state;
      return {
        ...state,
        count: --state.count
      };
    }
    default:
      return state;
  }
};
