import { get, post } from './http';
import { AxiosPromise } from 'axios';

export interface LoginByPhoneSMSParma {
  phone: string;
  code: string;
}

export interface FetchSMSCodeRes {
  show_captcha: boolean;
  test: string;
  captcha: string;
  server_time: string;
}

/**
 * 
 * @param data
 */
export function loginByPhoneSMS(data: LoginByPhoneSMSParma): AxiosPromise<any> {
  return post('users/signup-signin/', data);
}

/**
 * 
 * @param data
 */
export function fetchSMSCode(data: { phone: string }): AxiosPromise<FetchSMSCodeRes> {
  return post('sms/code/', data);
}

/**
 * ()
 * @param data
 */
export function fetchSMSCodeWithCaptcha(data: {
  phone: string;
  captcha: string;
}): AxiosPromise<any> {
  return post('sms/code_with_captcha/', data);
}

/**
 * 
 * @param data
 */
export function updateCaptcha(data: { phone: string }): AxiosPromise<any> {
  return post('sms/update_captcha/', data);
}

/**
 * 
 */
export function logout() {
  return post('users/signout/', {});
}

/**
 * 
 */
export function getUserInfo(): AxiosPromise<any> {
  return get('users/me/');
}

/**
 * Pad
 */
export function getPadInfo(): AxiosPromise<any> {
  return get('users/pad_info/');
}

/***
 * 
 */
export function registerForm(data: object): AxiosPromise<any> {
  return post('users/fill_profile/', data);
}

/**
 * 
 */
export function changeheadshot(data: any): AxiosPromise<any> {
  return post('users/changeheadshot/', data);
}

/**
 * paper
 */
export function uploadPaper(data: any): AxiosPromise<any> {
  return post('users/paper/', data);
}

export function EditUser(data: { dest_port_id: string; [index: string]: any }): AxiosPromise<any> {
  return post('users/edit/', data);
}
