import { getCookie, removeCookie } from '../../common/cookie';

const WSS_ROOT = process.env.WSS_ROOT
  ? process.env.WSS_ROOT + '/ws'
  : 'wss://tradex-ws-pre.tradex-cdn.com/ws';

const getAuth = () => {
  let token = getCookie('Authorization');
  return (token = token ? token.replace('Token ', '') : '');
};

const HEART_BEAT_INTERVAL = 30000; // 
const CONNECTING = 'CONNECTING';
const OPEN = 'OPEN';
const CLOSING = 'CLOSING';
const CLOSED = 'CLOSED';
const mapReadyState: string[] = [CONNECTING, OPEN, CLOSING, CLOSED];

/**
 * ws
 */
function getWebSocketURL(params: WebSocketManagerParam) {
  const token = getAuth();
  if (params.chatroom_id) {
    return `${WSS_ROOT}/chatroom/${params.chatroom_id}/?token=${token}`;
  } else if (params.notification) {
    return `${WSS_ROOT}/${params.notification}/?token=${token}`;
  } else {
    return `${WSS_ROOT}/user/?token=${token}`;
  }
}

interface WebSocketManagerParam {
  onOpen?: Function;
  onClose?: Function;
  onError?: Function;
  onMessage?: Function;
  chatroom_id?: string;
  notification?: string;
}

export class WebSocketManager {
  wss: WebSocket;
  isManualClosed: Boolean;
  params: WebSocketManagerParam;
  checker!: number;

  constructor(params: WebSocketManagerParam = {}) {
    this.params = params;

    // false
    this.isManualClosed = false;

    // WebSocket
    this.wss = new WebSocket(getWebSocketURL(params));

    // onOpen
    this.wss.onopen = () => {
      this.checker = this.heartCheck(params);
      params.onOpen && params.onOpen();
    };

    // onMessage
    this.wss.onmessage = (data: any) => {
      params.onMessage && params.onMessage(JSON.parse(data.data));
    };

    // onClose
    this.wss.onclose = () => {
      params.onClose && params.onClose();

      if (!this.isManualClosed) {
        //
        this.wss = new WebSocket(getWebSocketURL(params));
      }
    };

    // onError
    this.wss.onerror = () => {
      params.onError && params.onError();

      if (!this.isManualClosed) {
        //
        this.wss = new WebSocket(getWebSocketURL(params));
      }
    };
  }

  /**
   * 
   */
  heartCheck = (params: WebSocketManagerParam): number => {
    const timer = window.setInterval(() => {
      const state = this.checkReadyState();
      if (state === OPEN) {
        this.wss.send(JSON.stringify({ type: 'ping' }));
      } else if (!this.isManualClosed) {
        //
        this.wss = new WebSocket(getWebSocketURL(params));
      }
    }, HEART_BEAT_INTERVAL);
    return timer;
  };

  checkReadyState = () => Reflect.get(mapReadyState, this.wss.readyState);

  // send
  send = (data: any) => {
    const state = this.checkReadyState();
    state === OPEN
      ? this.wss.send(JSON.stringify(data))
      : this.params.onError && this.params.onError(`Current socket ready state is ${state}`);
  };

  // close
  close = () => {
    //
    this.isManualClosed = true;
    this.checker && clearInterval(this.checker);
    this.wss.close();
  };
}
