import React from 'react';
import { Button } from 'antd';
import { FormattedMessage } from 'react-intl';
import IconFont from '../ui/TradexIcon';
import styles from './index.module.scss';
import Modal from './Modal';

type AlertModalProps = {
  children?: any;
  content?: any;
  confirmMessage?: any;
  onClose?: () => any;
  handleConFirm?: () => any;
};

const AlertModal = (props: AlertModalProps) => {
  const { children, content, onClose, confirmMessage, handleConFirm } = props;

  return (
    <div className={styles.mask}>
      {children ? (
        children
      ) : (
        <div className={styles.container}>
          <div className={styles.content}>
            {content ? content : <FormattedMessage id="alertModal_content" />}
          </div>
          <div className={styles.divider}></div>
          <div className={styles.operation}>
            <Button className={styles.button} onClick={handleConFirm || onClose}>
              {confirmMessage ? confirmMessage : <FormattedMessage id="alertModal_buttonContent" />}
            </Button>
          </div>
          <div className={styles.cancel} onClick={onClose}>
            <IconFont type="iconicon_cancel" />
          </div>
        </div>
      )}
    </div>
  );
};

export default AlertModal;
export { Modal };
