import hmacSHA256 from 'crypto-js/hmac-sha256';
import Base64 from 'crypto-js/enc-base64';

const SECRET = '!&y0#s&9(=y36%z741#p^aj13#mt=idyl)!*bm6da#*al8)o$=';

const createSignature = (endpoint: string): string => {
  let hash = hmacSHA256(endpoint, SECRET);
  let signature = Base64.stringify(hash);
  return signature;
};
const Security = {
  createSignature
};

export default Security;
