import React, { useState } from 'react';
import styles from './UploadImage.module.scss';
import { Upload, Spin, Icon } from 'antd';
import { getCookie } from '../../../../../../common/cookie';
import _get from '../../../../../../common/get';
import { ImageInfo } from '../types';
import { FormattedMessage } from 'react-intl';

enum STATUS {
  DONE = 'done',
  LOADING = 'uploading',
  ERROR = 'error'
}

const UploadIcon = ({ width, height }: any) => (
  <svg
    width={width || '100%'}
    height={height || '100%'}
    viewBox="0 0 960 960"
    version="1.1"
    xmlns="http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
  >
    <title>icon 18</title>
    <desc>Created with Sketch.</desc>
    <g id="icon-18" stroke="none" strokeWidth="1" fill="none" fillRule="evenodd">
      <g id="" transform="translate(150.000000, 60.000000)">
        <path
          d="M450,0 L660,210 L660,780 C660,813.137085 633.137085,840 600,840 L60,840 C26.862915,840 4.05812251e-15,813.137085 0,780 L0,60 C-4.05812251e-15,26.862915 26.862915,6.08718376e-15 60,0 L450,0 Z M330,265 L180,415 L270,415 L270,575 L390,575 L390,415 L480,415 L330,265 Z"
          id=""
          fill="#EBEBEB"
        ></path>
        <path
          d="M450,0 L660,210 L480,210 C463.431458,210 450,196.568542 450,180 L450,0 L450,0 Z"
          id=""
          fill="#CCCCCC"
        ></path>
      </g>
    </g>
  </svg>
);

const Padding = ({ style, className, ...restProps }: any) => {
  const PaddingIcon = <Icon type="loading" style={{ fontSize: 24 }} spin />;
  return (
    <div style={style} className={className}>
      <Spin indicator={PaddingIcon} />
    </div>
  );
};

const UploadImage = ({ style, className, ...restProps }: any) => {
  const { warning, isNoImages } = restProps;
  const [status, setStatus] = useState(STATUS.DONE);

  const API_ROOT = process.env.API_ROOT ? process.env.API_ROOT + '/api' : '/api';
  const uploadProps = {
    showUploadList: false,
    action: API_ROOT + `/v2/pictures/`,
    accept: '.jpg, .png',
    headers: {
      Authorization: getCookie('Authorization')
    } as any,
    data: (file: any) => ({ image: file }),
    onChange: (info: any) => {
      const { status } = info.file;
      if (status === STATUS.LOADING) {
        setStatus(STATUS.LOADING);
      } else if (status === STATUS.DONE) {
        const { onChange } = restProps;
        const { url, server_time: serverTime } = _get(info, ['file', 'response']);
        setStatus(STATUS.DONE);
        if (onChange) {
          onChange({ url, serverTime } as ImageInfo);
        }
      } else {
        setStatus(STATUS.ERROR);
      }
    }
  };

  const warningMessage = (
    <span className={styles.warning}>
      <FormattedMessage id="drawer_imagesWarning" />
    </span>
  );

  const uploadImage = (
    <div style={style} className={`${styles.container} ${className}`}>
      <Upload {...uploadProps}>
        <div style={style}>{status === STATUS.LOADING ? <Padding /> : <UploadIcon />}</div>
      </Upload>
    </div>
  );

  const noImagesUploadImage = (
    <div className={`${styles.container1} ${className}`}>
      <Upload className={styles.upload} style={styles} {...uploadProps}>
        <div style={style}>{status === STATUS.LOADING ? <Padding /> : <UploadIcon />}</div>
      </Upload>
      <span className={styles.noImages}>
        <FormattedMessage id="drawer_noImages" />
      </span>
      {warning ? warningMessage : ''}
    </div>
  );

  return <React.Fragment>{isNoImages ? noImagesUploadImage : uploadImage}</React.Fragment>;
};
export default UploadImage;
