import React, { useState, useEffect } from 'react';
import Button from '../../../../components/Btn';
import UploadImage from '../../../../components/UploadImage';
import ImageDiv from '../../../../components/ImageDiv';
import IconFont from '../../../../components/ui/TradexIcon';
import styles from './index.module.scss';

interface Image {
  url: string;
}

const UploadPhoto = (props: any) => {
  const { className, style } = props;
  let [carPictures, setCarPictures] = useState([] as Image[]);
  let [stickers, setStickers] = useState([] as Image[]);
  const removeImage = (images: Image[], setImages: any, deletedImage: Image) => {
    let newImages = images.filter(image => image.url !== deletedImage.url);
    setImages(newImages);
  };
  return (
    <div className={`${styles.container} ${className}`} style={style}>
      <div className={styles.pictureSection}>
        <div className={styles.title}>
          <div className={styles.text1}>*</div>
          <div>Car Picturs</div>
        </div>
        <div className={styles.pictures}>
          {carPictures.map(image => {
            return (
              <ImageDiv className={styles.picture} url={image.url}>
                <IconFont
                  type="iconicon_delete_line"
                  className={styles.icon}
                  onClick={() => removeImage(carPictures, setCarPictures, image)}
                />
              </ImageDiv>
            );
          })}
          <UploadImage
            className={styles.uploadImage}
            onChange={(image: any) => setCarPictures([...carPictures, image])}
          />
        </div>
      </div>
      <div className={styles.divider}></div>
      <div className={styles.pictureSection}>
        <div className={styles.title}>
          <div className={styles.text1}>*</div>
          <div>Window Sticker / Bill of Sale</div>
        </div>
        <div className={styles.pictures}>
          {stickers.map(image => {
            return (
              <ImageDiv className={styles.picture} url={image.url}>
                <IconFont
                  type="iconicon_delete_line"
                  className={styles.icon}
                  onClick={() => removeImage(stickers, setStickers, image)}
                />
              </ImageDiv>
            );
          })}
          <UploadImage
            className={styles.uploadImage}
            onChage={(image: any) => setStickers([...stickers, image])}
          />
        </div>
      </div>
      <div className={styles.next}>
        <Button>Next</Button>
      </div>
    </div>
  );
};

export default UploadPhoto;
