import React from 'react';
import { FormattedNumber } from 'react-intl';

enum PriceType {
  DEFAULT = 'default',
  ORIGINAL = 'original'
}

type FormattedPriceProps = {
  prefix?: string;
  currency?: string;
  type?: PriceType;
  value: number;
};

export const FormattedPrice = (props: FormattedPriceProps) => {
  const { value, currency, prefix, type = PriceType.DEFAULT } = props;

  switch (type) {
    case PriceType.ORIGINAL:
      return (
        <React.Fragment>
          {prefix ? prefix : ''}
          <FormattedNumber value={value} maximumFractionDigits={2} minimumFractionDigits={2} />
          {currency ? currency : ''}
        </React.Fragment>
      );
    default:
      return (
        <React.Fragment>
          {`$`}
          <FormattedNumber value={value} maximumFractionDigits={2} minimumFractionDigits={2} />
          {` ${currency}`}
        </React.Fragment>
      );
  }
};
