import { get, post, put } from './http';
import request from './http/request';
import { RootAPI } from '../api/http/config';
import { AxiosPromise } from 'axios';

export interface GetOrderListParams {
  page?: number;
  is_buyer?: 1 | 0;
  role: string;
  order_id?: string;
}

export function CreateOrder(garage: string): AxiosPromise {
  return post('/orders/', { garage });
}

export function GetOrderList(params: GetOrderListParams): AxiosPromise {
  return get('/orders/center/', params);
}

export function GetOrderDetail(id: string): AxiosPromise {
  return get(`/orders/${id}/`);
}

export function ConfirmConditionReport(id: string): AxiosPromise {
  return put(`conditionreports/${id}/confirm/`);
}

export function RejectConditionReport(): AxiosPromise {
  return put('/conditionreports/11/reject/');
}

export function AddVehicle(
  id: string,
  order_type: number,
  opt: {
    vin: string;
    exterior_color: string;
    interior_color: string;
    distance_to_warehouse: string;
    distance_unit: 'KM' | 'M';
  }
): AxiosPromise {
  return order_type === 2
    ? post(`/rfq/orders/${id}/vehicle/`, opt)
    : post(`/orders/${id}/vehicle/`, opt);
}

export function getWarehouseWorkingTime(id: string): AxiosPromise {
  return get(`/warehouses/${id}/working_time/`);
}

export function appointment(
  id: string, // subOrder id
  opt: {
    type: 1 | 2; //1    2
    book_time: any;
    warehouse_id: string;
  }
): AxiosPromise {
  return post(`/orders/${id}/appointment/`, opt);
}

export function signLc(id: string, params?: any): AxiosPromise {
  return post(`/orders/${id}/sign_lc/`, params);
}

export function getSignImages(url: string) {
  return get(`${url}&format=json`, {}, false);
}

export function disbursements(id: string) {
  return get(`/orders/${id}/disbursements/`);
}

export function getOrdeMakes() {
  return get(`/ordermake/`);
}

export function getOrdermonth() {
  return get(`/ordermonth/`);
}

export function getOrderStatus() {
  return get(`/orders/status/`);
}

//make=Aston%20Martin&page=1&role=buyer&status=3
export function orderfilter(opt: object): AxiosPromise {
  return get('/orderfilter/', opt, true, true);
}

//role:'buyer' | 'seller'
export function ordersearch(opt: { page: number; role: string; search_txt: string }): AxiosPromise {
  return get('/ordersearch/', opt, true, true);
}
