import React from 'react';
import { FormattedMessage } from 'react-intl';
import Item from './Item';
import Input from './Input';
import styles from './index.module.scss';

const InputItem = ({ style, className, ...restProps }: any) => {
  const { amount, amountChange, inputWarning, setInputWarning, currency } = restProps;

  return (
    <Item style={style} className={`${styles.inputItem} ${className}`}>
      <div className={styles.name}>
        <FormattedMessage id="drawer_payDeposit_inputName" />
      </div>
      <div className={styles.inputSection}>
        <div className={styles.input}>
          <Input
            className={`${inputWarning ? styles.invalid : ''}`}
            prefix={<div className={styles.prefix}>$</div>}
            onChange={amountChange}
            value={amount}
            warning={inputWarning}
            setWarning={setInputWarning}
          />
        </div>
        <div className={styles.currency}>{currency || ''}</div>
      </div>
    </Item>
  );
};

export default InputItem;
