import React, { useState } from 'react';
import styles from './Image.module.scss';
import UploadImage from '../../../components/UploadImage';
import MaskImage from '../../../components/MaskImage';
import IconFont from '../../../components/ui/TradexIcon';
import Swiper from '../../../components/Swiper';
import util from '../../../common/util';

type ImageProps = {
  type?: 'view' | 'edit';
  src?: string;
  warning?: boolean;
  onChange?: any;
};

const Iamge = (props: ImageProps) => {
  const [src, setSrc] = useState(props.src || '');
  const [showSwiper, setShowSwiper] = useState(false);

  const { warning = false, type = 'view' } = props;

  const onChange = (value: any) => {
    setSrc(value.url);
    props.onChange && props.onChange(value.url);
  };

  const zoom = () => setShowSwiper(true);
  const remove = () => onChange({ url: '' });
  const closeSwiper = () => setShowSwiper(false);

  const maskImage = (
    <MaskImage url={src} className={warning ? styles.warning : ''}>
      <div className={styles.MaskIcon} onClick={zoom}>
        <IconFont type="iconicon_zoom" />
      </div>
    </MaskImage>
  );

  const editMaskImage = (
    <MaskImage
      url={src}
      className={warning ? styles.warning : ''}
      zoom={zoom}
      remove={remove}
    ></MaskImage>
  );
  const uploadImage = <UploadImage className={warning ? styles.warning : ''} onChange={onChange} />;

  const Image = type === 'view' ? maskImage : editMaskImage;

  return (
    <div className={styles.container}>
      {src ? Image : uploadImage}
      {showSwiper ? (
        <Swiper
          imageSrc={[util.addUrlSuffix(src, 'x-oss-process=style/web_640')]}
          onClose={closeSwiper}
        />
      ) : (
        ''
      )}
    </div>
  );
};

export default Iamge;
