export default {
  pluralize(n: number, unit: string, cap = false) {
    let u = n > 1 ? `${unit}s` : unit;
    u = cap ? u.toUpperCase() : u;
    return `${n} ${u}`;
  },

  lockScroll(clasName: string, setHeight = false) {
    let els: HTMLElement[] = Array.from(document.querySelectorAll(`.${clasName}`));
    if (els.length === 0) {
      console.error(`prevent scroll overflow err: no selector ${clasName}`);
      return;
    }

    els.forEach(el => {
      if (setHeight) {
        el.style.height = '100%';
      }

      (el as any).onmousewheel = function(e: any) {
        const scrollTop = this.scrollTop,
          scrollHeight = this.scrollHeight,
          height = el.clientHeight,
          delta = e.deltaY;

        const isBottom = scrollTop + height === scrollHeight && delta > 0,
          isTop = scrollTop === 0 && delta < 0;

        if (isBottom || isTop) {
          e.stopPropagation();
          e.preventDefault();
        }
      };
    });
  },

  scrollTop(el = document.body) {
    el.scroll({
      top: 0,
      behavior: 'smooth'
    });
  },

  numberWithCommas(x: number | string) {
    return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
  },

  formatDate(t: number) {
    const date = new Date(t);

    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    const hours = date.getHours();
    const minutes = date.getMinutes();
    const minutesStr = minutes < 10 ? '0' + minutes : minutes;
    return year + '-' + month + '-' + day + ' ' + hours + ':' + minutesStr;
  },

  getMessageTime(last: string) {
    if (!last) return false;
    const originPrev = new Date(Number(last));
    const prev = originPrev.getTime();
    const now = Date.now();
    const dateDiff = now - prev;
    const minuteDiff = Math.floor(dateDiff / (60 * 1000));
    // console.log('last: ', last);
    // console.log('originPrev: ', originPrev);
    // console.log('prev: ', prev);
    // console.log('now: ', now);
    // console.log('minuteDiff: ', minuteDiff);
    // console.log('dateDiff: ', dateDiff);
    const dayDiff = Math.floor(dateDiff / (24 * 60 * 60 * 1000));
    const year = `${originPrev.getFullYear()}`;
    const month = `${originPrev.getMonth() + 1 < 10 ? '0' : ''}${originPrev.getMonth() + 1}`;
    const day = `${originPrev.getDate() < 10 ? '0' : ''}${originPrev.getDate()}`;
    const hour = `${originPrev.getHours() < 10 ? '0' : ''}${originPrev.getHours()}`;
    const minute = `${originPrev.getMinutes() < 10 ? '0' : ''}${originPrev.getMinutes()}`;
    if (dayDiff < 1 && new Date().getDay() === originPrev.getDay()) {
      return `${hour}:${minute}`;
    }
    if (dayDiff < 2) {
      return `yesterday ${hour}:${minute}`;
    }
    if (dayDiff < 365) {
      return `${month}-${day} ${hour}:${minute}`;
    }
    return `${year}-${month}-${day} ${hour}:${minute}`;
  },

  throttle(func: any, wait: number, options: any) {
    var context: any, args: any, result: any;
    var timeout: any = null;
    var previous = 0;
    if (!options) options = {};
    var later = function() {
      previous = options.leading === false ? 0 : Date.now();
      timeout = null;
      result = func.apply(context, args);
      if (!timeout) context = args = null;
    };
    return function(this: any) {
      var now = Date.now();
      if (!previous && options.leading === false) previous = now;
      var remaining = wait - (now - previous);
      context = this;
      args = arguments;
      if (remaining <= 0 || remaining > wait) {
        if (timeout) {
          clearTimeout(timeout);
          timeout = null;
        }
        previous = now;
        result = func.apply(context, args);
        if (!timeout) context = args = null;
      } else if (!timeout && options.trailing !== false) {
        timeout = setTimeout(later, remaining);
      }
      return result;
    };
  },

  getQuery(qs: string, key: string): string {
    qs = decodeURIComponent(qs);
    const reg = new RegExp(`${key}=(.+)!?&`);
    const match = qs.match(reg);
    if (match && match.length > 1) {
      return match[1];
    }
    return '';
  },

  formatNoticeLeftTimeStr(t: number) {
    // notice 
    let str: string;
    t = t * 1000;
    const left = (t / 1000 - Date.now() / 1000) / 60;
    if (left > 60) {
      str = Math.floor(left / 60) + ' h left';
    } else if (left > 30) {
      str = '1 h left';
    } else if (left > 15) {
      str = '30 min left';
    } else if (left > 5) {
      str = '15 min left';
    } else if (left > 0) {
      str = '5 min left';
    } else {
      str = 'Expired';
    }
    return str;
  },

  debounce(fn: any, interval = 300) {
    let timeout: any = null;
    return function() {
      clearTimeout(timeout);
      timeout = setTimeout(() => fn(), interval);
    };
  },

  addUrlSuffix(url: string, suf: string) {
    let connector = url.includes('?') ? '&' : '?';
    return `${url}${connector}${suf}`;
  }
};
