import React, { useState } from 'react';
import styles from './index.module.scss';
import { FormattedMessage } from 'react-intl';
import MaskImage from '../../../../../../components/MaskImage';
import UploadImage from './UploadImage';
import InputItem from '../InputItem';
import Item from '../Item';
import Swiper from '../../../../../../components/Swiper';
import { ImageInfo } from '../types';
import util from '../../../../../../common/util';

const ImageSection = ({ style, className = '', ...resProps }: any) => {
  const {
    images = [],
    amount,
    amountChange,
    imagesChange,
    inputWarning,
    setInputWarning,
    uploadWarning,
    currency
  } = resProps;

  const [showSwiper, setShowSwiper] = useState(false);

  const removeImage = (imageInfo: ImageInfo) => {
    const newImages = images.filter(
      (image: ImageInfo) => image.serverTime !== imageInfo.serverTime
    );
    imagesChange && imagesChange(newImages);
  };
  const addImage = (imageInfo: ImageInfo) => {
    const newImages = [...images, imageInfo];
    imagesChange && imagesChange(newImages);
  };
  const zoomImage = () => setShowSwiper(true);
  const closeSwiper = () => setShowSwiper(false);
  const getImageClass = (index: number) => {
    let iamgeClass = `${styles.imageDiv}`;
    if (index % 4 === 0) {
      iamgeClass += ` ${styles.lastImage}`;
    }
    if (index > 4) {
      iamgeClass += ` ${styles.multiLineImage}`;
    }
    return iamgeClass;
  };

  return (
    <div
      style={style}
      className={`${styles.container} ${
        inputWarning || uploadWarning ? styles.invalid : ''
      } ${className}`}
    >
      <InputItem
        className={styles.item}
        {...{ amount, amountChange, currency, setInputWarning, inputWarning }}
      />
      <Item className={styles.warningItem}>
        {inputWarning ? (
          <div className={styles.warningMessage}>
            <FormattedMessage id="drawer_inputWarning" />
          </div>
        ) : (
          ''
        )}
      </Item>
      <div className={styles.divider}></div>
      <div className={`${styles.uploadSection} ${images.length < 1 ? styles.noImages : ''}`}>
        {images.map((image: ImageInfo, index: number) => {
          const props = {
            key: image.serverTime,
            url: image.url,
            zoom: zoomImage,
            remove: () => removeImage(image)
          };
          return <MaskImage className={getImageClass(index + 1)} {...props} />;
        })}
        <UploadImage
          className={getImageClass(images.length + 1)}
          onChange={addImage}
          isNoImages={images.length < 1}
          warning={uploadWarning}
        />
      </div>
      {showSwiper ? (
        <Swiper
          imageSrc={images.map((image: ImageInfo) =>
            util.addUrlSuffix(image.url, 'x-oss-process=style/web_640')
          )}
          onClose={closeSwiper}
        />
      ) : (
        ''
      )}
    </div>
  );
};

export default ImageSection;
