import React, { ReactElement, useEffect, useState, Suspense } from 'react';
import { Route, BrowserRouter, Switch, Redirect, RouteComponentProps } from 'react-router-dom';
import { useLocation } from 'react-router-dom';
import Routers, { defaultDocTitle } from './routers';
import { getCookie } from '../common/cookie';
import BasicLayout from '../components/Layout/BasicLayout';
import Notice from '../components/Notice/index';
import { AppStore } from '../store/reducers';
import { connect } from 'react-redux';
import { UserInfoStore } from '../store/types';
const Chat = React.lazy(() => import('../components/Chat/index'));

interface IApp {
  user: UserInfoStore;
}

const App: React.FC<IApp> = ({ user }) => {
  let location = useLocation();
  const getValidUrl = (url: string) => (url === '/' ? url : url.slice(0, url.lastIndexOf('/')));

  const [auth, setAuth] = useState(false);

  useEffect(() => {
    const currentPath = getValidUrl(location.pathname);
    const mapedItem = Routers.find(v => {
      const itemPath = getValidUrl(v.path);
      if (!itemPath || !currentPath) return false;
      return itemPath === currentPath;
    });
    document.title = (mapedItem && mapedItem.title) || defaultDocTitle;
    getCookie('Authorization') && setAuth(true);
  });

  const paperStatus = (JSON.parse(localStorage.getItem('paper') as string) || {}).status;

  return (
    <React.Fragment>
      <Switch>
        {Routers.map((item, index) => {
          return (
            <Route
              key={index}
              path={item.path}
              exact={item.path !== '*'}
              render={(props: RouteComponentProps) =>
                item.auth &&
                (!getCookie('Authorization') ||
                  (item.path.indexOf('register') == -1 && paperStatus != 2)) ? (
                  <Redirect
                    to={{
                      pathname: '/login',
                      state: { from: props.location }
                    }}
                  />
                ) : item.withBasicLayout ? (
                  <BasicLayout style={{ backgroundColor: item.layoutBackColor }}>
                    <item.component {...props} key={props.location.pathname} />
                  </BasicLayout>
                ) : (
                  <item.component {...props} />
                )
              }
            />
          );
        })}
      </Switch>
      {auth &&
        paperStatus == 2 &&
        user.userInfo.userId &&
        localStorage.getItem('hideChat') !== '1' && (
          <Suspense fallback={<React.Fragment />}>
            <Chat />
          </Suspense>
        )}
      {auth && paperStatus == 2 && user.userInfo.userId && <Notice />}
    </React.Fragment>
  );
};

const mapStateToProps = (state: AppStore): Pick<AppStore, 'user'> => ({
  user: state.user
});

const WithStoreApp = connect(mapStateToProps)(App);

export default (): ReactElement => {
  return (
    <BrowserRouter>
      <WithStoreApp />
    </BrowserRouter>
  );
};
