import React from 'react';
import { DrawerConfig } from '../../../index';
import { ActionStatus } from '../../index';
import IconFont from '../../../../../components/ui/TradexIcon';
import styles from './index.module.scss';
import { copyTextToClipboard } from '../../../../../common/copyToClipboard';

import Maersk_Logo from '../../../../../assets/img/Maersk_Logo.png';

interface Props {
  drawerConfig: DrawerConfig;
  detail: any;
  data: any;
  setActionStatus: (status: ActionStatus) => void;
}

class Track extends React.Component<Props, object> {
  static defaultProps: Props;
  state = {
    showTips: false,
    TrackNumber: 0,
    Warehouse: '',
    Arrived: '',
    ArrivedCoiuntry: ''
  };

  componentDidMount() {
    this.getTrackData();
  }

  getTrackData = () => {
    const { detail } = this.props;
    // console.log('detail', detail)
    let Warehouse = '',
      Arrived = '',
      TrackNumber = 0,
      ArrivedCoiuntry = '';
    try {
      if (detail && detail.trackers) {
        detail.trackers.map((item: object) => {
          // @ts-ignore
          if (item.track_type === 5 && item.details) {
            // @ts-ignore
            item.details.map(de => {
              if (de.msg_type === 37) {
                Warehouse = de.extra.warehouse.name.key;
                TrackNumber = de.extra.track_num;
              }
              if (de.msg_type === 38) {
                Arrived = de.extra.port.name;
                ArrivedCoiuntry = de.extra.port.country;
              }
            });
          }
        });
      }
    } catch (err) {
      console.log('err', err);
    }
    this.setState({
      Warehouse,
      TrackNumber,
      Arrived,
      ArrivedCoiuntry
    });
  };

  componentDidUpdate(nextProps: Props) {
    if (nextProps !== this.props) {
      this.getTrackData();
    }
  }

  toTRackComponey = () => {
    window.open('https://www.maersk.com/', '_blank');
  };

  copy = () => {
    const { TrackNumber } = this.state;
    // console.log('String(TrackNumber)', String(TrackNumber));
    const res = copyTextToClipboard(String(TrackNumber));
    // console.log('res', res);
    if (res) {
      this.setState({
        showTips: true
      });
      setTimeout(
        () =>
          this.setState({
            showTips: false
          }),
        2000
      );
    }
  };

  render() {
    const { showTips, Warehouse, TrackNumber, Arrived, ArrivedCoiuntry } = this.state;
    // console.log('showTips', showTips);
    return (
      <div className={styles.content}>
        <div className={styles.title}>Track Shipment</div>
        <div className={styles.subTitle}>
          {ArrivedCoiuntry
            ? `The car has arrived at ${Arrived}, ${ArrivedCoiuntry}.`
            : Arrived
            ? `The car has been shipped from ${Warehouse} warehouse.`
            : ''}
        </div>
        <div className={styles.container}>
          <div className={styles.LogisticsVendor}>
            <span className={styles.label}> Logistics Vendor</span>
            <img src={Maersk_Logo} alt="" className={styles.lvImg} />
          </div>
          <div className={styles.trackNumBox}>
            <span className={styles.label}>Tracking Number</span>
            <div className={styles.trackNumCon}>
              <span className={styles.trackNum}>{TrackNumber ? TrackNumber : ''}</span>
              <IconFont
                type="iconicon_copy_line"
                color="#3bcdfe"
                className={styles.copyIcon}
                onClick={this.copy}
              />
            </div>
          </div>
          {showTips ? (
            <div className={styles.tips}>The tracking number has been copied.</div>
          ) : null}
        </div>
        <button className={styles.submitBtn} onClick={this.toTRackComponey}>
          Track
        </button>
      </div>
    );
  }
}

export default Track;
