import React, { useState, Component } from 'react';
import { ActionStatus } from '../../index';
import { DrawerConfig } from '../../../index';
import styles from './index.module.scss';
import IconFont from '../../../../../components/ui/TradexIcon';
import Swiper from '../../../../../components/Swiper';
import '../../../../../assets/style/common.scss';
import { OperType } from '../../../index';
import { ConfirmConditionReport, RejectConditionReport } from '../../../../../api/order';
import util from '../../../../../common/util';

interface Props {
  data: any;
  detail: any;
  drawerConfig: DrawerConfig; //operType: OperType;roleType: RoleType;orderType: OrderType;
  setActionStatus: (status: ActionStatus) => void;
}

interface State {
  isShowSwiper: boolean;
}

export default class Home extends Component<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      isShowSwiper: false
    };
  }

  componentDidMount() {
    console.log(this.props);
  }

  confirmConditionReport = () => {
    let condition_report_id = this.props.detail.condition_reports[0].id || '';
    this.props.setActionStatus(ActionStatus.Loading);
    ConfirmConditionReport(condition_report_id)
      .then((response: any) => {
        this.props.setActionStatus(ActionStatus.Success);
      })
      .catch(rejection => {
        this.props.setActionStatus(ActionStatus.Error);
      });

    this.props.setActionStatus(ActionStatus.Loading);
  };

  rejectConditionReport = () => {
    // this.props.onClose();
    // RejectConditionReport()
    //   .then((response: any) => {
    //     this.props.setActionStatus(ActionStatus.Success);
    //   })
    //   .catch(rejection => {
    //     this.props.setActionStatus(ActionStatus.Error);
    //   });
    // this.props.setActionStatus(ActionStatus.Loading);
  };

  showSwiper = () => {
    this.setState({
      isShowSwiper: true
    });
  };

  render() {
    const {
      detail,
      drawerConfig: { operType },
      setActionStatus
    } = this.props;

    const condition_reports = (detail && detail.condition_reports[0]) || {};

    return (
      <div>
        <div className={styles.ConditionReportContainer}>
          {/* Condition Report Title */}
          <div className={styles.ConditionReport}>
            Condition Report
            <span
              className={styles.MatchingRatio}
            >{`(Matching Ratio: ${condition_reports.match_percentage}%)`}</span>
          </div>
          <div className={styles.ConditionReportBox}>
            {/* Condition Report */}
            <div className={styles.ConditionReportLinkContainer}>
              <div className={styles.ConditionReportLink}>Condition Report</div>
              <div
                className={styles.ConditionReportBtn}
                onClick={() => window.open(condition_reports.doc_url)}
              >
                <IconFont type="iconicon_condition_line" className={styles.Rectangle} />
                <span className={styles.View}>View</span>
              </div>
            </div>

            {/* hr-solid */}
            <div className="hr-solid" style={{ marginTop: 0 }}></div>

            {/* Car Condition Photos */}
            <div className={styles.ConditionReportPhotosContainer}>
              <div className={styles.CarConditionPhotos}>Car Condition Photos</div>
              <div className={styles.ConditionReportPhotos}>
                {condition_reports.images &&
                  condition_reports.images.map((n: any, index: number) => {
                    return (
                      <picture key={index} onClick={this.showSwiper}>
                        <img className={styles.ConditionReportPhotosImg} src={n && n.image} />
                        <IconFont type="iconicon_zoom" className={styles.iconZoomOfPhoto} />
                      </picture>
                    );
                  })}
              </div>
            </div>
          </div>
        </div>

        {operType === OperType.Edit && (
          <div className={styles.ConditionReportBtns}>
            {/* <div
              className={styles.ConditionReportBtnsReject}
              onClick={() => this.rejectConditionReport()}
            >
              Reject
            </div> */}
            <div
              className={styles.ConditionReportBtnsConfirm}
              onClick={() => this.confirmConditionReport()}
            >
              Confirm
            </div>
          </div>
        )}

        {this.state.isShowSwiper && (
          <Swiper
            imageSrc={
              condition_reports.images &&
              condition_reports.images.map((n: any) => {
                return util.addUrlSuffix(n.image, 'x-oss-process=style/web_640');
              })
            }
            onClose={() => {
              this.setState({ isShowSwiper: false });
            }}
          />
        )}
      </div>
    );
  }
}
