import React from 'react';

export function Avatar(props: {
  url: string;
  size?: number;
  style?: React.CSSProperties;
  onClick?: any;
}) {
  const size = props.size || 70;
  return (
    <img
      src={props.url}
      style={{
        width: size,
        height: size,
        borderRadius: '50%',
        ...props.style
      }}
      onClick={() => {
        props.onClick && props.onClick();
      }}
    />
  );
}

export function Flag(props: { country: string; size?: number; style?: React.CSSProperties }) {
  const size = props.size || 21;
  return (
    <img
      src={require(`../../../assets/img/flag/${props.country.slice(-2)}.png`)}
      style={{
        width: size,
        height: (size * 16) / 21,
        ...props.style
      }}
    />
  );
}
export function AvatarWithFlag(props: {
  avatarUrl: string;
  countryUrl: string;
  avatarSize?: number;
  countrySize?: number;
  className?: string;
  style?: React.CSSProperties;
  onClick?: () => void;
}) {
  const { avatarUrl, countryUrl, onClick, avatarSize, countrySize, className, style = {} } = props;
  return (
    <div
      className={className}
      onClick={onClick}
      style={{
        width: avatarSize,
        height: avatarSize,
        cursor: 'pointer',
        position: 'relative',
        ...style
      }}
    >
      <Avatar url={avatarUrl} size={avatarSize} />
      <Flag
        country={countryUrl}
        size={countrySize}
        style={{
          position: 'absolute',
          right: 0,
          bottom: 0
        }}
      />
    </div>
  );
}
