import { ModalStatus, ModalStore, ModalActions, ModalTypes } from '../types';

export const initialState: ModalStore = {
  status: ModalStatus.CLOSED
};

// todo action as any
export default (state = initialState, action: ModalActions): ModalStore => {
  switch (action.type) {
    case ModalTypes.SET_CLOSE: {
      return {
        ...state,
        status: ModalStatus.CLOSED
      };
    }
    case ModalTypes.SET_OPEN: {
      return {
        ...state,
        status: ModalStatus.OPENED
      };
    }
    case ModalTypes.SET_LOADING: {
      return {
        ...state,
        status: ModalStatus.LOADING
      };
    }
    default:
      return { ...initialState };
  }
};
