import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import BasicLayout from '../../components/Layout/BasicLayout';
import Card from '../../components/Card';
import { searchMarketList } from '../../api/buy';
import { getUserInfo } from '../../api/user';
import { getSetting } from '../../api/setting';
import { CardType } from '../../components/Card/CardTypes';
import Pagination from '../../components/Pagination';
import _get from '../../common/get';
import Loading from '../../components/Card/loading';
import FilterBar from '../../components/FilterBar';
import { connect } from 'react-redux';
import { AppStore } from '../../store/reducers/index';
import { SettingInfoStore, UserActions } from '../../store/types';
import { SetUserInfo } from '../../store/actions/user';
import { SetSettingInfo } from '../../store/actions/setting';
import Empty from '../../components/Empty/Empty';
import { getCookie } from '../../common/cookie';
import { path } from '../../routes/routers';
import { bindActionCreators, compose, Dispatch } from 'redux';
import { cancel } from '../../api/http/request';
import styles from '../../components/Card/index.module.scss';

const _cancel = Symbol('cancel search market list');

interface IStateProps {
  setting: SettingInfoStore;
  company_country: string;
}

interface IDispatchProps {
  setUser: (arg: {
    id: string;
    first_name: string;
    last_name: string;
    phone: string;
    username: string;
    company_country: string;
    headshot: string;
    display_name: string;
  }) => UserActions;
}

const mapState = (state: AppStore): IStateProps => ({
  setting: state.setting,
  company_country: state.user.userInfo.company_country
});

const mapDispatch = (dispatch: Dispatch): IDispatchProps =>
  bindActionCreators(
    {
      setUser: ({
        id = '',
        first_name = '',
        last_name = '',
        phone = '',
        username = '',
        company_country = '',
        headshot = '',
        display_name = ''
      }) =>
        SetUserInfo({
          userId: id,
          first_name,
          last_name,
          phone,
          username,
          company_country,
          headshot,
          display_name
        })
    },
    dispatch
  );

type IProps = IStateProps & IDispatchProps & RouteComponentProps;

interface IState {
  page: number;
  pageSize: number;
  loading: boolean;
  count: number;
  next: string;
  previous: string | null;
  results: CardType[];
  server_time: string;
}

const initialState: IState = {
  page: 1,
  pageSize: 15,
  loading: false,
  count: 0,
  next: '',
  previous: '',
  results: [],
  server_time: ''
};

class BuyMarket extends React.Component<IProps, IState> {
  static defaultProps: IProps;
  private filterBar = React.createRef<any>();

  state = initialState;

  isUnMounted = false;

  async componentDidMount() {
    this.loadData();
  }

  onChange = (page: number, pageSize: number) => {
    if (this.state.loading) return;
    this.setState({ page, pageSize }, () => this.loadData());
  };

  onFilterChange = (opt: object) => this.loadData({ ...opt });

  goto = (path: string) => {
    const {
      history: { push }
    } = this.props;
    push(path);
  };

  loadSetting = async () => {
    const { data } = await getSetting();
    const { ALL_CURRENCIES } = data;
    SetSettingInfo(ALL_CURRENCIES);
  };

  loadUserInfo = async () => {
    const res = await getUserInfo();
    const {
      id = '',
      first_name = '',
      last_name = '',
      phone = '',
      username = '',
      company_country = '',
      headshot = '',
      display_name = ''
    } = res.data;
    this.props.setUser({
      id,
      first_name,
      last_name,
      phone,
      username,
      company_country,
      headshot,
      display_name
    });
  };

  resetState = () =>
    new Promise((resolve, reject) => this.setState({ ...initialState }, () => resolve()));

  loadData = async (opt?: object) => {
    try {
      if (!getCookie('Authorization')) {
        this.goto('/login');
      }

      if (opt) {
        await this.resetState();
      }

      const { setting, company_country } = this.props;
      const { getConditions } = this.filterBar.current;
      const { page, pageSize } = this.state;

      cancel && cancel(_cancel);
      this.setState({ loading: true });

      // check user and setting infomation
      if (setting.ALL_CURRENCIES.length === 0 && !company_country) {
        await Promise.all([this.loadSetting(), this.loadUserInfo()]);
      }
      if (setting.ALL_CURRENCIES.length === 0) {
        await this.loadSetting();
      }
      if (!company_country) {
        await this.loadUserInfo();
      }

      const conditions = opt ? { ...opt } : { ...getConditions() };

      const res = await searchMarketList({
        page,
        size: pageSize,
        ...conditions,
        car_type: 2,
        status: 2
      });
      // setState
      if (this.isUnMounted) return;
      !!res.data && this.setState({ loading: false, ...res.data });
    } catch (e) {
      const _self = this;
      if (_get(e, ['response', 'status']) === 401) {
        _self.goto('/login');
      }
      if (_get(e, ['message']) === _cancel) {
        return this.setState({ loading: true });
      }
      this.setState({ loading: false, results: [], count: 0 });
    }
  };

  onCardClick = (id: string) => (e: React.MouseEvent<HTMLDivElement>) =>
    window.open(`${path.BuyCarDetail}/${id}`);

  onCardBottomClick = (arg: any) => (e: React.MouseEvent<HTMLDivElement>) => {
    e.stopPropagation();
    // console.log(arg, '');
  };

  componentWillUnmount() {
    this.isUnMounted = true;
  }

  render() {
    const { loading, results, page, pageSize, count, previous, next } = this.state;
    const {
      setting: { ALL_CURRENCIES },
      company_country
    } = this.props;
    function matchSymbol(currency: string): string {
      const matched = ALL_CURRENCIES.find(v => v.code === currency);
      return matched ? matched.symbol : '$';
    }

    return (
      <BasicLayout>
        <div className={styles.wrappedContainer}>
          <FilterBar
            total={count}
            loading={loading}
            onChange={this.loadData}
            ref={this.filterBar}
          />
          {loading && <Loading />}
          {!loading && results.length === 0 && <Empty emptyType="empty_market" />}
          {!loading && results.length > 0 && (
            <div className={styles.CardContainer} id="CardContainer">
              {results.map((v: CardType) => (
                <Card
                  key={v.id}
                  type="buy_market"
                  id={v.id}
                  is_new_car={v.is_new_car}
                  image={v.image}
                  make_logo={v.make_logo}
                  make_name={v.make_name}
                  view_count={v.view_count}
                  display_name={v.display_name}
                  distance_to_warehouse={v.distance_to_warehouse}
                  distance_unit={v.distance_unit}
                  exterior_color={v.exterior_color}
                  exterior_color_name={v.exterior_color_name}
                  interior_color={v.interior_color}
                  interior_color_name={v.interior_color_name}
                  warehouse={v.warehouse}
                  car_status={v.car_status}
                  user={v.user}
                  quantity={v.quantity}
                  available={v.available}
                  price={v.price}
                  currency={v.currency}
                  msrp={v.msrp}
                  user_company_country={company_country}
                  money_symbol={matchSymbol(v.currency)}
                  onCardClick={this.onCardClick}
                  onCardBottomClick={this.onCardBottomClick}
                />
              ))}
            </div>
          )}
          {results.length > 0 && (previous || next) && (
            <Pagination
              currentPage={page}
              total={count}
              pageSize={pageSize}
              onChange={this.onChange}
              loading={loading}
            />
          )}
        </div>
      </BasicLayout>
    );
  }
}

export default compose(
  withRouter,
  connect(
    mapState,
    mapDispatch
  )
)(BuyMarket);
