import { ChatRole, ChatActionTypes, ChatAction, ChatStore, RoomItem } from '../types';
import LightPrompt from '../../components/LightPrompt';
import _get from '../../common/get';

const initialState: ChatStore = {
  unreadNum: {
    sell: 0,
    buy: 0
  },
  chatList: {
    chatBuyList: [],
    chatSellList: [],
    buyListCurrentPage: 0,
    sellListCurrentPage: 0,
    hasnextBuylistPage: true,
    hasnextSelllistPage: true
  },
  // activeRoomId: ['01c99b3c-7045-4dc9-b1a0-deef1736d70b', '212202b9-e7a1-4a7f-9b54-67b84b989191']
  activeRoomId: []
};

const roomNumber = Math.floor((window.innerWidth - 320 - 40) / (512 + 16));
const maxRooms = roomNumber < 2 ? 2 : roomNumber;
// console.log('maxRooms: ', maxRooms);

export default (state = initialState, action: ChatAction): ChatStore => {
  switch (action.type) {
    case ChatActionTypes.SET_UNREAD_NUM: {
      // console.log('set_num: ', action.num);
      return {
        ...state,
        unreadNum: action.num
      };
    }
    case ChatActionTypes.ADD_ACTIVE_ROOM_ID: {
      const { activeRoomId } = state,
        {
          item: { id, msg_id }
        } = action;
      if (!id) return state;
      const exitItem = activeRoomId.find((d: RoomItem) => d.id === id);
      if (!!exitItem && _get(exitItem, ['status']) === 'min') {
        return {
          ...state,
          activeRoomId: state.activeRoomId.map((v: RoomItem) => {
            if (v.id === id) {
              return { ...v, status: 'common' };
            }
            return v;
          })
        };
      }

      if (!!exitItem && _get(exitItem, ['status']) === 'common') {
        // LightPrompt('');
        return state;
      }

      if (!exitItem && activeRoomId.length >= maxRooms) {
        const nextActiveRoomId = activeRoomId.slice(1).concat({ id, status: 'common', msg_id });
        return {
          ...state,
          activeRoomId: nextActiveRoomId
        };
      }

      return {
        ...state,
        activeRoomId: activeRoomId.concat({ id, status: 'common', msg_id })
      };
    }

    case ChatActionTypes.DELETE_ACTIVE_ROOM_ID: {
      const { activeRoomId } = state;
      return {
        ...state,
        activeRoomId: activeRoomId.filter((d: RoomItem) => d.id !== action.id)
      };
    }

    case ChatActionTypes.UPDATE_ACTIVE_ROOM_ID: {
      const { activeRoomId } = state;
      const {
        item: { id, status }
      } = action;
      const targetIndex = activeRoomId.findIndex((v: RoomItem) => v.id === id);
      if (targetIndex < 0) return state;
      activeRoomId[targetIndex].status = status;
      return {
        ...state,
        activeRoomId: activeRoomId
      };
    }

    case ChatActionTypes.SET_CHAT_BUY_LIST: {
      const { chatList } = state;
      const _chatList = Object.assign({}, chatList);
      const { list, buyListCurrentPage, hasnextBuylistPage } = action;
      if (buyListCurrentPage === chatList.buyListCurrentPage) {
        return state;
      }
      _chatList.chatBuyList = chatList.chatBuyList.concat(list);
      _chatList.buyListCurrentPage = buyListCurrentPage;
      _chatList.hasnextBuylistPage = hasnextBuylistPage;
      return {
        ...state,
        chatList: _chatList
      };
    }

    case ChatActionTypes.SET_CHAT_SELL_LIST: {
      const { chatList } = state;
      const _chatList = Object.assign({}, chatList);
      const { list, sellListCurrentPage, hasnextSelllistPage } = action;
      if (sellListCurrentPage === chatList.sellListCurrentPage) {
        return state;
      }
      _chatList.chatSellList = chatList.chatSellList.concat(list);
      _chatList.sellListCurrentPage = sellListCurrentPage;
      _chatList.hasnextSelllistPage = hasnextSelllistPage;
      return {
        ...state,
        chatList: _chatList
      };
    }

    case ChatActionTypes.UPDATE_CHATROOM_UNRADE: {
      const { chatList } = state;
      const _chatList = Object.assign({}, chatList);
      const { chatRoomId, chatrooms, activeRole } = action;
      activeRole === ChatRole.Buyer
        ? (_chatList.chatBuyList = _chatList.chatBuyList.map(i => {
            i.chatrooms.map(j => {
              // j.unread_count == i.unread_count
              j.chatroom_id === chatRoomId ? (j.unread_count = 0) : '';
              return j;
            });
            return i;
          }))
        : (_chatList.chatSellList = _chatList.chatSellList.map(i => {
            i.chatrooms.map(j => {
              j.chatroom_id === chatRoomId ? (j.unread_count = 0) : '';
              return j;
            });
            return i;
          }));
      return {
        ...state,
        chatList: _chatList
      };
    }

    default:
      return state;
  }
};
