import _get from '../../../../common/get';
import { initialState } from '../../../../store/reducers/user';
import { getUserInfo } from '../../../../api/user';

const initialUserInfo: any = { ...initialState.userInfo };

export const getUserAddress = (userInfo: any = {}) => [
  _get(userInfo, ['company_address']),
  _get(userInfo, ['company_address2']),
  _get(userInfo, ['company_address3']),
  _get(userInfo, ['city']),
  _get(userInfo, ['province_state']),
  _get(userInfo, ['company_country_full_name']),
  _get(userInfo, ['postal_or_zip_code'])
];

export const fetchUserInfo = async () => {
  let response: any = await getUserInfo();
  let userInfo: any = {};
  Object.keys(initialUserInfo).map(
    key => (userInfo[key] = response.data[key] || initialUserInfo[key])
  );
  return userInfo;
};
