import React from 'react';
import IconFont from '../ui/TradexIcon';
import './index.scss';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';
import styles from '../Card/index.module.scss';
import { Tooltip } from 'antd';

type Props = {
  imageSrc: Array<string>;
  damageImageSrc: Array<{
    src: string;
    describe: string;
  }>;
  exteriorColor: string;
  interiorColor: string;
};

type Sates = {
  showMask: boolean;
  currentType: string;
  maskCurrent: number;
};

function NextArrow(props: { onClick: any }) {
  const { onClick } = props;
  return (
    <div className="slick-next" onClick={onClick}>
      <IconFont type="iconicon_rightarrow" className="icon" />
    </div>
  );
}

function PrevArrow(props: { onClick: any }) {
  const { onClick } = props;
  return (
    <div className="slick-prev" onClick={onClick}>
      <IconFont type="iconicon_leftarrow" className="icon" />
    </div>
  );
}

function MaskNextArrow(props: { onClick: any }) {
  const { onClick } = props;
  return (
    <div className="slick-next" onClick={onClick}>
      {/*<IconFont type="iconicon_righrtarrow_big_pc" className="icon" />*/}
    </div>
  );
}

function MaskPrevArrow(props: { onClick: any }) {
  const { onClick } = props;
  return (
    <div className="slick-prev" onClick={onClick}>
      {/*<IconFont type="iconicon_lefttarrow_big_pc" className="icon" />*/}
    </div>
  );
}

class Carousel extends React.Component<Props, Sates> {
  private slider: any;
  private maskSlider1: any;
  constructor(props: Readonly<Props>) {
    super(props);
    this.state = {
      showMask: false,
      currentType: 'carImage',
      maskCurrent: 0
    };
  }

  changeTab(tabName: string) {
    this.setState({ maskCurrent: 0 });
    setTimeout(() => {
      this.maskSlider1.slickGoTo(0, false);
    }, 250);
    this.setState({ currentType: tabName });
  }

  next() {
    this.slider.slickNext();
  }
  previous() {
    this.slider.slickPrev();
  }

  maskNext() {
    this.maskSlider1.slickNext();
  }
  maskPrevious() {
    this.maskSlider1.slickPrev();
  }
  maskBeforeChange(current: number, next: number) {
    this.setState({ maskCurrent: next });
  }

  handleGoTo(index: any) {
    this.setState({ maskCurrent: index });
    this.maskSlider1.slickGoTo(index, false);
  }

  thumbnailPrev() {
    if (this.state.maskCurrent >= 1) {
      this.setState({ maskCurrent: this.state.maskCurrent - 1 });
      this.maskSlider1.slickGoTo(this.state.maskCurrent - 1, false);
    } else {
      let originImageList =
        this.state.currentType === 'carImage' ? this.props.imageSrc : this.props.damageImageSrc;
      this.setState({ maskCurrent: originImageList.length - 1 });
      this.maskSlider1.slickGoTo(originImageList.length - 1, false);
    }
  }

  thumbnailNext() {
    let originImageList =
      this.state.currentType === 'carImage' ? this.props.imageSrc : this.props.damageImageSrc;
    if (this.state.maskCurrent < originImageList.length) {
      let result = this.state.maskCurrent + 2;
      if (result === originImageList.length + 1) {
        this.setState({ maskCurrent: 0 });
        this.maskSlider1.slickGoTo(0, false);
      } else {
        this.setState({ maskCurrent: this.state.maskCurrent + 1 });
        this.maskSlider1.slickGoTo(this.state.maskCurrent + 1, false);
      }
    }
  }

  showDetail(index: any) {
    if (index) {
      this.setState({
        showMask: true,
        maskCurrent: index,
        currentType: 'carImage'
      });
      setTimeout(() => {
        this.maskSlider1.slickGoTo(index, false);
      }, 0);
    } else {
      this.setState({
        showMask: true,
        maskCurrent: 0,
        currentType: 'carImage'
      });
    }
  }

  static defaultProps = {
    imageSrc: [
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/1.jpg',
      'http://img.diudou.com/file/dongman/news/2017-05-12/7c8a6ce85838085c87bc5f4fe01922bf.jpg',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/axis.png',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/4.jpg',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/5.jpg',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/6.jpg'
    ],
    damageImageSrc: [
      {
        src: 'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/4.jpg',
        describe: 'This is bad1!'
      },
      {
        src: 'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/5.jpg',
        describe: 'This is bad2!'
      },
      {
        src: 'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/6.jpg',
        describe: 'This is bad3!'
      }
    ],
    exteriorColor: '#198cff',
    interiorColor: '#001a33'
  };

  render() {
    const settings = {
      dots: true,
      infinite: false,
      speed: 500,
      slidesToScroll: 1,
      prevArrow: <PrevArrow onClick={this.previous} />,
      nextArrow: <NextArrow onClick={this.next} />,
      customPaging: (index: number) => {
        if (index !== 4) {
          return (
            <div key={index}>
              <img src={this.props.imageSrc[index]} />
            </div>
          );
        } else {
          return (
            <div key={index}>
              <div className="thumbnailMore" onClick={() => this.setState({ showMask: true })}>
                See More
              </div>
              <img src={this.props.imageSrc[index]} className="image" />
            </div>
          );
        }
      }
    };
    let imageSlideList = this.props.imageSrc
      .slice(0, 5)
      .map((src, index) => (
        <img src={src} key={index} onClick={this.showDetail.bind(this, index)} />
      ));
    let carImageList = this.props.imageSrc.map((src, index) => <img src={src} key={index} />);
    let carThumbnailImageList = this.props.imageSrc.map((src, index) => (
      <li key={index} className={this.state.maskCurrent === index ? 'active' : ''}>
        <img src={src} onClick={this.handleGoTo.bind(this, index)} />
      </li>
    ));

    let damageImageList = this.props.damageImageSrc.map((item, index) => (
      <img src={item.src} key={index} />
    ));
    let damageThumbnailImageList = this.props.damageImageSrc.map((item, index) => (
      <li key={index} className={this.state.maskCurrent === index ? 'active' : ''}>
        <img src={item.src} onClick={this.handleGoTo.bind(this, index)} />
      </li>
    ));

    let maskImageList = this.state.currentType === 'carImage' ? carImageList : damageImageList;
    let maskThumbnailImageList =
      this.state.currentType === 'carImage' ? carThumbnailImageList : damageThumbnailImageList;
    let mask;
    const maskSettings = {
      infinite: true,
      speed: 200,
      slidesToShow: 1,
      nextArrow: <MaskNextArrow onClick={this.maskNext} />,
      prevArrow: <MaskPrevArrow onClick={this.maskPrevious} />,
      beforeChange: this.maskBeforeChange.bind(this)
    };
    let isLg = document.body.clientWidth >= 1920;

    let translatedX =
      (this.state.maskCurrent >= 5 ? this.state.maskCurrent - 4 : 0) * (isLg ? -162 : -121);

    if (this.state.showMask) {
      document.documentElement.style.overflowY = 'hidden';
      mask = (
        <div className="carousel-mask">
          <IconFont
            type="iconicon_cancel"
            className="close-icon"
            onClick={() => this.setState({ showMask: false })}
          />
          <div className={'content ' + (isLg ? 'lg-content' : 'md-content')}>
            <div className="tab">
              <ul>
                <li
                  className={this.state.currentType === 'carImage' ? 'active' : ''}
                  onClick={this.changeTab.bind(this, 'carImage')}
                >
                  Car Image
                </li>
                {this.props.damageImageSrc.length > 0 && (
                  <li
                    className={this.state.currentType === 'damage' ? 'active' : ''}
                    onClick={this.changeTab.bind(this, 'damage')}
                  >
                    Damage
                  </li>
                )}
              </ul>
            </div>
            <div className="mask-slider">
              <div className="mask-slider-content">
                <Slider {...maskSettings} ref={c => (this.maskSlider1 = c)}>
                  {maskImageList}
                </Slider>
                <div className="currentTag">
                  {this.state.maskCurrent + 1}/{maskImageList.length}
                </div>
                {this.state.currentType === 'damage' && (
                  <div className="describe">
                    {this.props.damageImageSrc[this.state.maskCurrent].describe}
                  </div>
                )}
              </div>
            </div>
            <div className="mask-thumbnail-wrap">
              <div className="dot-prev" onClick={this.thumbnailPrev.bind(this)}>
                <IconFont type="iconicon_leftarrow" className="icon" />
              </div>
              <div className="slick-dots">
                <ul
                  style={{
                    width: maskThumbnailImageList.length * (isLg ? -162 : -121) + 'px',
                    transform: `translate3d(${translatedX}px, 0px, 0px)`
                  }}
                >
                  {maskThumbnailImageList}
                </ul>
              </div>
              <div className="dot-next" onClick={this.thumbnailNext.bind(this)}>
                <IconFont type="iconicon_rightarrow" className="icon" />
              </div>
            </div>
          </div>
        </div>
      );
    } else {
      document.documentElement.style.overflowY = 'scroll';
    }

    let color = (
      <div className="colorWrap">
        <Tooltip
          placement="bottomLeft"
          // defaultVisible={true}
          arrowPointAtCenter={true}
          autoAdjustOverflow={false}
          overlayClassName={styles.Tooltip}
          // getPopupContainer={() => (document.getElementById('CardContainer') || document.body)}
          // getPopupContainer={triggerNode => triggerNode.parentElement || document.body}
          // trigger="click"
          title="Exterior color"
        >
          <div className="color" style={{ backgroundColor: `#${this.props.exteriorColor}` }} />
        </Tooltip>
        <div className="divider" />
        <Tooltip
          placement="bottomLeft"
          // defaultVisible={true}
          arrowPointAtCenter={true}
          autoAdjustOverflow={false}
          overlayClassName={styles.Tooltip}
          // getPopupContainer={() => (document.getElementById('CardContainer') || document.body)}
          // getPopupContainer={triggerNode => triggerNode.parentElement || document.body}
          // trigger="click"
          title="Interior color"
        >
          <div className="color" style={{ backgroundColor: `#${this.props.interiorColor}` }} />
        </Tooltip>
      </div>
    );
    let img = null;
    if (this.props.imageSrc.length === 1) {
      img = <img src={this.props.imageSrc[0]} className={'slick-img'} />;
    }

    return (
      <div className="carouselWrap">
        <div className="slick-wrap">
          <Slider ref={c => (this.slider = c)} {...settings}>
            {imageSlideList}
          </Slider>
          {img}
        </div>
        {color}
        {mask}
      </div>
    );
  }
}

export default Carousel;
