import { Action } from 'redux';
import { ThunkAction } from 'redux-thunk';
import { AppStore } from '../reducers';
import {
  ChatMode,
  UnionChatAction,
  UnionChatActionTypes,
  RouteItemParams,
  ChatActionTypes,
  ChatRole,
  RoomItem
} from '../types';

let loading = false;

export const UnionChatRouteGoTo = (
  mode: ChatMode,
  params: RouteItemParams
): ThunkAction<void, AppStore, null, Action<string>> => (
  dispatch: any,
  getState: () => AppStore
) => {
  dispatch({ type: UnionChatActionTypes.UnionChatRouteGoTo, mode, params });

  // common mode
  if (mode === ChatMode.WINDOW_MODE) {
    const { roomId, msg_id, forceUpdate } = params;

    if (forceUpdate) {
      const {
        chat: { activeRoomId }
      } = getState();
      const has = activeRoomId.find((v: RoomItem) => v.id === roomId);
      if (has) {
        if (loading) return;
        loading = true;
        dispatch({
          type: ChatActionTypes.DELETE_ACTIVE_ROOM_ID,
          id: roomId
        });

        return setTimeout(() => {
          dispatch({
            type: ChatActionTypes.ADD_ACTIVE_ROOM_ID,
            item: {
              id: roomId,
              msg_id
            }
          });

          loading = false;
        }, 0);
      }
    }

    dispatch({
      type: ChatActionTypes.ADD_ACTIVE_ROOM_ID,
      item: {
        id: roomId,
        msg_id
      }
    });
  }
};

export const UnionChatRouteGoAway = (mode: ChatMode): UnionChatAction => {
  return {
    type: UnionChatActionTypes.UnionChatRouteGoAway,
    mode
  };
};
