import { get, put } from './http';
import request from './http/request';
import { RootAPI } from '../api/http/config';
import { AxiosPromise } from 'axios';

// 
export function getNoticeList(params?: object): AxiosPromise<any> {
  return get('notification-center/', params);
}

// 
export function changeNoticeItemState(
  id: string,
  opt: {
    ignore?: boolean;
    is_read?: boolean;
  }
): AxiosPromise<any> {
  return put(`notification-center/${id}/`, opt);
}
