import request from './request';
import { RootAPI } from './config';
import { AxiosPromise } from 'axios';

export function get(
  url: string,
  params: any = {},
  useRootAPI = true,
  cancelControl?: any
): AxiosPromise<any> {
  return request({
    url: useRootAPI ? RootAPI(url) : url,
    method: 'get',
    headers: { 'content-type': 'application/json' },
    params: params,
    cancelToken: cancelControl
  });
}

export function post(url: string, params: any, useRootAPI = true): AxiosPromise<any> {
  return request({
    url: useRootAPI ? RootAPI(url) : url,
    method: 'post',
    headers: { 'content-type': 'application/json' },
    data: params
  });
}

export function put(url: string, params?: any, useRootAPI = true): AxiosPromise<any> {
  return request({
    url: useRootAPI ? RootAPI(url) : url,
    method: 'put',
    headers: { 'content-type': 'application/json' },
    data: params
  });
}

export function _delete(url: string, params: any, useRootAPI = true): AxiosPromise<any> {
  return request({
    url: useRootAPI ? RootAPI(url) : url,
    method: 'delete',
    headers: { 'content-type': 'application/json' },
    data: params
  });
}
