import React, { ReactElement } from 'react';
import { RouteComponentProps } from 'react-router-dom';
import { connect } from 'react-redux';
import util from '../../common/util';
import styles from './index.module.scss';
import '../../assets/style/common.scss';
import { CarPrice } from '../../types';
import { AppStore } from '../../store/reducers';
import TradexIcon from '../../components/ui/TradexIcon';
import Mileage from '../../components/ui/Mileage';
import Mask from '../../components/feedback/Mask';
import { AvatarWithFlag } from '../../components/ui/Avatar';
import MakeOffer from '../../components/MakeOffer';
import Cascader from '../../components/Cascader';
import Carousel from '../../components/Carousel';
import { Tag, FillTag } from '../../components/ui/Tag';
import { Modal, Button, Popover } from 'antd';
import { FormattedNumber, FormattedDate } from 'react-intl';
import { GetGarageDetail } from '../../api/garage';
import { CreateOrder } from '../../api/order';
import { EditUser } from '../../api/user';
import { WrapperSplit, InfoItem, TabItemHead, DisclosureItem, TabItemSubHead } from './components';
import Skel from './components/Skel';
import { UnionChatRouteGoTo } from '../../store/actions/route';
import { ChatMode, ChatRole, UserInfo } from '../../store/types';
import MapPop from '../../components/MapPop';
import _get from '../../common/get';
import { upDateRoomUnread } from '../../store/actions/chat';

interface DisclosureItem {
  key: string;
  isNormal: boolean;
  value: string;
}

interface KvStrObj {
  key: string;
  value: string;
}

interface DamageImg {
  describe: string;
  src: string;
}

type PageName = 'report' | 'sticker';

interface Props extends RouteComponentProps {
  userInfo: AppStore['user']['userInfo'];
  chatList: AppStore['chat']['chatList'];
  UnionChatRouteGoTo: any;
}
interface State {
  loading: boolean;
  garageId: string;
  detail: any;
  isSelf: boolean;
  disclosure: DisclosureItem[];
  statement: string[];
  specification: KvStrObj[];
  sellerDisclosure: any[];
  mask: {
    show: boolean;
    url: string;
    title: string;
  };
  isCollapse: boolean;
  showMakeOfferStatus: boolean;
  makeOfferLeft: number;
  makeOfferType: number;
  damageImages: DamageImg[];
  previewMapShow: boolean;
  menuTitles: string[];
}

const Specification = (global: any, specification: any[], window_sticker: string) => {
  const elList = specification.map((d: any) => (
    <InfoItem itemKey={d.key} itemValue={d.value} key={d.key} />
  ));

  const elSticker =
    (window_sticker && (
      <InfoItem itemKey="Bill of sale / window sticker / worksheet" key={window_sticker}>
        <FillTag type="blue" text="View" onClick={global.openTab.bind(global, 'sticker')} />
      </InfoItem>
    )) ||
    null;

  elList.splice(1, 0, elSticker as any);
  return elList;
};

// todo , statechat
const mapStateToProps = (state: AppStore) => ({
  userInfo: state.user.userInfo,
  chatList: state.chat.chatList
});

const mapDispatchToProps = {
  UnionChatRouteGoTo
};
class CarDetail extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      loading: true,
      garageId: '',
      isSelf: false,
      disclosure: [],
      statement: [],
      specification: [],
      sellerDisclosure: [],
      damageImages: [],
      detail: null,
      mask: {
        show: false,
        url: '',
        title: ''
      },
      isCollapse: false,
      showMakeOfferStatus: false,
      makeOfferLeft: 0,
      makeOfferType: 0,
      previewMapShow: false,
      menuTitles: ['Payment & Shipping', 'Condition', 'Specification', 'Options']
    };
  }

  makeOfferButton: any = null;

  componentDidMount() {
    const { id } = this.props.match.params as any;
    const { company_country } = this.props.userInfo;
    this.getGarageDetail(id);
    this.setState({
      garageId: id
    });
  }

  // offercar detailmake offer
  openMakeOffer = (button: any) => {
    const { operation } = this.props.match.params as any;
    if (operation === 'open_make_offer' && button) {
      this.showMakeOffe(styles.make_offer);
    }
  };

  getGarageDetail = async (id: string) => {
    const result: any = await GetGarageDetail(id).then(d => d);
    const data = result.data;
    console.log(data);
    const disclosure: DisclosureItem[] = [];
    const statement: string[] = [];
    const specification: KvStrObj[] = [];
    const sellerDisclosure: any[] = [];
    let damageImages: DamageImg[] = [];

    const specKeys = [
      ['vin', 'VIN', 0],
      ['car_id', 'Listing ID', 0],
      ['model_year', 'Model year', 0],
      ['specification_location', 'Country specification', 1],
      ['model_name', 'Model', 1],
      ['make_name', 'Make', 1],
      ['body', 'Body', 1],
      ['trim_name', 'Trim', 1],
      ['engine_type', 'Engine', 1],
      ['fuel_type', 'Fuel type', 1],
      ['door_number', 'Door number', 0],
      ['transmission', 'Transmission', 1],
      ['displacement', 'Engine size', 0],
      ['l_or_r', 'Steering position', 1]
    ];
    const sellerDisKeys = [
      ['paint_items', 'Paint', 1, 'icon_paint_area'],
      ['interior_damage_items', 'Interior', 1, 'neishi-mianicon_interior_area'],
      ['exterior_damage_items', 'Exterior', 1, 'waishi-mianicon_exterior_area'],
      ['tire_condition', 'Tire', 0, 'icon_tire_area'],
      ['key', 'Key', 0, 'icon_key_area'],
      ['odor', 'Odor', 0, 'icon_odor_area'],
      ['manuals', 'Manuals', 0, 'icon_manual_area']
    ];
    const damageImgKey = ['Paint', 'Interior', 'Exterior', 'Tire', 'Odor', 'Manuals'];

    specKeys.forEach((d: any) => {
      let value = d[2] === 0 ? data[d[0]] : data[d[0]]['display_value'];
      if (d[0] === 'displacement') {
        value += ' L';
      }
      specification.push({
        key: d[1],
        value: value
      });
    });

    if (!data.is_new_car) {
      sellerDisKeys.forEach((d: any) => {
        const item = data.seller_disclosure.find((s: any) => s.key === d[0]);
        if (!item || !item.value.length || item.value.some((d: any) => typeof d !== 'object')) {
          sellerDisclosure.push({
            key: d[1],
            icon: d[3]
          });
          return;
        }
        const subList = item.value.map((f: any) => {
          const { description, severity = '', condition = '', image_url } = f;
          const key = d[2] === 0 ? description : `${description}: ${condition} ${severity}`;
          return {
            key,
            value: `${condition} ${severity}`,
            img: image_url
          };
        });

        sellerDisclosure.push({
          key: d[1],
          type: d[2],
          icon: d[3],
          value: subList
        });
      });
    }

    sellerDisclosure.forEach(d => {
      if (damageImgKey.includes(d.key) && d.value instanceof Array) {
        damageImages = damageImages.concat(
          d.value
            .filter((f: any) => f.img)
            .map((m: any) => ({
              describe: m.key,
              src: m.img
            }))
        );
      }
    });

    data.condition.forEach((d: any) => {
      if (d.key === 'Disclosure') {
        d = d.value[0];
        d.values &&
          disclosure.push({
            key: d.section_key,
            value: d.values.map((v: any) => v.display_value).join('/'),
            isNormal: d.values.every((v: any) => v.type === 'none' || v.key === 'Normal')
          });
      }
      if (d.key === 'Seller statement') {
        const val = d.value[0].description;
        val &&
          JSON.parse(val) &&
          JSON.parse(val).forEach((d: any) => {
            statement.push(d.description);
          });
      }
    });

    // hard coding
    const soldAsIsITem = disclosure.find(d => d.key === 'SOLD AS-IS');
    if (soldAsIsITem) {
      soldAsIsITem.isNormal = soldAsIsITem.value === 'Not Applicable';
    }

    if (data.options && data.is_new_car) {
      const optExtr = [];
      if (data.base_msrp) {
        optExtr.push(['Base MSRP', data.base_msrp]);
      }
      if (data.subtotal_msrp) {
        optExtr.push(['MSRP Subtotal', data.subtotal_msrp]);
      }
      if (data.msrp) {
        optExtr.push(['Total MSRP', data.msrp]);
      }
      optExtr.map(d => {
        data.options.push({
          key: d[0],
          value: [{ description: d[1] }]
        });
      });
    }

    const _menuTitles = this.state.menuTitles.slice();
    if (data.is_new_car) {
      _menuTitles[1] = 'Details';
    }

    this.setState(
      {
        detail: data,
        disclosure,
        statement,
        specification,
        sellerDisclosure,
        damageImages,
        isSelf: data.user.id === this.props.userInfo.userId,
        loading: false,
        menuTitles: _menuTitles
      },
      () => this.openMakeOffer(this.makeOfferButton)
    );
  };

  goSellerDetail = () => {
    const {
      user: { id }
    } = this.state.detail;
    this.showComingSoon();
  };

  callChat = (e: React.MouseEvent<HTMLDivElement>) => {
    let { isSelf, garageId, detail } = this.state;
    // garageId = '04ea559f-e190-43e6-a8a1-c11a8061d28b';

    e.stopPropagation();
    if (isSelf) {
      this.props.UnionChatRouteGoTo(ChatMode.LIST_MODE, {
        categoryId: garageId,
        roomId: '',
        activeRole: ChatRole.Seller,
        timestamp: Date.now()
      });
    } else {
      CreateOrder(garageId).then(data => {
        if (data && data.data && data.data.chatroom_id) {
          this.props.UnionChatRouteGoTo(ChatMode.WINDOW_MODE, {
            categoryId: detail.user.id,
            roomId: data.data.chatroom_id,
            activeRole: ChatRole.Buyer
          });
        }
      });
    }
  };

  removeGarage = () => {
    this.showComingSoon();
  };

  showComingSoon = () => {
    Modal.info({
      title: 'coming soon...'
    });
  };

  openTab = (page: PageName) => {
    const { detail, mask } = this.state;
    const { seller_disclosure_link, window_sticker, cr_image } = detail;

    if (page === 'sticker') {
      this.showMask({
        title: 'Bill of sale / window sticker / worksheet',
        url: window_sticker
      });
    } else if (page === 'report') {
      if (seller_disclosure_link) {
        window.open(seller_disclosure_link, '_blank');
      } else if (cr_image) {
        this.showMask({
          title: 'Condition Report',
          url: cr_image
        });
      }
    }
  };

  showMask = (config: { url: string; title?: string }) => {
    this.setState({
      mask: {
        show: true,
        title: config.title || '',
        url: config.url
      }
    });
  };

  hideMask = (url: string) => {
    this.setState({
      mask: {
        show: false,
        title: '',
        url: ''
      }
    });
  };

  toggleCollapse = () => {
    const status = this.state.isCollapse;
    this.setState({
      isCollapse: !status
    });
  };

  updateShippingFee = (data: any) => {
    const { detail } = this.state,
      newDetail = Object.assign({}, detail, { dest_port: data });

    this.setState(
      {
        detail: newDetail
      },
      this.updateDestPort.bind(null, data.id)
    );
  };

  showMakeOffe = (className: string) => {
    const el: any = document.querySelector(`.${className}`);
    const width = (getComputedStyle(el).width as any).replace('px', '');
    if (!el) return;
    const left = el.offsetLeft + parseFloat(width) / 2 - (window.innerWidth - 1200) / 2;
    const makeOfferType =
      className === styles.buy_now ? 2 : className === styles.make_offer ? 1 : 0;
    // MakeOffer, MakeOffer
    this.setState({ ...this.state, showMakeOfferStatus: false }, () => {
      this.setState({
        showMakeOfferStatus: true,
        makeOfferLeft: left,
        makeOfferType
      });
    });
  };

  hideMakeOffer = (destPort: any) => {
    this.setState({
      showMakeOfferStatus: false
    });
    if (destPort) {
      const newDetail = Object.assign({}, this.state.detail, {
        dest_port: destPort
      });
      this.setState({
        detail: newDetail
      });
    }
  };

  hasUnread = (
    chatList: AppStore['chat']['chatList'],
    isSelf: boolean,
    sellerUserId: string,
    garageId: string
  ) => {
    if (isSelf) {
      const carItem = chatList.chatSellList.find(d => d.car_info.id === garageId);
      return (
        carItem && carItem.chatrooms.map(d => d.unread_count).reduce((pre, next) => pre + next) > 0
      );
    }

    const userItem = chatList.chatBuyList.find(d => d.user.id === sellerUserId);
    const roomItem = userItem && userItem.chatrooms.find(d => d.car_info.id === garageId);
    return roomItem && roomItem.unread_count > 0;
  };

  handlePreviewInMap = () =>
    this.setState((prev: State) => ({ previewMapShow: !prev.previewMapShow }));

  updateDestPort = (id: string) => {
    EditUser({
      dest_port_id: id
    });
  };
  render(): ReactElement {
    if (this.state.loading) {
      return <Skel />;
    }

    let {
      loading,
      garageId,
      detail,
      disclosure,
      statement,
      sellerDisclosure,
      mask,
      isCollapse,
      showMakeOfferStatus,
      makeOfferLeft,
      makeOfferType,
      damageImages,
      specification,
      previewMapShow
    } = this.state;

    let {
      display_name,
      price,
      currency,
      msrp,
      car_status,
      available,
      distance_to_warehouse,
      view_count,
      view_on_fire,
      vehicle_location,
      user,
      status,

      is_support_TT,
      is_support_LC,
      warehouse,
      warehouse: { latitude, longitude, name, location },
      days_to_warehouse,

      vehicle_history,
      cr_type,
      cr_grading,
      options,
      dest_port,
      images,
      exterior_color,
      interior_color,
      window_sticker,
      is_new_car
    } = detail;

    let isSelf = user.id === this.props.userInfo.userId;

    images.forEach((n: any) => {
      n.image = util.addUrlSuffix(n.image, 'x-oss-process=style/web_640');
    });

    damageImages = damageImages.map((item: { src: string; describe: string }) =>
      Object.assign({}, item, { src: util.addUrlSuffix(item.src, 'x-oss-process=style/web_640') })
    );

    const isCN = this.props.userInfo.company_country === 'CN';
    const priceType =
      price !== '-1' ? CarPrice.Price : msrp !== '-1' ? CarPrice.Msrp : CarPrice.Negotiable;

    const hasUnreadSign = this.hasUnread(this.props.chatList, isSelf, detail.user.id, garageId);
    const isSold = status === 4;

    const supportPaymentValue = () => {
      // todo 
      return 'EFT';

      const tem = [];
      is_support_TT && tem.push('TT');
      is_support_LC && tem.push('LC');
      return tem.join(' / ');
    };

    const earliesetShippingDate = () => {
      const currDate = new Date(),
        shippingDate = currDate.getTime() + days_to_warehouse * 24 * 3600 * 1000,
        earlistDate = new Date(shippingDate);
      return `${earlistDate.getFullYear()}-${earlistDate.getMonth() + 1}-${earlistDate.getDate()}`;
    };

    return (
      <div className={styles['page-car-detail']}>
        {!loading && (
          <div className={styles.container}>
            {mask.show && (
              <Mask onClose={this.hideMask.bind(this, '')} title={mask.title}>
                <img
                  style={{ objectFit: 'cover', width: '100%', height: '100%' }}
                  src={mask.url}
                  alt=""
                />
              </Mask>
            )}
            <div className={styles.head}>
              <div className={styles.swiper}>
                <Carousel
                  imageSrc={images.map((d: any) => d.image)}
                  damageImageSrc={damageImages}
                  exteriorColor={exterior_color}
                  interiorColor={interior_color}
                />
              </div>
              <div className={styles['head-info']}>
                <span className={styles['car-name']}>{display_name}</span>
                <div className={styles['price-info']}>
                  <div className={styles['price-item']}>
                    <span className={styles.price}>
                      {priceType === CarPrice.Price || priceType === CarPrice.Msrp ? (
                        <FormattedNumber value={priceType === CarPrice.Price ? price : msrp}>
                          {(val: any) => (
                            <>
                              ${val}
                              {` ${currency}`}
                            </>
                          )}
                        </FormattedNumber>
                      ) : (
                        'NEGOTIABLE'
                      )}
                    </span>
                    {priceType !== CarPrice.Negotiable && (
                      <FillTag
                        type={priceType === CarPrice.Price ? 'buy' : 'msrp'}
                        text={priceType === CarPrice.Price ? 'BUY NOW' : 'MSRP'}
                      />
                    )}
                  </div>
                  <div className={styles.tags}>
                    <Tag
                      size="big"
                      type={car_status === 1 ? 'instock' : 'incoming'}
                      text={car_status === 1 ? 'IN STOCK' : 'INCOMING'}
                    />
                    <Tag size="big" type="units" text={util.pluralize(available, 'UNIT', true)} />
                  </div>
                  <Mileage iconSize={20} textSize={18} space={8} value={distance_to_warehouse} />
                  <div className="hr-solid" style={{ marginTop: 12, marginBottom: 16 }}></div>
                  <div className={styles.location}>
                    <img
                      src={require(`../../assets/img/flag/${vehicle_location.country.key}.png`)}
                      alt=""
                    />
                    <span>{`${vehicle_location.country.display_value}, ${vehicle_location.name.display_value}`}</span>
                    {view_on_fire && (
                      <TradexIcon
                        type="iconicon_fire_area"
                        size={16}
                        color="#ff9910"
                        style={{ marginTop: -1 }}
                      />
                    )}
                    <span className={styles['view-num']}>{util.pluralize(view_count, 'view')}</span>
                  </div>
                </div>

                <div className={styles.seller}>
                  <AvatarWithFlag
                    onClick={this.goSellerDetail}
                    avatarUrl={user.headshot}
                    countryUrl={vehicle_location.country.key}
                  />
                  <div className={styles['seller-info']}>
                    <span className={styles.name} onClick={this.goSellerDetail}>
                      {user.display_name}
                    </span>
                    <div className={styles.others}>
                      <div>
                        <span>
                          <i>Joined:</i>
                          <FormattedDate
                            value={new Date(Number(user.created_at))}
                            year="numeric"
                            month="long"
                            day="2-digit"
                          />
                        </span>
                        <span>
                          <i>Active:</i> {user.last_time_online}
                        </span>
                      </div>
                      <div>
                        <span>
                          <i>Total traded cars:</i>
                          {user.trade_history.cars_bought + user.trade_history.cars_sold}
                        </span>
                        <span>
                          <i>Local time:</i>
                          {new Date(user.local_time).toLocaleString('en-US', {
                            hour: 'numeric',
                            minute: 'numeric',
                            hour12: true
                          })}
                        </span>
                      </div>
                    </div>
                  </div>
                  {!isSold && (
                    <div
                      className={`${styles.chat} ${hasUnreadSign ? styles.unread : ''}`}
                      onClick={this.callChat}
                    >
                      <TradexIcon type="iconicon_comment_area" size={28} color="#ffffff" />
                    </div>
                  )}
                </div>
                <div className={styles.btns}>
                  {isSold ? (
                    <Button className={`${styles.btn} ${styles.gray}`}>Sold</Button>
                  ) : isSelf ? (
                    <>
                      <Button
                        className={`${styles.btn} ${styles.blue}`}
                        onClick={this.removeGarage}
                      >
                        Move to garage
                      </Button>
                      <Button
                        className={`${styles.btn} ${styles.black}`}
                        onClick={this.removeGarage}
                      >
                        Remove
                      </Button>
                    </>
                  ) : (
                    <>
                      {priceType === CarPrice.Price && (
                        <Button
                          className={`${styles.btn} ${styles.blue} ${styles.buy_now}`}
                          onClick={this.showMakeOffe.bind(null, styles.buy_now)}
                          // ref={(button: any) => this.autoOpen(button)}
                        >
                          Buy Now
                        </Button>
                      )}
                      <Button
                        ref={(button: any) => (this.makeOfferButton = button)}
                        className={`${styles.btn} ${styles.black} ${styles.make_offer}`}
                        onClick={this.showMakeOffe.bind(null, styles.make_offer)}
                      >
                        Make Offer
                      </Button>
                    </>
                  )}
                </div>
              </div>
            </div>
            {showMakeOfferStatus && (
              <MakeOffer
                isCN={isCN}
                transactionMethod={makeOfferType}
                style={{ marginTop: 10 }}
                detail={this.state.detail}
                position={makeOfferLeft}
                onClose={this.hideMakeOffer}
                garageId={garageId}
              />
            )}
            <div className={styles['switch-tabs']}>
              <div className={styles.tabs}>
                <a href="#payment" className={styles['active']}>
                  {this.state.menuTitles[0]}
                </a>
                <a href="#condition">{this.state.menuTitles[1]}</a>
                <a href="#specification">{this.state.menuTitles[2]}</a>
                <a href="#options">{this.state.menuTitles[3]}</a>
              </div>
              <TabItemHead text={this.state.menuTitles[0]} id="Payment" iconType="icon_port_line" />
              <div className={styles['item-content']}>
                <InfoItem itemKey="Accepted payment method" itemValue={supportPaymentValue()} />
                <InfoItem itemKey="Warehouse">
                  <div style={{ marginRight: -120 }}>
                    <span className={styles.bold} style={{ marginRight: 12 }}>
                      {warehouse.name.display_value}
                    </span>
                    {/* <Popover content="Comming Soon"> */}
                    <span style={{ cursor: 'pointer' }} onClick={this.handlePreviewInMap}>
                      <FillTag type="blue" text="View in map" />
                    </span>
                    {/* </Popover> */}
                  </div>
                </InfoItem>
                <InfoItem
                  itemKey="Estimated days to drop off to warehouse"
                  itemValue={util.pluralize(days_to_warehouse, 'Day')}
                />
                <InfoItem itemKey="Earliest shipping date" itemValue={earliesetShippingDate()} />
                <InfoItem itemKey="Shipping to">
                  <Cascader
                    garageId={garageId}
                    country={dest_port.country}
                    port={dest_port.name}
                    onPortChange={this.updateShippingFee}
                  />
                </InfoItem>
                <InfoItem
                  itemKey="Shipping fee"
                  itemValue={`${dest_port.shipment_fee} ${
                    dest_port.shipment_fee === 'Quote' ? '' : currency
                  }`}
                />
              </div>
              <TabItemHead
                text={this.state.menuTitles[1]}
                id="condition"
                iconType="icon_condition_line"
              />
              <div className={styles['item-content']}>
                <FormattedNumber value={distance_to_warehouse}>
                  {(val: any) => (
                    <InfoItem itemKey="Odometer" itemValue={`${val} KM`} itemKeyClass="black" />
                  )}
                </FormattedNumber>
                {vehicle_location && (
                  <InfoItem
                    itemKey="Current location"
                    itemValue={`${vehicle_location.country.display_value}, ${vehicle_location.name.display_value}`}
                    itemKeyClass="black"
                  />
                )}
                {is_new_car && (
                  <InfoItem
                    itemKey="Status"
                    itemValue={car_status === 1 ? 'IN STOCK' : 'INCOMING'}
                    itemKeyClass="black"
                  />
                )}

                {disclosure.length !== 0 && (
                  <>
                    <TabItemSubHead text="Disclosure" />
                    {disclosure.map((d, idx) => (
                      <InfoItem
                        key={idx}
                        itemKey={d.key}
                        itemValue={d.value}
                        iconType={d.isNormal ? 'icon_choose_line' : 'icon_fork_line_round'}
                      />
                    ))}
                  </>
                )}

                {vehicle_history.length > 0 && (
                  <>
                    <TabItemSubHead text="Vehicle History" hideLine={true} />
                    <div className={styles.tags}>
                      {vehicle_history.map((d: any, idx: number) => (
                        <FillTag
                          type={d.type === 'none' || d.key === 'Normal' ? 'blue' : 'red'}
                          text={d.display_value}
                          key={idx}
                        />
                      ))}
                    </div>
                  </>
                )}

                {statement.length > 0 && (
                  <>
                    <TabItemSubHead text="Seller Statement" hideLine={true} />
                    <div className={styles.statement}>
                      <div className={styles.arraw_up}></div>
                      <TradexIcon type="iconicon_share_areasvg" className={styles.share_areasvg} />
                      {statement.map((d: any, idx: number) => (
                        <p className="font-gray" key={idx}>
                          {d}
                        </p>
                      ))}
                      <TradexIcon
                        type="iconicon_quotation_right"
                        className={styles.quotation_right}
                      />
                    </div>
                  </>
                )}
                <div
                  className={`${styles.collapse} ${
                    !sellerDisclosure.length ? styles['no-padding'] : ''
                  } ${isCollapse ? styles.hide : ''}`}
                >
                  <div className={styles.content}>
                    {cr_type === 'seller_disclosure' && sellerDisclosure.length > 0 && (
                      <>
                        <TabItemSubHead text="Seller Disclosure" />
                        {sellerDisclosure.map((d: any, idx: number) => (
                          <DisclosureItem iconType={d.icon} title={d.key} key={idx}>
                            {!(d.value instanceof Array) ? (
                              <div className={styles['no-damage']}>No damage</div>
                            ) : (
                              d.value.map((d2: any, idx: number) =>
                                d.type === 0 ? (
                                  <InfoItem
                                    className="split3"
                                    itemKey={d2.key}
                                    itemValue={d2.value}
                                    key={idx}
                                  />
                                ) : (
                                  <WrapperSplit className="split3" key={idx}>
                                    <div className={styles['disclosure-content-item']}>
                                      <p className={styles['img-desc']}>{d2.key}</p>
                                      <img
                                        src={d2.img}
                                        onClick={this.showMask.bind(this, {
                                          url: d2.img,
                                          title: d2.key
                                        })}
                                      />
                                    </div>
                                  </WrapperSplit>
                                )
                              )
                            )}
                          </DisclosureItem>
                        ))}
                      </>
                    )}
                    {cr_type === '3rd' && (
                      <>
                        <TabItemSubHead text="3rd Party Condition Report" />
                        <DisclosureItem
                          iconType="icon_grading_area"
                          title="Grading"
                          style={{ marginTop: 20 }}
                        >
                          <InfoItem itemKey="" className="split3">
                            <div style={{ marginRight: -115 }}>
                              <span className="bold" style={{ marginRight: 8 }}>
                                <FormattedNumber
                                  value={cr_grading}
                                  minimumFractionDigits={1}
                                  maximumFractionDigits={1}
                                ></FormattedNumber>
                              </span>
                              <FillTag
                                type="blue"
                                text="View Report"
                                onClick={this.openTab.bind(this, 'report')}
                              />
                            </div>
                          </InfoItem>
                        </DisclosureItem>
                      </>
                    )}
                  </div>
                  {sellerDisclosure.length !== 0 && (
                    <div
                      className={`bold cursor-pointer ${styles['collapse-btn']}`}
                      onClick={this.toggleCollapse}
                    >
                      {isCollapse ? 'Expand' : 'Collapse'} Condition
                    </div>
                  )}
                </div>
              </div>
              <TabItemHead
                text={this.state.menuTitles[2]}
                id="specification"
                iconType="icon_specification_line"
              />
              <div className={styles['item-content']}>
                {Specification(this, specification, window_sticker)}
              </div>
              <TabItemHead
                text={this.state.menuTitles[3]}
                id="options"
                iconType="icon_option_line"
              />
              <div className={styles['item-content']}>
                {options.map((d: any, idx: number) => (
                  <InfoItem
                    itemKey={d.key}
                    itemValue={d.value.map((d: any) => d.description).join('/')}
                    key={idx}
                  />
                ))}
              </div>
            </div>
          </div>
        )}

        {/* preview in map */}
        {previewMapShow && (
          <MapPop
            title={name && name.display_value ? name.display_value : ''}
            subtitle={location && location.display_value ? location.display_value : ''}
            coordinate={{ lat: latitude, lng: longitude }}
            onClose={this.handlePreviewInMap}
          />
        )}
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(CarDetail);
