import React, { useState } from 'react';
import styles from './index.module.scss';
import { FormattedMessage } from 'react-intl';
import { Alert, Result } from 'antd';
import Item from '../Item';
import { BankAccount, BankAccountInfoProps } from '../types';
import CopyButton from '../CopyButton';

const InfoSection = ({ style, className, ...restProps }: any) => (
  <Item style={style} className={`${className || ''} ${styles.infoSection}`}>
    <Item className={styles.infoPanel}>
      <Item className={styles.field}>{restProps.field}</Item>
      <Item className={styles.value}>{restProps.value}</Item>
    </Item>
    {restProps.hideDivider ? '' : <div className={styles.divider}></div>}
  </Item>
);

const getText = (accountItems: any) =>
  accountItems.map((item: any) => item.title + '\n' + item.value + '\n').join('\n');

const BankAccountInfo = ({ style, className = '', result }: BankAccountInfoProps) => {
  const [showToast, setShowToast] = useState(false);

  const accountItems = result && result.data && result.data.length ? result.data : [];
  const text = getText(accountItems);
  const duration = 2;
  const alertStyle = { transition: `opacity ${duration}s` };
  let timer: any;

  const renderToast = () => {
    timer ? clearTimeout(timer) : '';
    setShowToast(true);
    timer = setTimeout(() => setShowToast(false), duration * 1000);
  };

  return (
    <div style={style} className={`${styles.container} ${className}`}>
      <Item className={styles.titleItem}>
        {result && result.title}
        <CopyButton className={styles.copyButton} text={text} onSuccess={renderToast} />
      </Item>
      {accountItems.map((ele: any, index: number) => {
        const { title: field, value } = ele;
        if (index < accountItems.length - 1) {
          return <InfoSection {...{ field, value, key: field }} />;
        } else {
          return <InfoSection {...{ field, value, key: field, hideDivider: true }} />;
        }
      })}
      <Alert
        style={showToast ? { ...alertStyle, opacity: 0.7 } : { ...alertStyle, opacity: 0 }}
        className={styles.toast}
        message={
          <div className={styles.toastMessage}>
            <FormattedMessage id="drawer_bankAccount_copyToast" />
          </div>
        }
      />
    </div>
  );
};

export default BankAccountInfo;
