import {
  UnionChatActionTypes,
  UnionChatAction,
  UnionChatStore,
  ChatMode,
  ChatRole
} from '../types';

const initialState: UnionChatStore = {
  modes: [ChatMode.LIST_MODE],
  params: {
    [ChatMode.ALL_IN_ONE]: {
      roomId: '',
      categoryId: '',
      activeRole: ChatRole.Buyer
    },
    [ChatMode.WINDOW_MODE]: {
      roomId: '',
      categoryId: '',
      activeRole: ChatRole.Buyer
    },
    [ChatMode.LIST_MODE]: {
      roomId: '',
      categoryId: '',
      activeRole: ChatRole.Buyer,
      timestamp: Date.now()
    }
  }
};

export default (state = initialState, action: UnionChatAction): UnionChatStore => {
  switch (action.type) {
    case UnionChatActionTypes.UnionChatRouteGoTo: {
      let _modes = state.modes.concat();
      if (!state.modes.includes(action.mode)) {
        _modes.push(action.mode);
      }

      return {
        ...state,
        modes: _modes,
        params: Object.assign({}, state.params, {
          [action.mode]: action.params
        })
      };
    }

    case UnionChatActionTypes.UnionChatRouteGoAway: {
      let _modes = state.modes.filter(m => m != action.mode);

      return {
        ...state,
        modes: _modes,
        params: Object.assign({}, state.params, {
          [action.mode]: action.params
        })
      };
    }

    case UnionChatActionTypes.UnionChatSetAciveRole: {
      let _modes = state.modes.concat();
      if (!state.modes.includes(action.mode)) {
        _modes.push(action.mode);
      }

      return {
        ...state,
        modes: _modes,
        params: Object.assign({}, state.params, {
          [action.mode]: action.params
        })
      };
    }
    default:
      return state;
  }
};
