enum ActionType {
  SET_CAR_INFO = 'set_car_info'
}
enum VehicleType {
  NewCar = 1,
  UsedCar = 2
}
enum VehicleStatus {
  InStock = '1',
  Incoming = '2'
}
interface CreateCarInfo {
  car_status?: VehicleStatus;
  car_type?: VehicleType;
  specification_location?: string;
  make_name?: string;
  model_year?: string;
  model_name?: string;
  trim_name?: string;
  engine_type?: string;
  displacement?: string;
  fuel_type?: string;
  body?: string;
  door_number?: string;
  l_or_r?: string;
}
interface CreateCarAction {
  type: ActionType;
  payload: CreateCarInfo;
}

const SetCreateCarInfo = (createCarInfo: CreateCarInfo): CreateCarAction => ({
  type: ActionType.SET_CAR_INFO,
  payload: createCarInfo
});

export { ActionType, SetCreateCarInfo };
