import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Tooltip, Divider } from 'antd';
import styles from './index.module.scss';
import { AppStore } from '../../../../store/reducers';
import Avatar from '../../components/Avatar';
import Flag from '../../../../components/Flag';
import IconFont from '../../../../components/ui/TradexIcon';
import _get from '../../../../common/get';
import { getUserAddress, fetchUserInfo } from './utils';
import { FormattedMessage } from 'react-intl';
import InputWithTitle from '../../components/InputWithTitle';
import Modal, { ModalType } from '../../components/Modal';
import { SetUserInfo } from '../../../../store/actions/user';
import { UserInfo } from '../../../../store/types';

const Profile = (props: any) => {
  const [userInfo, setUserInfo] = useState(props.userInfo);
  const [modalInfo, setModalInfo] = useState({ isShow: false, type: ModalType.Username });

  useEffect(() => {
    if (props.userInfo !== userInfo) {
      setUserInfo(props.userInfo);
    }
    if (_get(props, ['extraData', 'match', 'params', 'operation']) === 'confirm_email') {
      setModalInfo({ isShow: true, type: ModalType.Email });
    }
  }, [props]);

  const { style, className = '' } = props;
  const username = _get(userInfo, ['display_name']);
  const isVerified = _get(userInfo, ['work_email_confirmed']);
  const position = _get(userInfo, ['position_at_company']);
  const companyCountry = _get(userInfo, ['company_country']);
  const companyCountryFullName = _get(userInfo, ['company_country_full_name']);
  const companyName = _get(userInfo, ['company_name']);
  const businessOperatingName = _get(userInfo, ['company_operating_name']);
  const companyIdentificationNumber = _get(userInfo, ['company_identification_number']);
  const taxID = _get(userInfo, ['tax_no']);
  const officePhoneNumber = _get(userInfo, ['office_phone_number']);
  const address = getUserAddress(userInfo);
  const popoverContent = (
    <div className={styles.popover}>
      <FormattedMessage
        id={isVerified ? 'account_profile_verified' : 'account_profile_notVerified'}
      />
    </div>
  );

  const openModal = (type: ModalType) => setModalInfo({ isShow: true, type });
  const updateUserInfo = async () => {
    const userInfo = await fetchUserInfo();
    setUserInfo(userInfo);
  };

  return (
    <div style={style} className={`${className} ${styles.container}`}>
      <div className={styles.header}>
        <div className={styles.content}>
          <Avatar
            className={styles.avatar}
            src={_get(userInfo, ['headshot'])}
            size={80}
            updateUserInfo={updateUserInfo}
          />
          <div className={styles.name}>{username || '--'}</div>
          <div className={styles.flag}>
            <Flag country={companyCountry} style={{ width: '100%', height: '100%' }} />
          </div>
          <div className={styles.country}>
            <div className={styles.countryName}>{companyCountryFullName}</div>
          </div>
          <div className={styles.PhoneAndMail}>
            <div className={styles.IconAndValue}>
              <div className={styles.icon}>
                <IconFont type="iconicon_phone_line" />
              </div>
              <div className={styles.value}>{_get(userInfo, ['phone'])}</div>
            </div>
            <div className={styles.IconAndValue}>
              <div className={styles.icon}>
                <IconFont type="iconicon_email_line" />
              </div>
              <div className={styles.value}>{_get(userInfo, ['work_email'])}</div>
              <div className={styles.badge}>
                <Tooltip placement="bottom" title={popoverContent}>
                  <IconFont
                    style={{ color: isVerified ? '#21c442' : '#f2565f' }}
                    type={isVerified ? 'iconicon_choose_line' : 'iconicon_warning'}
                  />
                </Tooltip>
              </div>
              <div className={styles.edit} onClick={() => openModal(ModalType.Email)}>
                <FormattedMessage id="account_profile_edit" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className={styles.body}>
        <InputWithTitle
          title={'Username'}
          onEdit={() => openModal(ModalType.Username)}
          value={username}
          className={styles.InputWithTitle}
          editable={true}
        />
        <Divider style={{ margin: '40px 0px' }} />
        <div className={styles.otherInfo}>
          <div className={styles.column}>
            <InputWithTitle title={'Position'} value={position} className={styles.item} />
            <InputWithTitle
              title={'Company Registered Country'}
              value={companyCountryFullName}
              className={styles.item}
            />
            <InputWithTitle
              title={'Company Legal Name'}
              value={companyName}
              className={styles.item}
            />
            <InputWithTitle
              title={'Business Operating Name'}
              value={businessOperatingName}
              className={styles.item}
            />
            <InputWithTitle
              title={'Company Identification Number'}
              value={companyIdentificationNumber}
              className={styles.item}
            />
          </div>
          <div className={styles.column}>
            <InputWithTitle title={'Tax ID'} value={taxID} className={styles.item} />
            <InputWithTitle
              title={'Office Phone Number'}
              value={officePhoneNumber}
              className={styles.item}
            />
            <InputWithTitle title={'Company Address'} value={address} className={styles.item} />
          </div>
        </div>
      </div>
      {modalInfo.isShow ? (
        <Modal
          onClose={() => setModalInfo({ ...modalInfo, isShow: false })}
          type={modalInfo.type}
          updateUserInfo={updateUserInfo}
        />
      ) : (
        ''
      )}
    </div>
  );
};

const mapStateToPros = (state: AppStore) => ({
  userInfo: state.user.userInfo
});

const mapDispatchToProps = (dispatch: any) => ({
  setUserInfo: (userInfo: UserInfo) => dispatch(SetUserInfo(userInfo))
});

export default connect(
  mapStateToPros,
  mapDispatchToProps
)(Profile);
