import { PadStore, PadAction, PadActionTypes } from '../types';
import _get from '../../common/get';

export const initialState: PadStore = {
  is_signed: true,
  doc: '',
  is_deleted: false,
  isUseful: false
};

// todo action as any
export default (state = initialState, action: PadAction): PadStore => {
  let newState: PadStore = {};
  Object.keys(state).forEach((key: string) => {
    newState[key] = _get(action, ['status', key]);
    newState[key] === undefined ? (newState[key] = state[key]) : '';
  });
  switch (action.type) {
    case PadActionTypes.SET_PAD_INFO: {
      return newState;
    }
    default:
      return state;
  }
};
