import React from 'react';
import { Popover } from 'antd';
import styles from './index.module.scss';
import { FormattedMessage } from 'react-intl';

const message = {
  primary: 'account_bankAccounts_tags_primary',
  pad: 'account_bankAccounts_tags_pad',
  popover: 'account_bankAccounts_tags_popover_primary'
};

export const PrirmaryTag = (props: any) => {
  const { className = '' } = props;
  const content = <FormattedMessage id={message.popover} />;
  return (
    <Popover content={content} placement="bottomRight" overlayClassName={`primary_overlay`}>
      <div className={`${styles.primary} ${className}`}>
        <FormattedMessage id={message.primary} />
      </div>
    </Popover>
  );
};

export const PadTag = (props: any) => {
  const { className = '' } = props;
  return (
    <div className={`${styles.pad} ${className}`}>
      <FormattedMessage id={message.pad} />
    </div>
  );
};
