import { get, post } from './http';
import request from './http/request';
import { RootAPI } from '../api/http/config';
import { AxiosPromise } from 'axios';

export function getRoomHistory(id: string, params?: object): AxiosPromise<any> {
  // return get(`chatrooms/${id}/history/`, params); // old api
  return get(`chats/${id}/history/`, params);
}

export function sendImageMessage(id: string, params: object): AxiosPromise<any> {
  return request({
    url: RootAPI(`chatrooms/${id}/image_message/`),
    method: 'post',
    headers: { 'Content-Type': 'multipart/form-data' },
    data: params
  });
}

export function GetUnreadMsgNum(): AxiosPromise<any> {
  return get('/messages/unread_count/', {});
}

export function getChatBuyList(params?: object): AxiosPromise<any> {
  return get('/chatrooms/buyer_list/', params);
}

export function getChatSellList(params?: object): AxiosPromise {
  return get('/chatrooms/seller_list/', params);
}

export function resetChatRoomUnreadCount(id: string): AxiosPromise {
  return get(`/chatrooms/${id}/reset_unread_count/`);
}

export function upDateChatRoomUnread(id: string): AxiosPromise {
  return get(`/chatrooms/${id}/top_bar/`);
}
