import React from 'react';
import { render } from 'react-dom';
import { Provider } from 'react-redux';
import { createStore, applyMiddleware } from 'redux';
import thunkMiddleware from 'redux-thunk';
import { IntlProvider } from 'react-intl';
import { composeWithDevTools } from 'redux-devtools-extension';
import * as Sentry from '@sentry/browser';
import rootReducer from './store/reducers';
import routes from './routes';
import { txLog, txError, txWarn } from './common/console';
import { messagesMap, lan } from './locale';
import { SetSettingInfo } from './store/actions/setting';
import { SetUserInfo } from './store/actions/user';
import { asyncSetPadInfo } from './store/actions/pad';
import { asyncSetBankCards } from './store/actions/bankCards';
import { getUserInfo } from './api/user';
import { getSetting } from './api/setting';
import { getCookie } from './common/cookie';
import { WebSocketManager } from './api/wss';
import { setUnreadNum } from './store/actions/chat';
// import { unregister } from './serviceWorker';
import './common/preset';
import 'antd/dist/antd.css';

const isPro = process.env.NODE_ENV === 'production';

window.__tx__ = window.__tx__ || {};
window.__tx__.log = txLog;
window.__tx__.warn = txWarn;
window.__tx__.error = txError;

Sentry.init({
  dsn: 'https://729bfca2071a41a096dc277fdf6c95fb@sentry.io/1552611',
  enabled: isPro
});

const enhancer = !isPro
  ? composeWithDevTools(applyMiddleware(thunkMiddleware))
  : applyMiddleware(thunkMiddleware);
const store = createStore(rootReducer, enhancer);

//
getSetting().then((response: any) => {
  store.dispatch(SetSettingInfo(response.data));
});

//
const token = getCookie('Authorization');
const paperStatus = (JSON.parse(localStorage.getItem('paper') as string) || {}).status;

if (token && paperStatus == 2  && true) {//test 
  getUserInfo()
    .then((response: any) => {
      const {
        id = '',
        first_name = '',
        last_name = '',
        username = '',
        phone = '',
        company_country = '',
        headshot = '',
        display_name = '',
        work_email = '',
        work_email_confirmed = false,
        position_at_company = '',
        company_name = '',
        company_operating_name = '',
        company_identification_number = '',
        tax_no = '',
        office_phone_number = '',
        company_address = '',
        company_address2 = '',
        company_address3 = '',
        city = '',
        postal_or_zip_code = '',
        company_country_full_name = '',
        province_state = ''
      } = response.data || {};

      store.dispatch(
        SetUserInfo({
          userId: id,
          first_name,
          last_name,
          phone,
          username,
          company_country,
          headshot,
          display_name,
          work_email,
          work_email_confirmed,
          position_at_company,
          company_name,
          company_operating_name,
          company_identification_number,
          tax_no,
          office_phone_number,
          company_address,
          company_address2,
          company_address3,
          city,
          postal_or_zip_code,
          company_country_full_name,
          province_state
        })
      );
    })
    .catch(rejection => {});
  store.dispatch(asyncSetPadInfo() as any);
  store.dispatch(asyncSetBankCards() as any);
}
//WS
if (token) {
  const userWss = new WebSocketManager({
    onMessage(data: {
      type: string;
      data: {
        has_new: boolean;
        [index: string]: any;
      };
    }) {
      if (data.data && data.data.has_new) {
        store.dispatch(setUnreadNum() as any);
      }
    }
  });
}

render(
  <IntlProvider locale={lan} messages={messagesMap as any}>
    <Provider store={store}> {routes()} </Provider>
  </IntlProvider>,
  document.getElementById('root') as HTMLElement
);

// unregister();
