import { DisplayItem } from './common';

enum OrderType {
  Main,
  Sub,
  Both
}

interface Warehouse {
  name: DisplayItem;
  country: DisplayItem;
  location: DisplayItem;
  city_location: {
    name: DisplayItem;
  };
}

interface ColorOption {
  rgb: string;
}
interface CarInfo {
  id: string;
  thumbnail: string;
  display_name: string;
  payment_currency: string;
  make_logo: string;
  distance_to_warehouse: number;
  seller_note: string;
  car_id?: string;
  stock_no?: string;
  is_new_car?: boolean;
  car_status?: number;
}
interface CommonDetail {
  id: string;
  updated_at: string;
  notice: string;
  status: number;
  links: { key: string; type: string }[];
  actions: {
    action_name: string;
    action_type: number;
    action_params: { due_time: number };
    _leftStr: string;
    _disable: boolean;
  }[];
  car_info: CarInfo;
  rfq_info?: CarInfo;
}

interface OrderDetail extends CommonDetail {
  order_no: string;
  order_type: number;
  created_at: string;
  payment_method: number;
  garage: string;
  current_stage: string;
  ship_method: number;
  total_price: string;
  dest_port: Warehouse;
  warehouse: Warehouse;
  suborders: SubOrderDetail[];
  total_cars: number;
  deposit_percentage: number;
  deposit_title: string;
  seller_currency: string;
  chatuser: {
    id: string;
    display_name: string;
    headshot: string;
  };
  color_options: {
    exterior_color: ColorOption;
    interior_color: ColorOption;
    number_of_cars: number;
  }[];
}

interface SubOrderDetail extends CommonDetail {
  vin: string;
  interior_color: string;
  exterior_color: string;
  _price: string;
  _idx: number;
}

enum PaymentMethod {
  TT = 1,
  LC,
  EFT
}

enum OrderType {
  Garage = 1, //Instant request
  RFQ,
  USED_CAR
}

enum ShipmentMethod {
  Own = 1,
  Tradex
}

export { OrderType, OrderDetail, SubOrderDetail, PaymentMethod, ShipmentMethod };
