import React, { useState, Children } from 'react';
import { message as antdMessage } from 'antd';
import Button from '../../../../components/Btn';
import { FormattedMessage } from 'react-intl';
import { post } from '../../../../api/http';
import styles from './Register.module.scss';
import Input from '../../components/Input';
import Select from '../../components/Select';
import Image from '../../components/Image';
import _get from '../../../../common/get';

interface ParamType {
  [index: string]: string;
}
interface WarningType {
  [index: string]: boolean;
}

const message = {
  addBank: 'account_bankAccounts_drawer_title_add',
  bankDetail: 'account_bankAccounts_drawer_title_view',
  button: 'account_bankAccounts_drawer_button'
};

const inputTitle = {
  accountName: 'account_bankAccounts_drawer_content_accountName',
  accountAddress: 'account_bankAccounts_drawer_content_accountAddress',
  bankName: 'account_bankAccounts_drawer_content_bankName',
  bankAddress: 'account_bankAccounts_drawer_content_bankAddress',
  bankNumber: 'account_bankAccounts_drawer_content_bankNumber',
  institutionCode: 'account_bankAccounts_drawer_content_institutionCode',
  transitNumber: 'account_bankAccounts_drawer_content_transitNumber',
  swiftCode: 'account_bankAccounts_drawer_content_swiftCode',
  ibanCode: 'account_bankAccounts_drawer_content_ibanCode',
  currency: 'account_bankAccounts_drawer_content_currency',
  voidCheck: 'account_bankAccounts_drawer_content_voidCheck'
};

const initalParam: ParamType = {
  institution_postal: '',
  institution_code: '',
  SWIFT: '',
  institution_province: '',
  account_addr: '',
  transit_number: '',
  account_number: '',
  void_check: '',
  institution_city: '',
  currency: '',
  account_city: '',
  institution_addr: '',
  account_name: '',
  account_postal: '',
  IBAN: '',
  institution_name: '',
  account_province: ''
};

const initalWarning: WarningType = {
  institution_postal: false,
  institution_code: false,
  SWIFT: false,
  institution_province: false,
  account_addr: false,
  transit_number: false,
  account_number: false,
  void_check: false,
  institution_city: false,
  currency: false,
  account_city: false,
  institution_addr: false,
  account_name: false,
  account_postal: false,
  institution_name: false,
  account_province: false
};

const Col = (props: any) => {
  return (
    <div style={props.style} className={`${styles.col} ${props.className}`}>
      <div className={styles.message}>
        <FormattedMessage id={props.messageId} />
      </div>
      {props.children}
    </div>
  );
};

const Register = (props: any) => {
  const [param, setParam] = useState(() =>
    props.bankCardInfo ? { ...initalParam, ...props.bankCardInfo } : { ...initalParam }
  );
  const [inputWarning, setInputWarning] = useState(initalWarning);
  const [inputType, setInputType] = useState<'view' | 'edit'>(props.bankCardInfo ? 'view' : 'edit');

  const { options = [] } = props;

  const onChange = (key: string, value: string) => {
    if (value) {
      setInputWarning({ ...inputWarning, [key]: false });
      setParam({ ...param, [key]: value });
    } else {
      setInputWarning({ ...inputWarning, [key]: true });
      setParam({ ...param, [key]: value });
    }
  };
  const submit = () => {
    const tempWarning = { ...initalWarning };
    const invaildKeys = Object.keys(tempWarning).filter(key => !param[key]);
    invaildKeys.map(key => (tempWarning[key] = true));
    setInputWarning({ ...inputWarning, ...tempWarning });
    if (invaildKeys.length > 0) return;
    post('users/bank_accounts/', param)
      .then(() => {
        props.onClose && props.onClose();
        props.update && props.update();
      })
      .catch(e => {
        const message = _get(e, ['response', 'data', 'message']) || 'Error';
        antdMessage.error(message);
      });
  };

  return (
    <div className={styles.container}>
      <div className={`${styles.content} ${inputType === 'view' ? styles.view : ''}`}>
        <div className={styles.title}>
          <FormattedMessage id={inputType === 'view' ? message.bankDetail : message.addBank} />
        </div>
        <div className={styles.accountInfo}>
          <Col
            messageId={inputTitle.accountName}
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
          >
            <Input
              warning={inputWarning['account_name']}
              type={inputType}
              value={param['account_name']}
              onChange={(value: string) => onChange('account_name', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '184px' }}
            className={styles.InputItem}
            messageId={inputTitle.accountAddress}
          >
            <Input
              className={styles.input}
              type={inputType}
              value={param['account_addr']}
              warning={inputWarning['account_addr']}
              onChange={(value: string) => onChange('account_addr', value)}
              placeholder="Address"
            />
            <Input
              className={styles.input}
              type={inputType}
              value={param['account_city']}
              warning={inputWarning['account_city']}
              onChange={(value: string) => onChange('account_city', value)}
              placeholder="City"
            />
            <Input
              className={styles.input}
              type={inputType}
              value={param['account_province']}
              warning={inputWarning['account_province']}
              onChange={(value: string) => onChange('account_province', value)}
              placeholder="Provice/State"
            />
            <Input
              className={styles.input}
              type={inputType}
              value={param['account_postal']}
              warning={inputWarning['account_postal']}
              onChange={(value: string) => onChange('account_postal', value)}
              placeholder="Postal code"
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.bankName}
          >
            <Input
              type={inputType}
              value={param['institution_name']}
              warning={inputWarning['institution_name']}
              onChange={(value: string) => onChange('institution_name', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '184px' }}
            className={styles.InputItem}
            messageId={inputTitle.bankAddress}
          >
            <Input
              className={styles.input}
              type={inputType}
              value={param['institution_addr']}
              warning={inputWarning['institution_addr']}
              onChange={(value: string) => onChange('institution_addr', value)}
              placeholder="Address"
            />
            <Input
              className={styles.input}
              type={inputType}
              value={param['institution_city']}
              warning={inputWarning['institution_city']}
              onChange={(value: string) => onChange('institution_city', value)}
              placeholder="City"
            />
            <Input
              className={styles.input}
              type={inputType}
              value={param['institution_province']}
              warning={inputWarning['institution_province']}
              onChange={(value: string) => onChange('institution_province', value)}
              placeholder="Provice/State"
            />
            <Input
              className={styles.input}
              type={inputType}
              value={param['institution_postal']}
              warning={inputWarning['institution_postal']}
              onChange={(value: string) => onChange('institution_postal', value)}
              placeholder="Postal code"
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.bankNumber}
          >
            <Input
              type={inputType}
              value={param['account_number']}
              warning={inputWarning['account_number']}
              onChange={(value: string) => onChange('account_number', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.institutionCode}
          >
            <Input
              type={inputType}
              value={param['institution_code']}
              warning={inputWarning['institution_code']}
              onChange={(value: string) => onChange('institution_code', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.transitNumber}
          >
            <Input
              type={inputType}
              value={param['transit_number']}
              warning={inputWarning['transit_number']}
              onChange={(value: string) => onChange('transit_number', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.swiftCode}
          >
            <Input
              type={inputType}
              value={param['SWIFT']}
              warning={inputWarning['SWIFT']}
              onChange={(value: string) => onChange('SWIFT', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.ibanCode}
          >
            <Input
              type={inputType}
              value={param['IBAN']}
              onChange={(value: string) => onChange('IBAN', value)}
            />
          </Col>
          <Col
            style={{ minHeight: '64px' }}
            className={styles.InputItem}
            messageId={inputTitle.currency}
          >
            <Select
              disabled={inputType === 'view'}
              value={param['currency']}
              warning={inputWarning['currency']}
              onChange={(value: string) => onChange('currency', value)}
              options={options}
            />
          </Col>
          <Col
            style={{ minHeight: '197px' }}
            className={styles.InputItem}
            messageId={inputTitle.voidCheck}
          >
            <Image
              type={inputType}
              warning={inputWarning['void_check']}
              onChange={(value: string) => onChange('void_check', value)}
              src={param['void_check']}
            />
          </Col>
        </div>
      </div>
      {inputType === 'edit' ? (
        <Button className={styles.button} onClick={submit}>
          <FormattedMessage id={message.button} />
        </Button>
      ) : (
        ''
      )}
    </div>
  );
};

export default Register;
