import React from 'react';
import styles from './index.module.scss';
import { FormattedMessage } from 'react-intl';
import IconFont from '../../../../../components/ui/TradexIcon';

type WarningProps = {
  style?: React.CSSProperties;
  className?: any;
  title?: string;
};

const Warning = ({ style, className = '', title }: WarningProps) => (
  <div style={style} className={`${className} ${title ? styles.warning : ''}`}>
    {title ? (
      <>
        <div>
          <IconFont type="iconicon_warning" className={styles.iconWarning} />
        </div>
        <div className={styles.warningInfo}>{title}</div>
      </>
    ) : (
      ''
    )}
  </div>
);

export default Warning;
