import React, { useState } from 'react';
import { connect } from 'react-redux';
import { Input } from 'antd';
import IconFont from '../../../../components/ui/TradexIcon';
import Button from '../../../../components/Btn';
import _get from '../../../../common/get';
import { SetCreateCarInfo } from '../../../../store/actions/createCar';
import { AppStore } from '../../../../store/reducers/index';

import styles from './index.module.scss';

interface Props {
  className?: string;
  setCarStatus: SetCarStatus;
  setCarType: SetCarType;
  setCountrySpec: setCountrySpec;
  createCar?: any;
}
enum VehicleType {
  NewCar = 1,
  UsedCar = 2
}
enum VehicleStatus {
  InStock = '1',
  Incoming = '2'
}
enum CountrySpec {
  CA = 'Canada Spec',
  US = 'United Stated Spec',
  MX = 'Mexico Spec',
  AE = 'United Arab Emirates Spec'
}
type SetCarStatus = (status: VehicleStatus) => void;
type SetCarType = (type: VehicleType) => void;
type setCountrySpec = (spec: CountrySpec) => void;

const mapStateToPros = (state: AppStore) => ({
  createCar: state.createCar
});

const mapDispatchToProps = (dispatch: any) => ({
  setCarStatus: (status: VehicleStatus) => dispatch(SetCreateCarInfo({ car_status: status })),
  setCarType: (type: VehicleType) => dispatch(SetCreateCarInfo({ car_type: type })),
  setCountrySpec: (spec: CountrySpec) =>
    dispatch(SetCreateCarInfo({ specification_location: spec }))
});

const VehicleStatues = (props: Props) => {
  const { className } = props;
  const {
    car_type: vehicleType,
    car_status: vehicleStatus,
    specification_location: countrySpec
  } = props.createCar;

  const handleStatus = (e: any) => {
    let status = _get(e, 'target.dataset.vehicleStatus');
    if (status) {
      props.setCarStatus(status);
    }
  };
  const handleType = (e: any) => {
    let type = _get(e, 'target.dataset.vehicleType');
    if (type) {
      props.setCarType(Number(type));
    }
  };
  const handleCountrySpec = (e: any) => {
    let spec = _get(e, 'target.dataset.countrySpec');
    if (spec) {
      props.setCountrySpec(spec);
    }
  };

  return (
    <div className={`${styles.container} ${className}`}>
      <div className={`${styles.section}`}>
        <div className={`${styles.title}`}>
          <span className={`${styles.symbol}`}>*</span>
          <span>Vehicle Type</span>
        </div>
        <div className={`${styles.options}`}>
          <div
            data-vehicle-type={VehicleType.NewCar}
            className={`${styles.option} ${styles.inactive}`}
          >
            New Car
          </div>
          <div
            data-vehicle-type={VehicleType.UsedCar}
            className={`${styles.option} ${styles.active}`}
          >
            Used Car
          </div>
        </div>
      </div>
      <div className={`${styles.divider}`}></div>
      <div className={`${styles.section}`}>
        <div className={`${styles.title}`}>
          <span className={`${styles.symbol}`}>*</span>
          <span>Vehicle Status</span>
        </div>
        <div className={`${styles.options}`} onClick={handleStatus}>
          <div
            data-vehicle-status={VehicleStatus.InStock}
            className={`${styles.option} ${
              vehicleStatus === VehicleStatus.InStock ? styles.active : ''
            }`}
          >
            In Stock
          </div>
          <div
            data-vehicle-status={VehicleStatus.Incoming}
            className={`${styles.option} ${
              vehicleStatus === VehicleStatus.Incoming ? styles.active : ''
            }`}
          >
            Incoming
          </div>
        </div>
      </div>
      {vehicleStatus === VehicleStatus.Incoming ? (
        <>
          <div className={`${styles.divider}`}></div>
          <div className={`${styles.section}`}>
            <div className={`${styles.title}`}>
              <span className={`${styles.symbol}`}>*</span>
              <span>Estimated Date of possession</span>
            </div>
            <Input
              className={styles.estimatedDate}
              placeholder="e.g. 2019/09/13"
              suffix={<IconFont type="iconicon_dropdown_line" />}
            />
          </div>
        </>
      ) : null}
      <div className={`${styles.divider}`}></div>
      <div className={`${styles.section}`}>
        <div className={`${styles.title}`}>
          <span className={`${styles.symbol}`}>*</span>
          <span>Country Specification</span>
        </div>
        <div className={`${styles.options}`} onClick={handleCountrySpec}>
          <div
            data-country-spec={CountrySpec.CA}
            className={`${styles.option} ${countrySpec === CountrySpec.CA ? styles.active : ''}`}
          >
            Canada Spec
          </div>
          <div
            data-country-spec={CountrySpec.US}
            className={`${styles.option} ${countrySpec === CountrySpec.US ? styles.active : ''}`}
          >
            United Stated Spec
          </div>
          <div
            data-country-spec={CountrySpec.MX}
            className={`${styles.option} ${countrySpec === CountrySpec.MX ? styles.active : ''}`}
          >
            Mexico Spec
          </div>
          <div
            data-country-spec={CountrySpec.AE}
            className={`${styles.option} ${countrySpec === CountrySpec.AE ? styles.active : ''}`}
          >
            United Arab Emirates Spec
          </div>
        </div>
      </div>
      <div className={styles.next}>
        <Button>Next</Button>
      </div>
    </div>
  );
};

export default connect(
  mapStateToPros,
  mapDispatchToProps
)(VehicleStatues);
