import React from 'react';
import Item from './Item';
import BankAccountInfo from './BankAccountInfo';

type BankAccountItemProps = {
  style?: React.CSSProperties;
  className?: any;
  results: any[];
};

const BankAccountItem = ({ style, className = '', results }: BankAccountItemProps) => {
  const result = results.find(result => result.type === 'block_bank');
  return (
    <Item style={style} className={className}>
      {result ? <BankAccountInfo {...{ result }} /> : ''}
    </Item>
  );
};

export default BankAccountItem;
