import React from 'react';
// import { Input } from 'antd';
import Input from '../Input';
import styles from './index.module.scss';
import IconFont from '../../../../components/ui/TradexIcon';

type InputWithTitleProps = {
  style?: any;
  className?: any;
  titleClass?: any;
  inputClass?: any;
  title?: string;
  value?: string | Array<string>;
  editable?: boolean;
  onEdit?: any;
};

const InputWithTitle = (props: InputWithTitleProps) => {
  const { className = '', onEdit = () => {}, value = '' } = props;

  const renderTitle = () => {
    const { titleClass = '', title } = props;
    if (title) {
      return <div className={`${styles.title} ${titleClass}`}>{title}</div>;
    } else {
      return null;
    }
  };

  const renderInput = () => {
    const { inputClass = '', editable = false, value = '' } = props;
    const suffix = (
      <div className={styles.suffixIcon} onClick={onEdit}>
        <IconFont type="iconicon_edit_line" />
      </div>
    );
    let inputProps: any = {
      type: 'view',
      value
    };
    if (editable) {
      inputProps.suffix = suffix;
      inputProps.className = `${styles.editableInput} ${inputClass}`;
    } else {
      inputProps.className = `${styles.input} ${inputClass}`;
    }
    return <Input {...inputProps} />;
  };

  const renderInputs = () => {
    const { inputClass = '', value = [] } = props;

    const address = (value as Array<string>).slice(0, value.length - 2);
    const inputs = address
      .filter(item => item !== undefined && item !== null)
      .map((item, key) => {
        let inputProps: any = {
          type: 'view',
          className: `${styles.AddressInput} ${inputClass}`,
          value: item,
          key
        };
        return <Input {...inputProps} />;
      });
    const renderCityAndPost = () => {
      let [city, post] = (value as Array<string>).slice(-2);
      let cityInput: any = {
        type: 'view',
        className: `${styles.CityInput} ${inputClass}`,
        value: city
      };
      let postInput: any = {
        type: 'view',
        className: `${styles.PostInput} ${inputClass}`,
        value: post
      };
      return (
        <div className={styles.CityAndPost}>
          <Input {...cityInput} />
          <Input {...postInput} />
        </div>
      );
    };
    return (
      <>
        {inputs.map(item => item)}
        {renderCityAndPost()}
      </>
    );
  };

  return (
    <div className={`${styles.container} ${className}`}>
      {renderTitle()}
      {Array.isArray(value) ? renderInputs() : renderInput()}
    </div>
  );
};

export default InputWithTitle;
