import React from 'react';
import IconFont from '../ui/TradexIcon';
import './index.scss';
import Slider from 'react-slick';
import 'slick-carousel/slick/slick.css';

type Props = {
  imageSrc: Array<string>;
  onClose: Function;
};

type Sates = {
  maskCurrent: number;
};

function MaskNextArrow(props: { onClick: any }) {
  const { onClick } = props;
  return (
    <div className="slick-next" onClick={onClick}>
      {/*<IconFont type="iconicon_righrtarrow_big_pc" className="icon" />*/}
    </div>
  );
}

function MaskPrevArrow(props: { onClick: any }) {
  const { onClick } = props;
  return (
    <div className="slick-prev" onClick={onClick}>
      {/*<IconFont type="iconicon_lefttarrow_big_pc" className="icon" />*/}
    </div>
  );
}

class Swiper extends React.Component<Props, Sates> {
  private maskSlider1: any;

  constructor(props: Readonly<Props>) {
    super(props);
    this.state = {
      maskCurrent: 0
    };
  }

  maskNext() {
    this.maskSlider1.slickNext();
  }
  maskPrevious() {
    this.maskSlider1.slickPrev();
  }
  maskBeforeChange(current: number, next: number) {
    this.setState({ maskCurrent: next });
  }

  handleGoTo(index: any) {
    this.setState({ maskCurrent: index });
    this.maskSlider1.slickGoTo(index, false);
  }

  thumbnailPrev() {
    if (this.state.maskCurrent >= 1) {
      this.setState({ maskCurrent: this.state.maskCurrent - 1 });
      this.maskSlider1.slickGoTo(this.state.maskCurrent - 1, false);
    } else {
      let originImageList = this.props.imageSrc;
      this.setState({ maskCurrent: originImageList.length - 1 });
      this.maskSlider1.slickGoTo(originImageList.length - 1, false);
    }
  }

  thumbnailNext() {
    let originImageList = this.props.imageSrc;
    if (this.state.maskCurrent < originImageList.length) {
      let result = this.state.maskCurrent + 2;
      if (result === originImageList.length + 1) {
        this.setState({ maskCurrent: 0 });
        this.maskSlider1.slickGoTo(0, false);
      } else {
        this.setState({ maskCurrent: this.state.maskCurrent + 1 });
        this.maskSlider1.slickGoTo(this.state.maskCurrent + 1, false);
      }
    }
  }

  static defaultProps = {
    imageSrc: [
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/1.jpg',
      'http://img.diudou.com/file/dongman/news/2017-05-12/7c8a6ce85838085c87bc5f4fe01922bf.jpg',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/axis.png',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/4.jpg',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/5.jpg',
      'https://navcd-1252873427.cos.ap-guangzhou.myqcloud.com/blog_img/6.jpg'
    ]
  };

  render() {
    let carImageList = this.props.imageSrc.map((src, index) => <img src={src} key={index} />);
    let carThumbnailImageList = this.props.imageSrc.map((src, index) => (
      <li key={index} className={this.state.maskCurrent === index ? 'active' : ''}>
        <img src={src} onClick={this.handleGoTo.bind(this, index)} />
      </li>
    ));

    let maskImageList = carImageList;
    let maskThumbnailImageList = carThumbnailImageList;

    const maskSettings = {
      infinite: true,
      speed: 200,
      slidesToShow: 1,
      nextArrow: <MaskNextArrow onClick={this.maskNext} />,
      prevArrow: <MaskPrevArrow onClick={this.maskPrevious} />,
      beforeChange: this.maskBeforeChange.bind(this)
    };
    let isLg = document.body.clientWidth >= 1920;

    let translatedX =
      (this.state.maskCurrent >= 5 ? this.state.maskCurrent - 4 : 0) * (isLg ? -162 : -121);

    return (
      <div className="carouselWrap2">
        <div className="carousel-mask">
          <IconFont
            type="iconicon_close_appsystem"
            className="close-icon"
            onClick={this.props.onClose}
          />
          <div className={'content ' + (isLg ? 'lg-content' : 'md-content')}>
            <div className="mask-slider">
              <div className="mask-slider-content">
                <Slider {...maskSettings} ref={c => (this.maskSlider1 = c)}>
                  {maskImageList}
                </Slider>
                <div className="currentTag">
                  {this.state.maskCurrent + 1}/{maskImageList.length}
                </div>
              </div>
            </div>
            <div className="mask-thumbnail-wrap">
              <div className="dot-prev" onClick={this.thumbnailPrev.bind(this)}>
                <IconFont type="iconicon_leftarrow" className="icon" />
              </div>
              <div className="slick-dots">
                <ul
                  style={{
                    width: maskThumbnailImageList.length * (isLg ? -162 : -121) + 'px',
                    transform: `translate3d(${translatedX}px, 0px, 0px)`
                  }}
                >
                  {maskThumbnailImageList}
                </ul>
              </div>
              <div className="dot-next" onClick={this.thumbnailNext.bind(this)}>
                <IconFont type="iconicon_rightarrow" className="icon" />
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Swiper;
