import React from 'react';
import Warning from './Warning';
import Item from './Item';

type WarningItemProps = {
  style?: React.CSSProperties;
  className?: any;
  results: any[];
};

const WarningItem = ({ className = '', style, results }: WarningItemProps) => {
  const result = results.find(result => result.type === 'block_countdown');
  const title = result && result.data && result.data[0] && result.data[0].title;
  return (
    <Item style={style} className={className}>
      <Warning title={title} />
    </Item>
  );
};

export default WarningItem;
