import React from 'react';
import { Form, Input, Select, Button } from 'antd';
import { FormComponentProps } from 'antd/lib/form/Form';
import { compose } from 'redux';
import { connect } from 'react-redux';
import IconFont from '../../../components/ui/TradexIcon';
import _get from '../../../common/get';
import { AppStore } from '../../../store/reducers/index';
import styles from './VINForm.module.scss';

interface BasicProps {}

type IProps = BasicProps & FormComponentProps;

interface IState {}

class VINForm extends React.Component<IProps, IState> {
  container: any;

  generateID = () =>
    Math.random()
      .toFixed(8)
      .substring(2);

  addField = () => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    const nextKeys = keys.concat(this.generateID());
    form.setFieldsValue({
      keys: nextKeys
    });
  };

  deleteField = (k: number) => () => {
    const { form } = this.props;
    const keys = form.getFieldValue('keys');
    form.setFieldsValue({
      keys: keys.filter((key: number) => key !== k)
    });
  };

  handleSubmit = () => {
    const { form } = this.props;
    form.validateFieldsAndScroll(function(error, values) {
      if (error) {
        console.log(error);
        return;
      }

      console.log(values);
    });
  };

  render() {
    const { getFieldDecorator, getFieldsValue, getFieldValue } = this.props.form;
    console.log(getFieldsValue());
    getFieldDecorator('keys', { initialValue: [this.generateID()] });
    const keys = getFieldValue('keys');

    return (
      <div ref={re => (this.container = re)} className={styles.VehicleDetailForm}>
        {keys.map((k: any, index: number) => (
          <div className={styles.row} key={index}>
            <Form.Item label="VIN" required={true} className={styles.vinField}>
              {getFieldDecorator(`vins[${index}].vin`, {
                rules: [
                  {
                    required: true,
                    message: 'VIN is required!'
                  }
                ],
                validateTrigger: ['onChange', 'onBlur']
              })(<Input />)}
            </Form.Item>
            <div className={styles.fieldCombo}>
              <Form.Item label="Mileage" required={true} className={styles.mileageField}>
                {getFieldDecorator(`vins[${index}].mileage`, {
                  rules: [
                    {
                      required: true,
                      message: 'Mileage is required!'
                    }
                  ],
                  validateTrigger: ['onChange', 'onBlur']
                })(<Input placeholder="Number Input" />)}
              </Form.Item>
              <Form.Item label="unit" className={styles.inlineSelect}>
                {getFieldDecorator(`vins[${index}].unit`, {
                  initialValue: 'km',
                  rules: [
                    {
                      required: true,
                      message: 'VIN is required!'
                    }
                  ],
                  validateTrigger: ['onChange', 'onBlur']
                })(
                  <Select getPopupContainer={() => this.container}>
                    <Select.Option value="km">KM</Select.Option>
                    <Select.Option value="m">M</Select.Option>
                  </Select>
                )}
              </Form.Item>
            </div>
            <div className={styles.rowDelete} onClick={this.deleteField(k)}>
              Delete
            </div>
          </div>
        ))}

        <div className={styles.addFieldBtn} onClick={this.addField}>
          <IconFont type="iconicon_add_line_bold" />
          <span>Add More Vehicle</span>
        </div>

        <div className={styles.btnRow}>
          <Button
            type="primary"
            className={styles.nextBtn}
            disabled={keys.length === 0}
            onClick={this.handleSubmit}
          >
            Next
          </Button>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state: AppStore) => ({
  createCar: state.createCar
});

export default connect(mapStateToProps)(Form.create()(VINForm));
