import {
  CountTypes,
  ListStore,
  CountAction,
} from '../types'

const initialState: ListStore = {
  count: 5
};

export default (state = initialState, action: CountAction): ListStore => {
  switch (action.type) {
    case CountTypes.INCREASE_COUNT: {
      return {
        ...state,
        count: ++state.count
      };
    }
    case CountTypes.REDUCE_COUNT: {
      return {
        ...state,
        count: --state.count
      };
    }
    default:
      return state;
  }
};
