import React, { ReactElement, ReactHTML, ReactEventHandler } from 'react';

import styles from './index.module.scss';
import TradexIcon from '../ui/TradexIcon';
import Map from '../Map';
import util from '../../common/util';

type Coordinate = {
  lat: number;
  lng: number;
};

interface Props {
  title: string;
  subtitle: string;
  coordinate: Coordinate;
  onClose: Function;
}

class MapPop extends React.Component<Props> {
  constructor(props: Props) {
    super(props);
    util.lockScroll(styles.mask);
  }

  render() {
    const { title, subtitle, coordinate, onClose } = this.props;

    return (
      <div className={styles.mask}>
        <div className={styles.wrapper}>
          <div className={styles.head}>
            <div className={styles.title}>{title}</div>
            <div className={styles.subtitle}>{subtitle}</div>
            <TradexIcon
              type="iconicon_cancel"
              className={`${styles.icon_cancel} ${title ? '' : styles.out}`}
              onClick={onClose}
            />
          </div>
          <div className={styles.content}>
            <Map coordinate={coordinate} title={title} />
          </div>
        </div>
      </div>
    );
  }
}

export default MapPop;
