import React, { useState, useMemo, useEffect } from 'react';
import TradexIcon from '../ui/TradexIcon';
import styles from './index.module.scss';
import { GetAllPorts } from '../../api/garage';
import { DisplayItem, Port } from '../../types';

interface Props {
  garageId: string;
  country: DisplayItem;
  port: DisplayItem;
  onPortChange: (port: Port) => void;
}

interface ProvinceItem extends DisplayItem {
  ports: Port[];
}

export default function Cascader(props: Props) {
  const [show, setShow] = useState(false);
  const [countryList, setCountryList] = useState<ProvinceItem[]>([]);
  const [tabIdx, setTabIdx] = useState(0);
  const [countryKey, setCountryKey] = useState('');
  const portList: Port[] = useMemo(() => {
    const foundCountryItem = countryList.find(d => d.key === countryKey);
    return (foundCountryItem && foundCountryItem.ports) || [];
  }, [countryList, countryKey]);

  useEffect(() => {
    setCountryKey(props.country.key);
    GetAllPorts(props.garageId).then((data: any) => {
      setCountryList(data.data.results);
    });
  }, []);

  const countryClick = (province: ProvinceItem) => {
    setTabIdx(1);
    setCountryKey(province.key);
  };

  const portCick = (port: Port) => {
    props.onPortChange(port);
    setTabIdx(0);
    setShow(false);
  };
  return (
    <div
      className={styles.cascader}
      onMouseEnter={() => setShow(true)}
      onMouseLeave={() => setShow(false)}
    >
      <div className={`${styles.addr}`}>
        {props.country.display_value}, {props.port.display_value}
        <TradexIcon type="iconicon_dropdown_line" size={16} style={{ margin: '0 -9px 0 8px' }} />
      </div>
      {show && (
        <div className={styles.select}>
          <div className={styles.tab}>
            {['Country', 'Port/City'].map((d, idx) => (
              <div
                key={idx}
                className={`${styles.tabItem} ${
                  tabIdx > idx ? styles.ed : tabIdx === idx ? styles.active : ''
                }`}
                onClick={() => setTabIdx(idx)}
              >
                {d}
                <TradexIcon type="iconicon_dropdown_line" size={16} />
              </div>
            ))}
          </div>
          <div className={`${styles.section} prevent-scroll`}>
            {tabIdx === 0 &&
              countryList.length > 0 &&
              countryList.map((p, idx) => (
                <span
                  key={idx}
                  className={`${styles.selectItem} ${p.key === countryKey ? styles.active : ''}`}
                  onClick={countryClick.bind(null, p)}
                >
                  {p.display_value}
                </span>
              ))}
            {tabIdx === 1 &&
              portList.length > 0 &&
              portList.map((p, idx) => (
                <span
                  key={idx}
                  className={`${styles.selectItem} ${
                    props.port.display_value === p.name.display_value ? styles.active : ''
                  }`}
                  onClick={portCick.bind(null, p)}
                >
                  {p.name.display_value}
                </span>
              ))}
          </div>
        </div>
      )}
    </div>
  );
}
