/**
 * @description: hack checkbox
 * @param {IHackCheckBox}
 * @return {ReactNode}
 * @author zixiu
 */

import React, { SFC, memo } from 'react';
import IconFont from '../ui/TradexIcon';
import styles from './index.module.scss';

export interface IHackCheckBox {
  text: string;
  checked: boolean;
  onChange: any;
}

const HackCheckBox: SFC<IHackCheckBox> = ({ text, checked, onChange }) => {
  return (
    <label className={styles.hackCheckBoxContainer}>
      <span className={styles.textmark}>{text}</span>
      <input type="checkbox" checked={checked} onChange={onChange} />
      <span className={styles.checkmark}>
        {/* {!checked && <IconFont type="iconcheckbox_normal" className={styles.box} />} */}
        {checked && <IconFont type="iconicon_checkbox_area" className={styles.box} />}
      </span>
    </label>
  );
};

export default memo(HackCheckBox);
