export function getDate(data?: any): Date {
  return data ? new Date(parseInt(data)) : new Date();
}

export function toStr(num: number): any {
  const str = String(num);
  if (str[1]) return str;
  return '0' + str;
}

export function getYmdDate(data?: any): Array<string | number> {
  const argDate = data ? getDate(data) : getDate();
  const year = argDate.getFullYear();
  const month = toStr(argDate.getMonth() + 1);
  const day = toStr(argDate.getDate());
  const hour = toStr(argDate.getHours());
  const minutes = toStr(argDate.getMinutes());
  return [year, month, day, hour, minutes];
}

export function showDate(data: string): string {
  const now = new Date();
  const argDate = getDate(data);
  const [argYear, argMonth, argDay, argHour, argMinutes] = getYmdDate(data);
  const [nowYear, nowMonth, nowDay, nowHour, nowMinutes] = getYmdDate();
  if (argYear === nowYear) {
    if (argMonth === nowMonth) {
      if (argDay === nowDay) {
        return argHour + ':' + argMinutes;
      }
      if (Number(nowDay) - Number(argDay) > 1) {
        return argMonth + '-' + argDay;
      }
      return 'Yesterday';
    }
    return argMonth + '-' + argDay;
  }
  return argYear + '-' + argMonth + '-' + argDay;
}
