import React, { ReactElement, ReactHTML, ReactEventHandler } from 'react';
import styles from './index.module.scss';
import TradexIcon from '../../ui/TradexIcon';
import util from '../../../common/util';

interface Props {
  title?: string;
  children: ReactElement;
  onClose: () => void;
}

export default class Mask extends React.Component<Props, object> {
  contentClick = (e: React.MouseEvent<HTMLDivElement>) => {
    e.stopPropagation();
  };

  componentDidMount() {
    util.lockScroll(styles.mask);
  }

  render() {
    const { title } = this.props;
    return (
      <div className={styles.mask} onClick={this.props.onClose}>
        <div className={styles.wrapper}>
          <TradexIcon
            type="iconicon_cancel"
            className={`${styles.icon_cancel} ${title ? '' : styles.out}`}
            onClick={this.props.onClose}
          />
          {title && <div className={styles.head}>{title}</div>}
          <div className={styles.content} onClick={this.contentClick}>
            {this.props.children}
          </div>
        </div>
      </div>
    );
  }
}
