import React, { SFC, MouseEvent, ReactNode } from 'react';
import { Spin, Input } from 'antd';
import IconFont from '../ui/TradexIcon';
import _get from '../../common/get';
import HackCheckBox from '../CheckBox';
import RadioBtn from '../RadioBtn';
import { Notification } from '../Notification';
import { compose } from 'redux';
import styles from './index.module.scss';
import { getOrdeMakes, getOrdermonth, getOrderStatus } from '../../api/order';

const clean = (obj: any): any =>
  Object.entries(obj).reduce((prev: any, [k, v]) => {
    if (v === null || v === undefined || v === '' || v === []) return prev;
    prev[k] = v;
    return prev;
  }, {});

interface IProps extends SFC {
  total: number;
  loading: boolean;
  onChange: any;
  onSearch: any;
  searchValue: any;
}

interface IMakeItem {
  name: string;
  count: number;
  make: {
    key: string;
    display_value: string;
  };
  make_logo: string;
  checked: boolean;
}

interface IMake {
  name: string;
  results: IMakeItem[];
}

interface IMonth {
  name: string;
  year: string;
  count: number;
  display_value: string;
  checked: boolean;
}

interface IOrderStatus {
  display: string;
  statuses: string;
  checked: boolean;
}

type _carType = 'All' | 'New' | 'Used';
type _orderType = 'All' | 'Market' | 'Instant Request';

const carType_group: Record<'key' | 'value', _carType>[] = [
  { key: 'All', value: 'All' },
  { key: 'New', value: 'New' },
  { key: 'Used', value: 'Used' }
];
const orderType_group: Record<'key' | 'value', _orderType>[] = [
  { key: 'All', value: 'All' },
  { key: 'Market', value: 'Market' },
  { key: 'Instant Request', value: 'Instant Request' }
];

interface IState {
  filterMenuShow: boolean;
  orderAllAtatus: IOrderStatus[];
  monthes: IMonth[];
  makes: IMake[];
  carType: _carType;
  orderType: _orderType;
  conditions: IConditionItem[];
  search_txt: string;
  loading: boolean;
}

const nowYear = new Date().getFullYear();
const _priceRange = [2600, 500000];
const _yearRange = [nowYear - 20, nowYear + 2];
const _mileageRange = [0, 500];
const _readyToShipRange = [0, 60];

const initialState = {
  filterMenuShow: false,
  orderAllAtatus: [],
  monthes: [],
  makes: [],
  carType: 'All' as _carType,
  orderType: 'All' as _orderType,
  conditions: [],
  search_txt: '',
  loading: true
};

enum mapStateKeyToConditionsName {
  OfferAcceptedMonth = 'Offer Accepted Month',
  orderStatus = 'Order Status',
  makes = 'Make',
  carType = 'Car Type',
  orderType = 'Order Type'
}

type TConditions = 'Offer Accepted Month' | 'Order Status' | 'Car Type' | 'Order Type' | 'Make';

interface IConditionItem {
  type: TConditions;
  desc: string | ReactNode;
  onClose: any;
}

const ConditionItem: SFC<IConditionItem> = ({ type, desc, onClose }) => {
  return (
    <div className={styles.item}>
      <span className={styles.type}>{type}</span>
      <span className={styles.desc}>{desc}</span>
      <span className={styles.close} onClick={onClose}>
        <IconFont type="iconicon_cancel_small" />
      </span>
    </div>
  );
};

class OrderFilter extends React.PureComponent<IProps, IState> {
  state = initialState;
  private filter_menu_ref = React.createRef<HTMLDivElement>();

  toggleFilterMenu = () => {
    this.setState((prev: IState) => ({ filterMenuShow: !prev.filterMenuShow }));
  };

  onFilterMouseOver = (): void => {
    if (this.state.filterMenuShow) return;
    this.setState({ filterMenuShow: true });
  };

  onFilterMouseLeave = (): void => {
    if (!this.state.filterMenuShow) return;
    this.setState({ filterMenuShow: false });
  };

  onMonthChange = (item: IMonth) => () => {
    this.setState(
      (prev: IState) => ({
        monthes: prev.monthes.map((v: IMonth) =>
          v.display_value === item.display_value ? { ...v, checked: !v.checked } : v
        ),
        conditions: item.checked
          ? prev.conditions.filter((v: IConditionItem) =>
              v.type === 'Offer Accepted Month' && v.desc === item.display_value ? false : true
            )
          : prev.conditions.concat({
              type: 'Offer Accepted Month',
              desc: item.display_value,
              onClose: () => this.onMonthChange({ ...item, checked: true })()
            })
      }),
      () => this.updateFilter()
    );
  };

  orderStatusChange = (item: IOrderStatus) => () => {
    this.setState(
      (prev: IState) => ({
        orderAllAtatus: prev.orderAllAtatus.map((v: IOrderStatus) =>
          v.display === item.display ? { ...v, checked: !v.checked } : v
        ),
        conditions: item.checked
          ? prev.conditions.filter((v: IConditionItem) =>
              v.type === 'Order Status' && v.desc === item.display ? false : true
            )
          : prev.conditions.concat({
              type: 'Order Status',
              desc: item.display,
              onClose: () => this.orderStatusChange({ ...item, checked: true })()
            })
      }),
      () => this.updateFilter()
    );
  };

  onMakeChange = (item: IMakeItem) => () => {
    const isChecked = item.checked;
    this.setState(
      (prev: IState) => {
        const next_makes = prev.makes;

        next_makes.map((v: IMake) =>
          v.results.map((it: IMakeItem) => {
            // return it.make.key === item.make.key ? {...it, checked: !it.checked} : it
            if (it.make.key === item.make.key) {
              it.checked = !it.checked;
            }
            return it;
          })
        );

        // next_makes.find((v: IMake) =>
        //     v.results.find((it: IMakeItem) => {
        //         if (it.make.key === item.make.key && it.name === item.name) {
        //             it.checked = !it.checked;
        //             return true;
        //         }
        //         return false;
        //     })
        // );

        return {
          makes: next_makes,
          conditions: isChecked
            ? prev.conditions.filter((v: IConditionItem) =>
                v.type === 'Make' && v.desc === item.make.display_value ? false : true
              )
            : prev.conditions.concat({
                type: 'Make',
                desc: item.make.display_value,
                onClose: () => this.onMakeChange({ ...item, checked: true })()
              })
        };
      },
      () => this.updateFilter()
    );
  };

  onRadioChange = (key: 'carType' | 'orderType') => (e: React.ChangeEvent<HTMLInputElement>) => {
    this.setState(
      (prev: IState): IState => {
        const _next = e.target.value;
        const _key = mapStateKeyToConditionsName[key];
        let _conditions = prev.conditions.slice();
        const index = _conditions.findIndex((v: IConditionItem) => v.type === _key);
        if (_next === 'All') {
          _conditions = _conditions.filter((v: IConditionItem) => v.type !== _key);
        }
        if (_next !== 'All') {
          index === -1
            ? _conditions.push({
                type: _key as TConditions,
                desc: `${_next}`,
                onClose: () =>
                  this.setState(
                    (prev: IState) => ({
                      ...prev,
                      [key]: initialState[key],
                      conditions: prev.conditions.filter(v => v.type !== _key)
                    }),
                    () => this.updateFilter()
                  )
              })
            : (_conditions[index].desc = `${_next}`);
        }

        return {
          ...prev,
          [key]: _next,
          conditions: _conditions
        };
      },
      () => this.updateFilter()
    );
  };

  getOrdermonth = async () => {
    try {
      const { data, status } = await getOrdermonth();
      const res = _get(data, ['results']);
      var t: IMonth[] = [];
      res.map((item: any) =>
        item.results.map((ii: IMonth) => {
          t.push({
            ...ii,
            display_value: ii.display_value + ' ' + item.year,
            year: item.year,
            checked: false
          });
        })
      );
      // console.log('t',t)
      status === 200 &&
        res &&
        this.setState({
          // monthes: res.map((v: IMonth[]) => (v.map((vv:IMonth)=>({ ...vv, checked: false }))))
          monthes: t
        });
    } catch (error) {
      Notification({
        type: 'error',
        message: 'Get menu config error',
        description: _get(error, ['response', 'data', 'detail'])
      });
    }
  };

  getOrderStatus = async () => {
    try {
      const { data, status } = await getOrderStatus();
      const res = _get(data, ['results']);
      status === 200 &&
        res &&
        this.setState({
          orderAllAtatus: res.map((v: IOrderStatus) => ({ ...v, checked: false }))
        });
    } catch (error) {
      Notification({
        type: 'error',
        message: 'Get menu config error',
        description: _get(error, ['response', 'data', 'detail'])
      });
    }
  };

  getOrdeMakes = async () => {
    try {
      const { data, status } = await getOrdeMakes();
      const res = _get(data, ['results']);
      status === 200 &&
        res &&
        this.setState({
          makes: res
            .map((v: IMake) => ({
              ...v,
              results: v.results.map((re: IMakeItem) => ({
                ...re,
                name: v.name,
                checked: false
              }))
            }))
            .filter((item: IMake) => item.results[0].make_logo)
        });
    } catch (error) {
      Notification({
        type: 'error',
        message: 'Get menu config error',
        description: _get(error, ['response', 'data', 'detail'])
      });
    }
  };

  setDynamicHeight = () => {
    const main = document.getElementById('basic_layout_main');
    const filter_bar = this.filter_menu_ref.current;
    if (main && filter_bar) {
      const _target = (filter_bar as HTMLDivElement).clientHeight - 45 - 45 - 12 + 398;
      (main as any).style.minHeight = `${_target}px`;
    }
  };

  getConditions = () => {
    const { orderAllAtatus, carType, orderType, monthes } = this.state;
    const _query = {
      status:
        this.checked_orderStatus.length > 0
          ? this.checked_orderStatus
              .reduce((prev: string, curr: IOrderStatus) => `${prev},${curr.statuses}`, '')
              .substr(1)
          : null,
      make:
        this.checked_makes.length > 0
          ? this.checked_makes
              .reduce((prev: string, curr: IMakeItem) => `${prev},${curr.make.key}`, '')
              .substr(1)
          : null,
      car_type: carType == 'All' ? 0 : carType == 'New' ? 1 : 2,
      order_type: orderType == 'All' ? 0 : orderType == 'Market' ? 1 : 2,
      month:
        this.checked_months.length > 0
          ? this.checked_months
              .reduce((prev: string, curr: IMonth) => `${prev},${curr.name}`, '')
              .substr(1)
          : null
    };

    // console.log('__query',_query)
    const newQuery = clean(_query);

    return newQuery;
  };

  get checked_orderStatus() {
    return this.state.orderAllAtatus.filter((v: IOrderStatus) => v.checked);
  }

  get checked_months() {
    return this.state.monthes.filter((v: IMonth) => v.checked);
  }

  get checked_makes() {
    // return this.state.makes.reduce(
    //     (prev: IMakeItem[], curr: IMake) =>
    //         prev.concat(curr.results.filter((it: IMakeItem) => it.checked)),
    //     []
    // );

    return this.state.makes
      .reduce(
        (prev: IMakeItem[], curr: IMake) =>
          prev.concat(curr.results.filter((it: IMakeItem) => it.checked)),
        []
      )
      .reduce((prev: IMakeItem[], curr: IMakeItem) => {
        return prev.find((v: IMakeItem) => v.make.key === curr.make.key) ? prev : prev.concat(curr);
      }, []);
  }

  updateFilter = () =>
    compose(
      () => this.setState({ search_txt: '' }),
      this.props.onChange,
      this.getConditions
    )();

  componentDidUpdate(prevProps: IProps) {
    if (this.props.searchValue !== prevProps.searchValue) {
      this.setState({ search_txt: this.props.searchValue });
    }
    this.setDynamicHeight();
  }

  async componentDidMount() {
    this.setDynamicHeight();
    await Promise.all([this.getOrdeMakes(), this.getOrdermonth(), this.getOrderStatus()]).then(
      res => {
        // console.log('res', res)
        this.setState({ loading: false });
      }
    );
  }

  inputChange = (e: any) => {
    this.setState({
      search_txt: e.target.value
    });
  };

  handleEnterKey = (e: any) => {
    const {
      search_txt,
      orderAllAtatus,
      monthes,
      makes,
      carType,
      orderType,
      conditions
    } = this.state;
    // console.log('handleEnterKey ->', e)
    // if(search_txt !== ''){
    const _orderAllAtatus = orderAllAtatus.map((item: IOrderStatus) => ({
      ...item,
      checked: false
    }));
    const _monthes = monthes.map((item: IMonth) => ({ ...item, checked: false }));
    const _makes = makes.map((item: IMake) => ({
      ...item,
      results: item.results.map((IMakeItem: IMakeItem) => ({ ...IMakeItem, checked: false }))
    }));
    const _conditions: IConditionItem[] = [];
    this.setState({
      filterMenuShow: false,
      orderAllAtatus: _orderAllAtatus,
      monthes: _monthes,
      makes: _makes,
      conditions: _conditions,
      carType: 'All' as _carType,
      orderType: 'All' as _orderType
    });
    this.props.onSearch(search_txt.trim());
    // }
  };

  render() {
    const { total } = this.props;
    const {
      filterMenuShow,
      orderAllAtatus,
      makes,
      orderType,
      carType,
      monthes,
      conditions,
      search_txt,
      loading
    } = this.state;

    const filterMenu = (
      <div className={styles.filterMenuWrap}>
        <div
          className={`${styles.filterMenu} 
          ${filterMenuShow ? styles.filterShow : ''}
          `}
          ref={this.filter_menu_ref}
          // onMouseOver={this.onFilterMouseOver}
          // onMouseLeave={this.onFilterMouseLeave}
        >
          <div className={styles.title} onClick={this.toggleFilterMenu}>
            <span className={styles.symbol}></span>
            <span>Filters By</span>
            <IconFont type="iconicon_flod_square" className={styles.titleIcon} />
          </div>

          {/* order Status */}
          <div className={styles.subMenuArea}>
            <div className={styles.menu}>
              <span className={styles.menuTitle}>Order Status</span>
              <IconFont type="iconicon_rightarrow" className={styles.arrowIcon} />
            </div>

            <ul className={styles.menuList}>
              {orderAllAtatus.map((v: IOrderStatus, i: number) => (
                <li key={i}>
                  <HackCheckBox
                    text={v.display}
                    checked={v.checked}
                    onChange={this.orderStatusChange(v)}
                  />
                </li>
              ))}
            </ul>

            <ul className={styles.menuListChosen}>
              {this.checked_orderStatus.map((v: IOrderStatus, i: number) => (
                <li key={i}>
                  <span className={styles.tag} key={i}>
                    {v.display}
                  </span>{' '}
                  <IconFont type="iconicon_cancel" onClick={this.orderStatusChange(v)} />
                </li>
              ))}
            </ul>
          </div>

          {/*Car Type*/}
          <div className={styles.radioArea}>
            <div className={styles.menuTitle}>Car Type</div>
            <div className={styles.btnGroup}>
              <RadioBtn
                all={carType_group}
                value={carType}
                onChange={this.onRadioChange('carType')}
              />
            </div>
          </div>

          {/* Order Type*/}
          <div className={styles.radioArea}>
            <div className={styles.menuTitle}>Order Type</div>
            <div className={styles.btnGroup}>
              <RadioBtn
                all={orderType_group}
                value={orderType}
                onChange={this.onRadioChange('orderType')}
              />
            </div>
          </div>

          {/* makes */}
          <div className={styles.subMenuArea}>
            <div className={styles.menu}>
              <span className={styles.menuTitle}>Make</span>
              <IconFont type="iconicon_rightarrow" className={styles.arrowIcon} />
            </div>

            <ul className={`${styles.menuList} ${styles.menuListMakes}`}>
              {makes.length
                ? makes.map((v: IMake) => {
                    return (
                      <li key={v.name} className={styles.makeLi}>
                        <span className={styles.alpha}>{v.name}</span>
                        {v.results &&
                          v.results.length &&
                          v.results.map((it: IMakeItem, i: number) => (
                            <div className={styles.makeItem} key={i}>
                              <img className={styles.makeItemLogo} src={it.make_logo} />
                              <HackCheckBox
                                text={it.make && it.make.display_value}
                                checked={it.checked}
                                onChange={this.onMakeChange(it)}
                              />
                              <span className={styles.makeItemCount}>{it.count}</span>
                            </div>
                          ))}
                      </li>
                    );
                  })
                : null}
            </ul>

            <ul className={styles.menuListChosen}>
              {this.checked_makes.map((v: IMakeItem, i: number) => (
                <li key={i}>
                  <span className={styles.tag} key={v.make.key + i}>
                    {v.make.display_value}
                  </span>{' '}
                  <IconFont
                    type="iconicon_cancel"
                    // onClick={this.deleteCheckedMake(v)}
                    onClick={this.onMakeChange(v)}
                  />
                </li>
              ))}
            </ul>
          </div>

          {/* Offer Accepted Month */}
          <div className={styles.subMenuArea}>
            <div className={styles.menu}>
              <span className={styles.menuTitle}>Offer Accepted Month</span>
              <IconFont type="iconicon_rightarrow" className={styles.arrowIcon} />
            </div>

            <ul className={styles.menuList}>
              {monthes.map((v: IMonth, i: number) => (
                <li key={i}>
                  <HackCheckBox
                    text={v.display_value}
                    checked={v.checked}
                    onChange={this.onMonthChange(v)}
                  />
                </li>
              ))}
            </ul>

            <ul className={styles.menuListChosen}>
              {this.checked_months.map((v: IMonth, i: number) => (
                <li key={i}>
                  <span className={styles.tag} key={v.display_value + i}>
                    {v.display_value}
                  </span>{' '}
                  <IconFont type="iconicon_cancel" onClick={this.onMonthChange(v)} />
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    );

    return (
      <div className={styles.container}>
        <Spin spinning={loading} style={{ width: 1200, height: 60 }}>
          <div className={styles.FilterBar}>
            <div className={styles.left} onClick={this.toggleFilterMenu}>
              <IconFont type="iconicon_filter_vertical" className={styles.bigFilterIcon} />
              <span className={styles.filterTitle}>Filters</span>
              <IconFont type="iconicon_dropdown_square" className={styles.statusIcon} />
            </div>

            <div className={styles.right}>
              <Input
                className={styles.searcInput}
                onChange={this.inputChange}
                onPressEnter={this.handleEnterKey}
                value={search_txt}
                placeholder={'Search by Name/Make/VIN/Order ID/Seller Memo'}
                prefix={
                  <IconFont
                    type="iconicon_search"
                    color="#5c6166"
                    size={16}
                    className={styles.SearchIcon}
                  />
                }
              />
              <span className={styles.filterRes}>
                {total} {total > 1 ? ' orders' : ' order'} found
              </span>
            </div>
          </div>

          <div className={styles.menuDisplay}>{filterMenu}</div>

          <div className={styles.conditions}>
            {conditions.map(
              (v: IConditionItem, i: number): ReactNode => (
                <ConditionItem key={i} type={v.type} desc={v.desc} onClose={v.onClose} />
              )
            )}
          </div>
        </Spin>
      </div>
    );
  }
}

export default OrderFilter;
