import React from 'react';
import { Row, Input } from 'antd';
import { CardWithTitle } from '../Card';
import { Descriptions } from '../Descriptions';
import { FormattedMessage } from 'react-intl';
import styles from '../index.module.scss';

type Props = any;
type State = any;
export class AdditionalClauses extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      defaultStyle: {
        resize: 'none',
        fontFamily: 'SFProText',
        fontSize: 14,
        lineHeight: 1.5,
        color: '#8a9199',
        backgroundColor: '#f7f7f7'
      },
      style: {
        resize: 'none',
        fontFamily: 'SFProText',
        fontSize: 14,
        lineHeight: 1.5,
        color: '#8a9199',
        backgroundColor: '#f7f7f7'
      },
      onFocusStyle: {
        color: '#001a33',
        fontWeight: 500
      },
      defaultValue: 'Optional, input clauses'
    };
  }
  onFocus() {
    let { defaultStyle, onFocusStyle } = this.state;
    if (!this.props.value) {
      let style = { ...defaultStyle, ...onFocusStyle };
      this.setState({ ...this.state, style, defaultValue: '' });
    }
  }
  onBlur() {
    let { defaultStyle } = this.state;
    if (!this.props.value) {
      let style = { ...defaultStyle };
      this.setState({
        ...this.state,
        style,
        defaultValue: 'Optional, input clauses'
      });
    }
  }

  title = (
    <div className={styles.title}>
      <FormattedMessage
        id="makeOffer_additionalClauses_title"
        defaultMessage="Additional Clauses"
      />
    </div>
  );

  render() {
    let { defaultValue, style, className = '' } = this.state;
    let { additionalChange, ...restProps } = this.props;
    return (
      <CardWithTitle {...restProps}>
        <Row className={`${styles.cardHead} ${styles.row}`}>
          <FormattedMessage
            id="makeOffer_additionalClauses_title"
            defaultMessage="Additional Clauses"
          />
        </Row>
        <Row className={styles.row}>
          <Input.TextArea
            style={style}
            placeholder={defaultValue}
            value={this.props.value}
            onFocus={() => this.onFocus()}
            onBlur={() => this.onBlur()}
            onChange={(ev: any) => additionalChange(ev.target.value)}
            rows={3}
          />
        </Row>
      </CardWithTitle>
    );
    // return (
    //   <Descriptions title={this.title}>
    //     <Descriptions.Row>
    //       <div className={styles.name}>
    //         <FormattedMessage
    //           id="makeOffer_additionalClauses_title"
    //           defaultMessage="Additional Clauses"
    //         />
    //       </div>
    //       <div className={styles.value}>
    //         <Input.TextArea
    //           style={style}
    //           placeholder={defaultValue}
    //           value={this.props.value}
    //           onFocus={() => this.onFocus()}
    //           onBlur={() => this.onBlur()}
    //           onChange={(ev: any) => additionalChange(ev.target.value)}
    //           rows={3}
    //         />
    //       </div>
    //     </Descriptions.Row>
    //   </Descriptions>
    // )
  }
}
