import React from 'react';
import { Button } from 'antd';
import { copyTextToClipboard } from '../../../../../common/copyToClipboard';
import IconFont from '../../../../../components/ui/TradexIcon';

type CopyButtonProps = {
  style?: React.CSSProperties;
  className?: any;
  text?: string;
  onSuccess?: () => any;
  onFailure?: () => any;
};

const CopyButton = (props: CopyButtonProps) => {
  const { style, className = '', onSuccess, onFailure, text = '', ...restProps } = props;
  const copy = () => {
    const result = copyTextToClipboard(text);
    result ? onSuccess && onSuccess() : onFailure && onFailure();
  };
  return (
    <Button onClick={copy} style={style} className={className}>
      <IconFont type="iconicon_copy_line" />
    </Button>
  );
};

export default CopyButton;
