import _get from '../../../../../common/get';

// 
export const priceToNumberStr = (price: string): string =>
  price.replace(/\s/gi, '').replace(/,/gi, '');
// 
export const isNumberStr = (numberStr: string): boolean => {
  const reg = /^-?(0|[1-9][0-9]*)(\.[0-9]*)?$/;
  return !isNaN(Number(numberStr)) && reg.test(numberStr);
};
// 
export const isValidPrice = (price: string) => {
  const priceStr = priceToNumberStr(price);
  return isNumberStr(priceStr);
};
// tracker
export const getTrackerDetails = (
  { trackers }: { trackers: Array<any> },
  trackType: number
): Array<any> => {
  let payDepositTracker = trackers.find(tracker => tracker['track_type'] === trackType);
  let payDepositDetails: Array<any> = payDepositTracker['details'] || [];
  return payDepositDetails;
};
// 
export const countdownGenerator = (perTrigger: any, endTrigger: any) => {
  let clear: any;
  const timer = (time: number = 10, step: number = 1000) => {
    let cur = time;
    if (cur > 0) {
      clear && clearTimeout(clear);
      perTrigger && perTrigger(cur);
      clear = setTimeout(() => timer(cur - 1, step), step);
    } else {
      clear && clearTimeout(clear);
      endTrigger && endTrigger(cur);
    }
  };
  return timer;
};

// CA->CA
export const isCAtoCA = (port1: { city_location: any }, port2: { city_location: any }): boolean => {
  const country1 = _get(port1, ['city_location', 'country', 'key']);
  const country2 = _get(port2, ['city_location', 'country', 'key']);
  return country1 === 'CA' && country2 === 'CA';
};

export const rorate = (perTrigger: any, endTrigger: any, endTime: number) => {
  let clear: any;
  const timer = (time: number = 0) => {
    let cur = time;
    if (cur < endTime) {
      clear && clearTimeout(clear);
      perTrigger && perTrigger((cur / endTime) * 100);
      clear = setTimeout(() => timer(cur + 30), 30);
    } else {
      clear && clearTimeout(clear);
      endTrigger && endTrigger((cur / endTime) * 100);
    }
  };
  return timer();
};

export const isEFT = (results: any[]) => {
  const result = results.find(result => result.type === 'block_ops');
  const type = result && result.data && result.data[0] && result.data[0].type;
  return type === '2';
};

export const getNote = (results: any[]) => {
  const result = results.find(result => result.type === 'block_notes');
  if (result) {
    return result && result.data && result.data.length && result.data[0] && result.data[0].title;
  } else {
    return '';
  }
};
