import React, { ReactElement } from 'react';
import styles from './index.module.scss';
import '../../../assets/style/common.scss';
import TradexIcon from '../../../components/ui/TradexIcon';

const WrapperSplit = (props: { className: string; children: ReactElement }) => (
  <div className={`${styles['wrapper-split']} ${styles[props.className]}`}>{props.children}</div>
);

const InfoItem = (props: {
  itemKey?: string;
  itemValue?: string | number;
  children?: any;
  className?: 'split2' | 'split3';
  itemKeyClass?: string;
  iconType?: string;
}): ReactElement => {
  const { itemKey, itemValue, children, className = 'split2', itemKeyClass, iconType } = props;
  const isError = iconType === 'icon_fork_line_round';
  return (
    <WrapperSplit className={className}>
      <div className={styles['info-item']}>
        {iconType && (
          <TradexIcon type={`icon${iconType}`} size={14} color={isError ? '#f2565f' : '#3bcdfe'} />
        )}
        <span className={`${styles.key} font-gray ${itemKeyClass}`}>{itemKey}</span>
        {children || (
          <span className={`${styles.value} bold ${isError ? 'red' : ''}`}>{itemValue}</span>
        )}
      </div>
    </WrapperSplit>
  );
};

const TabItemHead = (props: { iconType: string; text: string; id?: string }): ReactElement => {
  const { iconType, text } = props;
  return (
    <>
      <div className={styles['item-head']} id={props.id}>
        <TradexIcon type={`icon${iconType}`} size={20} color="#3bcdfe" />
        <span className={styles['item-title']}> {text}</span>
      </div>
      <div className="hr-solid"></div>
    </>
  );
};

const DisclosureItem = (props: {
  iconType: string;
  title: string;
  children?: any;
  style?: React.CSSProperties;
}): ReactElement => {
  const { iconType, title, children, style } = props;
  return (
    <div className={styles.disclosure} style={style || {}}>
      <div className={styles['disclosure-title']}>
        <TradexIcon type={`icon${iconType}`} size={20} />
        <span>{title}</span>
      </div>
      <div className={styles['disclosure-content']}>{children}</div>
    </div>
  );
};

const TabItemSubHead = (props: { text: string; hideLine?: boolean }): ReactElement => {
  return (
    <div className={styles['item-sub-head']}>
      <p>{props.text}</p>
      {!props.hideLine && <div className="hr-dashed"></div>}
    </div>
  );
};

export { WrapperSplit, InfoItem, TabItemHead, DisclosureItem, TabItemSubHead };
