import React from 'react';
import { Icon } from 'antd';

export default function TradexIcon(props: { size?: number; color?: string; [index: string]: any }) {
  const { size, color } = props,
    extraStyle: any = {},
    Com = Icon.createFromIconfontCN({
      scriptUrl: '//at.alicdn.com/t/font_1358120_6gkk7117mn7.js'
    });

  if (size) {
    extraStyle.fontSize = `${size}px`;
  }

  if (color) {
    extraStyle.color = color;
  }

  return <Com {...props} style={Object.assign({}, props.style, extraStyle)} />;
}
