import React, { useState, useEffect } from 'react';
import { connect } from 'react-redux';
import { Input as AntdInput, Spin as AntdSpin, Select as AntdSelect } from 'antd';
import IconFont from '../../../../components/ui/TradexIcon';
import Button from '../../../../components/Btn';
import LottieIcon from '../../../../components/Lottie/LottieIcon';
import _get from '../../../../common/get';
import { decode, getOptions } from '../../../../api/createCar';
import styles from './index.module.scss';
import sectionStyles from './section.module.scss';
import spinStyles from './spin.module.scss';
import inputStyles from './input.module.scss';
import { AppStore } from '../../../../store/reducers/index';
import { SetCreateCarInfo } from '../../../../store/actions/createCar';

interface Props {
  className?: string;
}

type Car = {
  vin: string;
  distance_to_warehouse: string;
  distance_unit: string;
};

const mapStateToPros = (state: AppStore) => ({
  createCar: state.createCar
});
const mapDispatchToProps = (dispatch: any) => ({
  setCreateCarInfo: (carInfo: any) => dispatch(SetCreateCarInfo(carInfo))
});
interface BasicInfoProps extends Props {
  createCar: AppStore['createCar'];
  setCreateCarInfo: (payload: any) => void;
}
interface Color {
  value: Array<{ rgb: string; description: string }>;
}

const BasicInfo = (props: BasicInfoProps) => {
  const { className, createCar, setCreateCarInfo } = props;
  const { specification_location: country, cars } = createCar;
  const vin = cars.map(car => car.vin)[0] || '';
  const [spining, setSpining] = useState(true);
  const [exteriorColor, setExteriorColor] = useState({ value: [] } as Color);
  const [interiorColor, setInteriorColor] = useState({ value: [] } as Color);
  const indicator = (
    <LottieIcon
      style={{ width: '50px', height: '50px' }}
      animationData={require('../../../../assets/lottie/smileface.json')}
    />
  );
  const getBasicOptions = async () => {
    if (country && vin) {
      const response = await decode({ vin, country });
      const results = _get(response, 'data.results');
      const trimId = _get(results, 'trim_id');
      const payload = {
        make_name: _get(results, 'make_name.key'),
        model_year: _get(results, 'model_year'),
        model_name: _get(results, 'model_name.key'),
        trim_name: _get(results, 'trim_name.key'),
        engine_type: _get(results, 'engine_type.key'),
        displacement: _get(results, 'displacement'),
        fuel_type: _get(results, 'fuel_type.key'),
        body: _get(results, 'body.key'),
        door_number: _get(results, 'door_number'),
        l_or_r: _get(results, 'l_or_r.key')
      };
      const optionsReponse = await getOptions(trimId);
      const optionsResults = _get(optionsReponse, 'data.results') || [];
      setExteriorColor(optionsResults.find((ele: any) => ele.key === 'PRIMARY PAINT'));
      setInteriorColor(optionsResults.find((ele: any) => ele.key === 'INTERIOR COLOR'));
      setCreateCarInfo(payload);
      setSpining(false);
    }
  };
  useEffect(() => {
    getBasicOptions();
  }, []);

  return (
    <div className={`${styles.container} ${className}`}>
      <Spin spinning={spining} indicator={indicator}>
        <div className={`${styles.item}`}>
          <Section className={styles.section} required={true} title="Make">
            <Input className={styles.input} value={_get(createCar, 'make_name') || ''} />
          </Section>
          <Section className={styles.section} required={true} title="Model Year">
            <Input className={styles.input} value={_get(createCar, 'model_year') || ''} />
          </Section>
        </div>
        <div className={`${styles.item}`}>
          <Section className={styles.section} required={true} title="Model">
            <Input className={styles.input} value={_get(createCar, 'model_name') || ''} />
          </Section>
          <Section className={styles.section} required={true} title="Trim">
            <Input className={styles.input} value={_get(createCar, 'trim_name') || ''} />
          </Section>
        </div>
        <div className={`${styles.item}`}>
          <Section className={styles.section} required={true} title="Exterior Color">
            <AntdSelect
              className={`${styles.select}`}
              dropdownClassName={styles.dropdown}
              optionLabelProp="backfill"
              onChange={() => {}}
            >
              {exteriorColor.value.map(option => {
                const dotStyle = {
                  backgroundColor: `#${option.rgb}`,
                  borderRadius: `50%`,
                  width: `12px`,
                  height: `12px`
                };
                const backfill = (
                  <div className={styles.backfill}>
                    <div style={dotStyle} className={`${styles.dot}`}></div>
                    <div className={`${styles.description}`}>{option.description}</div>
                  </div>
                );
                const optionProps = {
                  value: option.rgb,
                  backfill
                } as any;
                return (
                  <AntdSelect.Option {...optionProps} className={`${styles.selectOption}`}>
                    <div style={dotStyle} className={`${styles.dot}`}></div>
                    <div className={`${styles.description}`}>{option.description}</div>
                  </AntdSelect.Option>
                );
              })}
            </AntdSelect>
          </Section>
          <Section className={styles.section} required={true} title="Interior Color">
            <AntdSelect
              className={`${styles.select}`}
              dropdownClassName={styles.dropdown}
              optionLabelProp="backfill"
              onChange={() => {}}
            >
              {interiorColor.value.map(option => {
                const dotStyle = {
                  backgroundColor: `#${option.rgb}`,
                  borderRadius: `50%`,
                  width: `12px`,
                  height: `12px`
                };
                const backfill = (
                  <div className={styles.backfill}>
                    <div style={dotStyle} className={`${styles.dot}`}></div>
                    <div className={`${styles.description}`}>{option.description}</div>
                  </div>
                );
                const optionProps = {
                  value: option.rgb,
                  backfill
                } as any;
                return (
                  <AntdSelect.Option {...optionProps} className={`${styles.selectOption}`}>
                    <div style={dotStyle} className={`${styles.dot}`}></div>
                    <div className={`${styles.description}`}>{option.description}</div>
                  </AntdSelect.Option>
                );
              })}
            </AntdSelect>
          </Section>
        </div>
        <div className={`${styles.item}`}>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Engine Type"
          >
            <Input className={styles.input} value={_get(createCar, 'engine_type') || ''} />
          </Section>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Engine Size"
          >
            <Input className={styles.input} value={_get(createCar, 'displacement') || ''} />
          </Section>
        </div>
        <div className={`${styles.item}`}>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Fuel Type"
          >
            <Input className={styles.input} value={_get(createCar, 'fuel_type') || ''} />
          </Section>
          <Section className={`${styles.section} ${styles.required}`} required={false} title="Body">
            <Input className={styles.input} />
          </Section>
        </div>
        <div className={`${styles.item}`}>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Door Number"
          >
            <Input className={styles.input} value={_get(createCar, 'door_number') || ''} />
          </Section>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Steering Position"
          >
            <Input className={styles.input} value={_get(createCar, 'l_or_r') || ''} />
          </Section>
        </div>
        <div className={`${styles.item}`}>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Transmission"
          >
            <Input className={styles.input} />
          </Section>
          <Section
            className={`${styles.section} ${styles.required}`}
            required={false}
            title="Speedometer Type"
          >
            <Input className={styles.input} />
          </Section>
        </div>
      </Spin>
    </div>
  );
};

interface SectionProps extends Props {
  title?: string;
  required?: boolean;
  children?: any;
}
const Section = (props: SectionProps) => {
  const required = props.required;
  const title = props.title;
  return (
    <div className={`${sectionStyles.container} ${props.className}`}>
      {title ? (
        <div className={`${sectionStyles.title}`}>
          {props.required ? <div className={`${sectionStyles.symbol}`}>*</div> : ''}
          <div>{props.title}</div>
        </div>
      ) : (
        ''
      )}
      <div className={`${sectionStyles.content} ${required ? sectionStyles.required : ''}`}>
        {props.children ? props.children : ''}
      </div>
    </div>
  );
};

interface InputProps extends Props {
  value?: string;
}
const Input = (props: InputProps) => {
  const { value = '' } = props;
  return (
    <AntdInput
      className={`${inputStyles.container} ${props.className}`}
      disabled={true}
      value={value}
    />
  );
};

const Spin = (props: any) => {
  const { children, className = '', ...restProps } = props;
  return (
    <AntdSpin {...restProps} className={`${spinStyles.container} ${className}`}>
      {props.children}
    </AntdSpin>
  );
};

const Dot = (props: any) => {
  const { size = 12, color = 'red' } = props;
  const style = {
    width: `${size}px`,
    height: `${size}px`,
    borderRadius: `50%`,
    backgroundColor: `#${color}`
  };
  return <div style={style}></div>;
};

export default connect(
  mapStateToPros,
  mapDispatchToProps
)(BasicInfo);
