import { BankCardsStore, BankCardsAction, BankCardsActionTypes } from '../types';

export const initialState: BankCardsStore = {
  cards: [],
  isUseful: false,
  isLoading: false
};

export default (state = initialState, action: BankCardsAction): BankCardsStore => {
  switch (action.type) {
    case BankCardsActionTypes.SET_BANK_CARDS: {
      return { ...state, ...action.bankCards };
    }
    case BankCardsActionTypes.LOADING: {
      return { ...state, isLoading: true };
    }
    default:
      return state;
  }
};
