import React from 'react';
import { Row, Col, Radio } from 'antd';
import { Descriptions } from '../Descriptions';
import { CardWithTitle } from '../Card';
import _get from '../../../common/get';
import { post } from '../../../api/http';
// import styles from '../index.module.scss';
import styles from './index.module.scss';
import Cascader from '../../Cascader/index';
import { FormattedMessage, FormattedNumber } from 'react-intl';

const tagStyle = {
  display: 'inline-block',
  fontFamily: 'SFProText',
  fontSize: 12,
  color: '#8a9199'
};

const activeStyle = {
  color: '#3acdfe',
  borderColor: '#3acdfe'
};

type Props = any;
type State = any;

export class Shiping extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      to: this.props.to,
      shipMethod: this.props.shipMethod,
      tradeXShippingFee: this.props.shippingFee,
      ownShipperFee: '0'
    };
  }
  isCA2CA(from: any, to: any) {
    let fromKey = _get(from, ['country', 'key']);
    let toKey = _get(to, ['country', 'key']);
    return fromKey === 'CA' && toKey === 'CA';
  }
  destPortChange(value: any) {
    let to = this.getPort(value);
    let newState: any = {
      to: this.getPort(value),
      tradeXShippingFee: value['shipment_fee'].toString()
    };
    if (!this.isCA2CA(this.props.from, to)) {
      newState.shipMethod = 2;
    }
    this.setState(
      {
        ...this.state,
        ...newState
      },
      () => this.handleChange()
    );
  }
  radioChange(shipMethod: number) {
    this.setState({ ...this.state, shipMethod }, () => this.handleChange());
  }
  handleChange() {
    let value = {
      to: this.state.to,
      shipMethod: this.state.shipMethod
    };
    this.props.onShippingChange({ ...value });
  }
  getPort(warehouse: any): object {
    let city = _get(warehouse, ['city_location', 'name']);
    let province = _get(warehouse, ['city_location', 'province']);
    let country = _get(warehouse, ['city_location', 'country']);
    let id = _get(warehouse, ['id']);
    city = city ? city : { key: '--', display_value: '--' };
    province = province ? province : { key: '--', display_value: '--' };
    country = country ? country : { key: '--', display_value: '--' };
    return { city, province, country, id };
  }

  title = (
    <div className={`${styles.title}`}>
      <FormattedMessage id="makeOffer_shipping_title" defaultMessage="Shipping" />
    </div>
  );

  render() {
    let { style, className = '', isCA2CA = false } = this.props;

    return (
      <Descriptions style={style} className={className} title={this.title}>
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage id="makeOffer_shipping_from" defaultMessage="From" />
          </div>
          <div className={styles.value}>
            {`${this.props.from.city['display_value']}, ${this.props.from.country['display_value']}`}
          </div>
        </Descriptions.Row>
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage id="makeOffer_shipping_destination" defaultMessage="Destination" />
          </div>
          <div className={styles.value}>
            <Cascader
              country={this.props.to.country}
              port={this.props.to.city}
              garageId={this.props.garageId}
              onPortChange={(value: any) => {
                this.destPortChange(value);
              }}
            />
          </div>
        </Descriptions.Row>
        {isCA2CA ? (
          <Descriptions.Row>
            <div className={styles.name}>
              <FormattedMessage id="makeOffer_shipping_shipMethod" defaultMessage="Ship method" />
            </div>
            <div className={styles.value}>
              <Radio.Group
                value={this.props.shipMethod}
                size="small"
                style={{ borderRadius: 2 }}
                onChange={ev => this.radioChange(ev.target.value)}
              >
                <Radio.Button value={2} style={getStyle(2, this.props.shipMethod)}>
                  <FormattedMessage id="makeOffer_shipping_tradeX" defaultMessage="TRADE X" />
                </Radio.Button>
                <Radio.Button value={1} style={getStyle(1, this.props.shipMethod)}>
                  <FormattedMessage
                    id="makeOffer_shipping_ownShipper"
                    defaultMessage="Own shipper"
                  />
                </Radio.Button>
              </Radio.Group>
            </div>
          </Descriptions.Row>
        ) : (
          ''
        )}
        <Descriptions.Row>
          <div className={styles.name}>
            <FormattedMessage id="makeOffer_shipping_shippingFee" defaultMessage="Shipping fee" />
          </div>
          <div className={styles.value}>
            {isNaN(this.props.shippingFee) ? '' : '$'}
            {isNaN(this.props.shippingFee) ? (
              'Quote'
            ) : (
              <FormattedNumber value={Number(this.props.shippingFee)} />
            )}
            {isNaN(this.props.shippingFee) ? '' : ` ${this.props.currency} * ${this.props.number}`}
          </div>
        </Descriptions.Row>
      </Descriptions>
    );
  }
}

function getStyle(value: number, shipMethod: number): object {
  if (value === shipMethod) {
    return { ...tagStyle, ...activeStyle };
  } else {
    return tagStyle;
  }
}
